import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity } from 'react-native';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { withTranslation, WithTranslation } from 'react-i18next';

import { DarkPrimary } from '../../Colors';
import { formatCurrency } from '../../helpers';

class Skola extends Component<RouteComponentProps & WithTranslation> {
  onPress = () => {
    const { location, history } = this.props;

    if (location.pathname !== '/home/debt') {
      history.push('/home/debt');
    }
  };

  render() {
    const { t } = this.props;

    return (
      <TouchableOpacity
        style={{ flexDirection: 'row', marginRight: -16 }}
        activeOpacity={0.8}
        onPress={this.onPress}
      >
        <View style={styles.triangle} />
        <View style={styles.amount}>
          <Text style={styles.amountText}>{t('debt').toUpperCase()}</Text>
        </View>
      </TouchableOpacity>
    );
  }
}

const styles = StyleSheet.create({
  triangle: {
    backgroundColor: 'transparent',
    borderStyle: 'solid',
    borderTopWidth: 56,
    borderTopColor: DarkPrimary,
    borderLeftWidth: 24,
    borderLeftColor: 'transparent',
  },
  amount: {
    justifyContent: 'center',
    alignItems: 'flex-end',
    height: 56,
    paddingLeft: 4,
    paddingRight: 16,
    backgroundColor: DarkPrimary,
  },
  amountText: {
    color: 'white',
    fontSize: 22,
    fontWeight: 'bold',
  },
});

export default withTranslation()(withRouter(Skola));
