import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';
import L from 'leaflet';
import { Map, Marker as MapMarker, Popup, TileLayer } from 'react-leaflet';
import MyLocation from '@material-ui/icons/MyLocationRounded';
import { withTranslation, WithTranslation } from 'react-i18next';

const UserLocationMarker = require('../../../../assets/myLocation.svg');
const ArtiIcon = require('../../../../assets/artimiausias_icon.png');

import userActions from '../../../actions/user';
import globalActions from '../../../actions/global';
import { IState, IPosition } from '../../../StateTypes';
import { Gray, Black } from '../../../Colors';

interface IMapState {
  position: IPosition | null;
  showMap: boolean;
  mapReady: boolean;
  zoom: number;
}

type IMapProps = WithTranslation & ReturnType<typeof mapStateToProps> & typeof localActions;

let zoomCache = 13;
let positionCache: IPosition | null = null;

class LeafletMap extends Component<IMapProps, IMapState> {
  state = { position: positionCache, showMap: false, mapReady: false, zoom: zoomCache };

  markerRefs: any = {};
  mapRef = React.createRef<Map>();

  componentDidMount() {
    const { userLocation } = this.props;

    let positionToCenterTo;

    if (positionCache) {
      positionToCenterTo = positionCache;
    } else {
      positionToCenterTo = {
        lat: userLocation.latitude,
        lng: userLocation.longitude,
      };
    }

    this.setState({
      position: positionToCenterTo,
      zoom: zoomCache,
      showMap: true,
    });
  }

  componentDidUpdate(prevProps: IMapProps) {
    const { selectedVendor } = this.props;
    const { mapReady } = this.state;
    const { selectedVendor: prevSelectedVendor } = prevProps;

    // Animate to position after selecting vendor
    if (
      (selectedVendor && !prevSelectedVendor) ||
      (selectedVendor &&
        prevProps.selectedVendor &&
        selectedVendor.id !== prevProps.selectedVendor.id &&
        mapReady)
    ) {
      this.mapRef!.current.leafletElement.panTo([selectedVendor.latitude, selectedVendor.longitude]);
    }

    //Show marker popup
    if (selectedVendor && this.markerRefs[selectedVendor.id] && mapReady) {
      this.markerRefs[selectedVendor.id].leafletElement.openPopup();
    }
  }

  onMoveEnd = (e: any) => {
    zoomCache = this.mapRef.current.leafletElement.getZoom();
    positionCache = this.mapRef.current.leafletElement.getCenter();
  };

  onMarkerPress = (id: number) => {
    this.props.setSelectedVendorById(id);
  };

  onMapReady = () => {
    this.setState({ mapReady: true });
  };

  render() {
    const { vendors, selectedVendor, userLocation, t } = this.props;
    const { position, zoom } = this.state;

    const selectedVendorId = selectedVendor ? selectedVendor.id : null;

    return (
      <View>
        <Map
          style={{ height: 350 }}
          center={position}
          zoom={zoom}
          whenReady={this.onMapReady}
          onMoveEnd={this.onMoveEnd}
          ref={this.mapRef}
          preferCanvas
        >
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
          />
          {vendors.map(({ id, name, address, latitude, longitude }) => (
            <MapMarker
              key={`${id}${selectedVendorId === id ? '-selected' : ''}`}
              position={[latitude, longitude]}
              title={name}
              riseOnHover={true}
              ref={(ref: any) => (this.markerRefs[id] = ref)}
              onClick={() => this.onMarkerPress(id)}
              icon={L.icon({
                iconUrl: ArtiIcon,
                iconRetinaUrl: ArtiIcon,
                iconSize: [34, 54],
                iconAnchor: [17, 54],
                popupAnchor: [0, -56],
              })}
            >
              <Popup>
                <View>
                  <Text style={{ color: Black, fontWeight: 'bold' }}>{name}</Text>
                </View>
                <View>
                  <Text style={{ color: Gray }}>{address}</Text>
                </View>
              </Popup>
            </MapMarker>
          ))}
          <MapMarker
            position={[userLocation.latitude, userLocation.longitude]}
            icon={L.icon({
              iconUrl: UserLocationMarker,
              iconRetinaUrl: UserLocationMarker,
              iconSize: [28, 28],
              iconAnchor: [14, 14],
            })}
            title={t('map.myPosition')}
          ></MapMarker>
        </Map>

        <div
          style={{
            position: 'absolute',
            justifyContent: 'center',
            alignItems: 'center',
            right: 4,
            bottom: 20,
            zIndex: 999,
          }}
          title={t('map.showMyPosition')}
        >
          <TouchableOpacity
            activeOpacity={0.8}
            style={{
              justifyContent: 'center',
              alignItems: 'center',
              padding: 4,
            }}
            onPress={() =>
              this.mapRef!.current.leafletElement.panTo([userLocation.latitude, userLocation.longitude])
            }
          >
            <MyLocation style={{ fontSize: 26, color: '#fff' }} />
          </TouchableOpacity>
        </div>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    alignSelf: 'stretch',
    justifyContent: 'center',
    alignItems: 'center',
    height: 350,
  },
  map: {
    alignSelf: 'stretch',
    height: 350,
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 68,
    height: 68,
    marginTop: -24,
    backgroundColor: 'white',
    borderRadius: 68 / 2,
    elevation: 3,
  },
});

const mapStateToProps = (state: IState) => ({
  vendors: state.globalReducer.vendors,
  selectedVendor: state.globalReducer.selectedVendor,
  userLocation: state.userReducer.location,
});

const localActions = {
  setSelectedVendorById: globalActions.setSelectedVendorById,
  setUserLocation: userActions.setUserLocation,
};

export default withTranslation('vendorMap')(connect(mapStateToProps, localActions)(LeafletMap));
