import React, { Component } from 'react';
import { StyleSheet, View, Text, ScrollView } from 'react-native';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { withTranslation, WithTranslation } from 'react-i18next';

import { DarkGray, Black, Gray, LightGray } from '../../../Colors';
import Item from '../CartConfirmation/Item';
import { IState, ICartProduct } from '../../../StateTypes';
import { formatCurrency } from '../../../helpers';
import userActions from '../../../actions/user';
import PrimaryButton from '../../../components/PrimaryButton';

type IVaultConfirmProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class VaultConfirm extends Component<IVaultConfirmProps> {
  render() {
    const { allProducts, cart, addToCart, removeFromCart, onConfirm, t } = this.props;

    let cartAmount = 0;
    let cartSum = 0;

    const cartProducts: ICartProduct[] = [];

    for (let productId in cart) {
      const product = allProducts.find((product) => product.id === Number(productId))!;
      const cartProduct = { ...product, amount: cart[productId] };

      cartProducts.push(cartProduct);
      cartAmount += cart[productId];
      cartSum += cartProduct.price * cartProduct.amount;
    }

    return (
      <ScrollView>
        <View style={{ paddingTop: 16, paddingBottom: 36 }}>
          <Text style={styles.sectionTitle}>{t('confirm.addToVaultConfirm')}</Text>

          {cartAmount === 0 ? (
            <Text style={styles.empty}>{t('confirm.empty')}</Text>
          ) : (
            <>
              {cartProducts.map((item) => (
                <Item
                  key={item.id}
                  item={item}
                  type='vault'
                  addToCart={addToCart}
                  removeFromCart={removeFromCart}
                  cartAmount={cartAmount}
                />
              ))}

              <View style={{ marginBottom: 24 }}>
                <View style={styles.totalSumWrapper}>
                  <Text style={styles.totalSumLabel}>{t('confirm.total')}:</Text>
                  <Text style={styles.totalSum}>{formatCurrency(cartSum)}</Text>
                </View>
              </View>

              <PrimaryButton width={180} onPress={() => onConfirm(cart)}>
                {t('confirm.continue').toUpperCase()}
              </PrimaryButton>
            </>
          )}
        </View>
      </ScrollView>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  empty: {
    marginTop: 48,
    marginBottom: 36,
    textAlign: 'center',
    fontSize: 18,
    color: Gray,
  },
  sectionTitle: {
    marginLeft: 16,
    color: DarkGray,
  },
  info: {
    marginHorizontal: 36,
    marginBottom: 24,
    textAlign: 'center',
    color: Gray,
  },
  endSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
  },
  endSum: {
    fontSize: 18,
    color: Black,
  },
  totalSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8,
    paddingHorizontal: 16,
  },
  totalSumLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    color: Black,
  },
  totalSum: {
    fontSize: 26,
    color: Black,
    fontWeight: 'bold',
  },
});

const mapStateToProps = (state: IState) => ({
  allProducts: state.globalReducer.products,
  cart: state.userReducer.vaultCart,
  membership: state.userReducer.membership!,
});

const localActions = {
  addToCart: userActions.addProductToVaultCart,
  removeFromCart: userActions.removeProductFromVaultCart,
  onConfirm: userActions.onVaultPaymentConfirm,
};

export default withTranslation('vault')(connect(mapStateToProps, localActions)(VaultConfirm));
