import React, { Component } from 'react';
import { StyleSheet, Text, TouchableWithoutFeedback, TouchableHighlight, View } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import vendorActions from './actions/vendor';
import globalActions from './actions/global';
import { Black, DarkGray, Gray, Primary } from './Colors';
import { IState } from './StateTypes';
import { formatCurrency } from './helpers';

type IModalProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class Modal extends Component<IModalProps> {
  onTake = () => {
    const { vendorId, productId } = this.props;

    if (this.props.location.pathname === '/home/machine') {
      this.props.takeProduct(vendorId, productId);
    } else {
      this.props.takeProductFromReservation(productId);
    }
  };

  onClose = () => {
    this.props.hideModal();
  };

  render() {
    const { show, allProducts, productId, vault, t } = this.props;

    if (!show) {
      return null;
    }

    const isInMachine = this.props.location.pathname === '/home/machine';
    const product = allProducts.find((product) => product.id === Number(productId))!;
    const hasInVault = vault.vault.some(
      (item) => item.product.id === productId && item.status === 'approved'
    );

    return (
      <TouchableWithoutFeedback onPress={this.onClose}>
        <Animatable.View
          style={styles.background}
          animation='fadeIn'
          easing='ease-out'
          duration={150}
          useNativeDriver
        >
          <TouchableWithoutFeedback onPress={() => {}}>
            <View style={styles.message}>
              <Text style={styles.title}>{product.name}</Text>
              <Text style={styles.body}>
                {isInMachine ? (
                  <Text>
                    {t('payment', { price: formatCurrency(product.storage_fee) })}{' '}
                    {hasInVault ? '' : <Text>{t('repay')}</Text>}
                  </Text>
                ) : (
                  t('areYouSure')
                )}
              </Text>
              <View style={styles.buttonWrapper}>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={this.onClose}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Gray }}>{t('cancel').toUpperCase()}</Text>
                </TouchableHighlight>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={this.onTake}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Primary }}>{t('take').toUpperCase()}</Text>
                </TouchableHighlight>
              </View>
            </View>
          </TouchableWithoutFeedback>
        </Animatable.View>
      </TouchableWithoutFeedback>
    );
  }
}

const styles = StyleSheet.create({
  background: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)',
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    elevation: 5,
    borderRadius: 3,
  },
  title: {
    marginBottom: 12,
    color: Black,
    fontWeight: 'bold',
    fontSize: 20,
  },
  body: {
    marginBottom: 16,
    color: DarkGray,
    fontSize: 16,
    lineHeight: 22,
  },
  buttonWrapper: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    marginRight: -12,
    marginBottom: -16,
  },
  button: {
    alignSelf: 'flex-end',
    paddingVertical: 10,
    paddingHorizontal: 12,
  },
});

const mapStateToProps = (state: IState) => ({
  show: state.globalReducer.takeModal.show,
  ...state.globalReducer.takeModal.info,
  allProducts: state.globalReducer.products,
  vault: state.userReducer.vault,
});

const localActions = {
  takeProduct: vendorActions.takeProduct,
  takeProductFromReservation: vendorActions.takeProductFromReservation,
  hideModal: globalActions.hideTakeModal,
};

export default withTranslation('takeModal')(connect(mapStateToProps, localActions)(withRouter(Modal)));
