import React, { Component } from 'react';
import { AppState, StyleSheet, View, AppStateStatus } from 'react-native';
import { Switch, Route, RouteComponentProps, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { differenceInSeconds } from 'date-fns';

import { getCurrentTime } from '../../api';

import Drawer from './Drawer';
import NotPayed from './NotPayed';
import NotIdentified from './NotIdentified';

import VendorMap from './VendorMap';
import VendingMachine from './VendingMachine';
import CartConfirm from './CartConfirmation';
import CartCheckout from './CartCheckout';

import Settings from './Settings';
import Debt from './Debt';
import Vault from './Vault';
import Help from './Help';

import CardModal from './CardModal';
import globalActions from '../../actions/global';
import userActions from '../../actions/user';
import loginActions from '../../actions/login';
import paymentActions from '../../actions/payment';
import vendorActions from '../../actions/vendor';
import { IState } from '../../StateTypes';
import ToastMessage from '../../components/ToastMessage';

type IHomeProps = RouteComponentProps & ReturnType<typeof mapStateToProps> & typeof localActions;

interface IHomeState {
  appState: AppStateStatus;
}

class Home extends Component<IHomeProps, IHomeState> {
  state = {
    appState: AppState.currentState,
  };

  startDate = '';

  componentDidMount() {
    AppState.addEventListener('change', this.handleAppStateChange);
  }

  handleAppStateChange = async (nextAppState: AppStateStatus) => {
    const { token, cancelPayment, logout } = this.props;
    const { appState } = this.state;

    // app going into background
    if (appState === 'active' && nextAppState.match(/inactive|background/)) {
      if (token) {
        try {
          this.startDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          this.startDate = new Date().toISOString();
        }
      }
    }

    // app back from background
    if (appState.match(/inactive|background/) && nextAppState === 'active') {
      if (token) {
        let currentDate;

        try {
          currentDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          currentDate = new Date().toISOString();
        }

        const secondsInBackground = differenceInSeconds(currentDate, this.startDate);

        if (secondsInBackground > 60 * 5) {
          cancelPayment();
          logout();
        }
      }
    }

    this.setState({ appState: nextAppState });
  };

  componentWillUnmount() {
    AppState.removeEventListener('change', this.handleAppStateChange);
  }

  render() {
    const { identified, payed, toastMessage, loggedIn, cardModal, vaultModal } = this.props;

    if (!loggedIn) {
      return <Redirect to='/' />;
    }

    return (
      <View style={styles.wrapper}>
        {!payed && <NotPayed />}
        {payed && !identified && <NotIdentified />}
        <View style={{ flex: 1, flexDirection: 'row' }}>
          <Drawer />
          <Switch>
            <Route exact path='/home' component={VendorMap} />
            <Route exact path='/home/machine' component={VendingMachine} />
            <Route exact path='/home/cart' component={CartConfirm} />
            <Route exact path='/home/cartCheckout' component={CartCheckout} />
            <Route exact path='/home/debt' component={Debt} />
            <Route path='/home/vault' component={Vault} />
            <Route path='/home/settings' component={Settings} />
            <Route exact path='/home/help' component={Help} />
          </Switch>
        </View>

        {cardModal.show && <CardModal />}
        {toastMessage.show && <ToastMessage />}
      </View>
    );
  }
}

const mapStateToProps = (state: IState) => ({
  identified: state.userReducer.identified,
  payed: state.userReducer.registrationFeePayed,
  takeModal: state.globalReducer.takeModal,
  globalModal: state.globalReducer.globalModal,
  codeModal: state.globalReducer.codeModal,
  toastMessage: state.globalReducer.toastMessage,
  loggedIn: state.loginReducer.loggedIn,
  cardModal: state.paymentReducer.cardModal,
  vaultModal: state.globalReducer.vaultModal,
  token: state.loginReducer.token?.access_token,
});

const localActions = {
  hideTakeModal: globalActions.hideTakeModal,
  hideGlobalModal: globalActions.hideGlobalModal,
  hideVaultModal: globalActions.hideVaultModal,
  cancelPayment: paymentActions.cancelPayment,
  setUserLocation: userActions.setUserLocation,
  clearPollInterval: vendorActions.clearPollInterval,
  logout: loginActions.logout,
};

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
});

export default connect(mapStateToProps, localActions)(Home);
