import React, { Component } from 'react';
import { View } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import LocationOn from '@material-ui/icons/LocationOn';
import CreditCard from '@material-ui/icons/CreditCard';
import FolderOpen from '@material-ui/icons/FolderOpen';
import Help from '@material-ui/icons/Help';
import Timer from '@material-ui/icons/Timer';

import Settings from '@material-ui/icons/Settings';
import CardMembership from '@material-ui/icons/CardMembership';

import MenuItem from './MenuItem';
import { IState } from '../../../../../StateTypes';
import { Gray, LightGray } from '../../../../../Colors';

interface IMenuOwnProps {
  persistent: boolean;
}

type IMenuProps = IMenuOwnProps & WithTranslation & ReturnType<typeof mapStateToProps>;

class Menu extends Component<IMenuProps> {
  render() {
    const { reservation, persistent, t } = this.props;
    return (
      <View style={{ flex: 1, paddingTop: persistent ? 0 : 16, paddingHorizontal: 8 }}>
        <MenuItem exact to='/home' icon={LocationOn}>
          {t('storages')}
        </MenuItem>

        <MenuItem to='/home/debt' icon={CreditCard}>
          {t('myDebt')}
        </MenuItem>

        <MenuItem to='/home/vault' icon={FolderOpen}>
          {t('myStorage')}
        </MenuItem>

        <MenuItem to='/home/settings' icon={Settings}>
          {t('settings')}
        </MenuItem>

        <MenuItem to='/home/help' icon={Help}>
          {t('help')}
        </MenuItem>

        {/* <MenuItem to='/home/membership' icon={CardMembership} disabled>
          {t('membership')}
        </MenuItem> */}

        {reservation && (reservation.status === 1 || reservation.status === 3) && (
          <View>
            <View style={{ marginVertical: 8, borderBottomWidth: 1, borderBottomColor: LightGray }} />
            <MenuItem to='/home/cartCheckout' icon={Timer}>
              {t('activeReservation')}
            </MenuItem>
          </View>
        )}
      </View>
    );
  }
}

const mapStateToProps = (state: IState) => {
  return {
    reservation: state.userReducer.reservations[0],
  };
};
export default withTranslation('menu')(connect(mapStateToProps)(Menu));
