import React, { Component } from 'react';
import { StyleSheet, Text, View, AppState, AppStateStatus } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { connect } from 'react-redux';
import { differenceInSeconds } from 'date-fns';

import { Black, DarkGray, Red } from './Colors';
import { IState } from './StateTypes';
import { formatCountdown } from './helpers';
import { getCurrentTime } from './api';
import { withTranslation, WithTranslation } from 'react-i18next';

type ICodeModalProps = WithTranslation & ReturnType<typeof mapStateToProps>;

interface ICodeModalState {
  secondsLeft: number;
  show: boolean;
  appState: AppStateStatus;
}

class CodeModal extends Component<ICodeModalProps, ICodeModalState> {
  state = {
    secondsLeft: 0,
    show: false,
    appState: AppState.currentState,
  };

  startDate = '';
  interval = 0;

  async componentDidMount() {
    const { token, lastModified } = this.props;

    AppState.addEventListener('change', this.handleAppStateChange);

    try {
      if (token) {
        const currentDate = (await getCurrentTime(token)).date;
        const secondsLeft = 60 - differenceInSeconds(currentDate, lastModified);

        this.setState(
          {
            secondsLeft: Math.max(0, secondsLeft),
            show: true,
          },
          () => {
            this.interval = setInterval(this.timerCb, 1000);
          }
        );
      }
    } catch (e) {
      console.error(e);
    }
  }

  handleAppStateChange = async (nextAppState: AppStateStatus) => {
    const { token } = this.props;
    const { appState } = this.state;
    // app going into background
    if (appState === 'active' && nextAppState.match(/inactive|background/)) {
      if (token) {
        this.stopCountdown();
        try {
          this.startDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          this.startDate = new Date().toISOString();
        }
      }
    }

    // app back from background
    if (appState.match(/inactive|background/) && nextAppState === 'active') {
      if (token) {
        let currentDate;

        try {
          currentDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          currentDate = new Date().toISOString();
        }

        const secondsInBackground = differenceInSeconds(currentDate, this.startDate);

        this.setState(
          (state) => ({
            secondsLeft: Math.max(0, state.secondsLeft - secondsInBackground),
          }),
          () => {
            this.interval = setInterval(this.timerCb, 1000);
          }
        );
      }
    }

    this.setState({ appState: nextAppState });
  };

  timerCb = () => {
    const { secondsLeft } = this.state;

    if (secondsLeft > 0) {
      this.setState((state) => ({ secondsLeft: state.secondsLeft - 1 }));
    } else {
      this.stopCountdown();
    }
  };

  stopCountdown = () => {
    clearInterval(this.interval);
  };

  componentWillUnmount() {
    this.stopCountdown();
    AppState.removeEventListener('change', this.handleAppStateChange);
  }

  render() {
    const { code, t } = this.props;
    const { secondsLeft, show } = this.state;

    return (
      <Animatable.View
        style={styles.wrapper}
        animation='fadeIn'
        easing='ease-out'
        duration={150}
        useNativeDriver
      >
        <View style={styles.message}>
          <Text style={styles.code}>{code}</Text>
          <Text style={styles.body}>{t('enterThisCombination')}</Text>
          <View style={styles.countdown}>
            {show ? (
              <Text style={styles.time}>{formatCountdown(secondsLeft)}</Text>
            ) : (
              <Text>{t('common:loading')}</Text>
            )}
          </View>
        </View>
      </Animatable.View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)',
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    elevation: 10,
    borderRadius: 3,
  },
  code: {
    color: Black,
    fontSize: 80,
    textAlign: 'center',
    letterSpacing: -5,
    lineHeight: 80,
  },
  body: {
    marginBottom: 16,
    color: DarkGray,
    fontSize: 14,
    lineHeight: 18,
    textAlign: 'center',
  },
  countdown: {
    height: 30,
    justifyContent: 'center',
    alignItems: 'center',
  },
  time: {
    color: Red,
    fontSize: 22,
  },
});

const mapStateToProps = (state: IState) => ({
  code: state.globalReducer.codeModal.code,
  lastModified: '',
  token: state.loginReducer.token?.access_token,
});

export default withTranslation('codeModal')(connect(mapStateToProps)(CodeModal));
