import * as React from 'react';
import { PureComponent } from 'react';
import { View, Text } from 'react-native';
import { connect } from 'react-redux';
import * as Animatable from 'react-native-animatable';

import { IState } from '../StateTypes';
import globalActions from '../actions/global';
import { DarkGray } from '../Colors';

type IToastMessageProps = ReturnType<typeof mapStateToProps> & typeof localActions;

class ToastMessage extends PureComponent<IToastMessageProps> {
  componentDidMount() {
    const { hide } = this.props;

    setTimeout(hide, 3000);
  }

  render() {
    const { show, message, duration } = this.props;

    if (show) {
      return (
        <Animatable.View animation='fadeIn' easing='ease-out' duration={200} useNativeDriver>
          <View
            style={{
              position: 'absolute',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              left: '50%',
              transform: [{ translateX: -130 }],
              bottom: 120,
              paddingHorizontal: 12,
              paddingVertical: 12,
              backgroundColor: DarkGray,
              width: 260,
              borderRadius: 20,
            }}
          >
            <Text style={{ color: 'white', textAlign: 'center' }}>{message}</Text>
          </View>
        </Animatable.View>
      );
    }

    return null;
  }
}

const mapStateToProps = (state: IState) => ({
  ...state.globalReducer.toastMessage,
});

const localActions = {
  hide: globalActions.hideToastMessage,
};

export default connect(mapStateToProps, localActions)(ToastMessage);
