import * as React from 'react';
import { Component } from 'react';
import { TouchableOpacity, ImageBackground, View, Image, Text, StyleSheet } from 'react-native';

import { IPaymentInstrument } from '../StateTypes';
import { Primary } from '../Colors';

interface MiniCreditCardProps {
  item: IPaymentInstrument;
  selected: boolean;
  onSelect: () => void;
}

class MiniCreditCard extends Component<MiniCreditCardProps> {
  getBackgroundImage = (type: string) => {
    if (type === 'Visa') {
      return require('../../assets/card-visa.png');
    } else if (type === 'MasterCard') {
      return require('../../assets/card-mastercard.png');
    } else return require('../../assets/card-unknown.png');
  };

  getLogo = (type: string) => {
    if (type === 'Visa') {
      return require('../../assets/visa.png');
    } else if (type === 'MasterCard') {
      return require('../../assets/mastercard.png');
    } else return require('../../assets/unknown.png');
  };

  render() {
    const { item, selected, onSelect } = this.props;

    return (
      <TouchableOpacity
        key={item.payment_id}
        activeOpacity={0.8}
        onPress={onSelect}
        style={{
          flexDirection: 'row',
          justifyContent: 'center',
          padding: 16,
          backgroundColor: selected ? 'hsl(28, 87%, 95%)' : undefined,
        }}
      >
        <View style={{ justifyContent: 'center', marginRight: 48 }}>
          {selected ? (
            <View style={styles.radioOuter}>
              <View style={styles.radioBackground}>
                <View style={styles.radioInner}></View>
              </View>
            </View>
          ) : (
            <View style={styles.radioOuter}>
              <View style={styles.radioBackground}></View>
            </View>
          )}
        </View>
        <View>
          <ImageBackground style={styles.imageBackground} source={this.getBackgroundImage(item.card_brand)}>
            <View style={{ padding: 16, paddingTop: 8 }}>
              <View
                style={{
                  marginBottom: 16,
                  alignSelf: 'flex-end',
                }}
              >
                <Image
                  resizeMode='cover'
                  style={{ height: 25, width: 35 }}
                  source={this.getLogo(item.card_brand)}
                />
              </View>

              <View style={styles.cardNumber}>
                <Text style={styles.cardNumberObscured}>
                  {'  '}{'  '}{'  '}
                </Text>
                <Text style={styles.cardNumberPan}>{item.pan}</Text>
              </View>

              <View style={{ flexDirection: 'row' }}>
                <View style={{ flex: 1, marginRight: 8 }}>
                  <Text numberOfLines={1} style={styles.cardText}>
                    {item.holder}
                  </Text>
                </View>
                <View>
                  <Text style={styles.cardText}>
                    {item.exp_month}/{String(item.exp_year).slice(2)}
                  </Text>
                </View>
              </View>
            </View>
          </ImageBackground>
        </View>
      </TouchableOpacity>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    justifyContent: 'center',
    padding: 16,
  },
  imageBackground: {
    width: 175,
    height: 110,
  },
  cardNumber: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 4,
  },
  cardNumberObscured: {
    color: 'white',
    fontSize: 18,
  },
  cardNumberPan: {
    color: 'white',
    fontSize: 14,
  },
  cardText: {
    color: 'white',
    fontSize: 12,
  },
  radioOuter: {
    width: 38,
    height: 38,
    borderRadius: 19,
    backgroundColor: Primary,
    justifyContent: 'center',
    alignItems: 'center',
  },
  radioBackground: {
    width: 29,
    height: 29,
    borderRadius: 20,
    backgroundColor: 'white',
    justifyContent: 'center',
    alignItems: 'center',
  },
  radioInner: {
    width: 18,
    height: 18,
    borderRadius: 9,
    backgroundColor: Primary,
  },
});

export default MiniCreditCard;
