import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity } from 'react-native';
import { RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Gray, DarkGray, Black } from '../../../Colors';
import { IState } from '../../../StateTypes';
import globalActions from '../../../actions/global';

type ISettingsProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

interface ISettingsState {
  showLanguageModal: boolean;
}

class Settings extends Component<ISettingsProps, ISettingsState> {
  render() {
    const { language, showLanguageModal, t } = this.props;

    return (
      <View style={styles.wrapper}>
        <Text style={{ marginLeft: 16, marginBottom: 12, color: DarkGray }}>{t('settings')}</Text>

        <View style={styles.optionsWrapper}>
          <TouchableOpacity onPress={showLanguageModal} activeOpacity={0.8}>
            <Text style={{ fontSize: 16, color: Black }}>{t('language:language')}</Text>
            <Text style={{ fontSize: 14, color: Gray, lineHeight: 14 }}>{t(`language:${language}`)}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1, paddingTop: 16 },
  optionsWrapper: { flex: 1, marginLeft: 16 },
});

const mapStateToProps = (state: IState) => ({
  language: state.globalReducer.language,
});

const localActions = {
  showLanguageModal: globalActions.showLanguageModal,
};

export default withTranslation('settings')(connect(mapStateToProps, localActions)(Settings));
