import React, { Component } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import { withTranslation, WithTranslation } from 'react-i18next';

interface IItemInfoProps {
  type: string;
  proof: number;
  volume: number;
  stock: number;
}

class ItemInfo extends Component<IItemInfoProps & WithTranslation> {
  render() {
    const { type, proof, volume, stock, t } = this.props;

    return (
      <View style={styles.wrapper}>
        <View style={{ flexDirection: 'row', marginRight: 16 }}>
          <View style={{ alignItems: 'flex-end', marginRight: 4 }}>
            <Text>{t('item.type')}:</Text>
            <Text>{t('item.proof')}:</Text>
          </View>
          <View>
            <Text style={styles.value}>{type[0].toUpperCase() + type.slice(1, type.length)}</Text>
            <Text style={styles.value}>{proof}%</Text>
          </View>
        </View>

        <View style={{ flexDirection: 'row' }}>
          <View style={{ alignItems: 'flex-end', marginRight: 4 }}>
            <Text>{t('item.volume')}:</Text>
            <Text>{t('item.stock')}:</Text>
          </View>
          <View>
            <Text style={styles.value}>{volume}ml</Text>
            <Text style={styles.value}>{stock}</Text>
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flexDirection: 'row', marginVertical: 24 },
  value: {
    fontWeight: 'bold',
  },
});

export default withTranslation('vendingMachine')(ItemInfo);
