import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import {
  IGlobalState,
  GlobalInitial,
  TakeModalInitial,
  GlobalModalInitial,
  CodeModalInitial,
  ToastMessageInitial,
  VaultModalInitial,
} from '../StateTypes';
import { vendors } from '../mockups';

export default function globalReducer(state = GlobalInitial, action: Action): IGlobalState {
  switch (action.type) {
    case getType(actions.showTakeModal):
      return { ...state, takeModal: { show: true, info: action.payload } };
    case getType(actions.hideTakeModal):
      return { ...state, takeModal: TakeModalInitial };
    case getType(actions.showVaultModal):
      return { ...state, vaultModal: { show: true } };
    case getType(actions.hideVaultModal):
      return { ...state, vaultModal: VaultModalInitial };
    case getType(actions.showGlobalModal):
      return { ...state, globalModal: action.payload };
    case getType(actions.hideGlobalModal):
      return { ...state, globalModal: GlobalModalInitial };
    case getType(actions.showCodeModal):
      return { ...state, codeModal: { show: true, code: action.payload } };
    case getType(actions.hideCodeModal):
      return { ...state, codeModal: CodeModalInitial };
    case getType(actions.createUserSuccess):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'success',
          title: 'createUserSuccessTitle',
          message: 'createUserSuccessMessage',
        },
      };

    case getType(actions.paymentSuccess):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'success',
          title: 'paymentSuccessTitle',
          message: 'paymentSuccessMessage',
        },
      };

    case getType(actions.registrationPaymentSuccess):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'success',
          title: 'registrationPaymentSuccessTitle',
          message: 'registrationPaymentSuccessMessage',
        },
      };

    case getType(actions.paymentFailure):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'failure',
          title: 'paymentFailureTitle',
          message: 'paymentFailureMessage',
        },
      };

    case getType(actions.showReservationInfoModal):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'info_reservation',
          title: 'reservationInfoTitle',
          message: 'reservationInfoMessage',
        },
      };

    case getType(actions.showToastMessage):
      return { ...state, toastMessage: { show: true, ...action.payload } };
    case getType(actions.hideToastMessage):
      return { ...state, toastMessage: ToastMessageInitial };

    case getType(actions.createReservationInit):
    case getType(actions.paymentInit):
    case getType(actions.vendInit):
      return {
        ...state,
        takeModal: TakeModalInitial,
        globalModal: { show: true, type: 'init', title: undefined, message: 'working' },
      };
    case getType(actions.vendSuccess):
      return {
        ...state,
        globalModal: {
          show: true,
          type: 'success',
          title: 'vendSuccessTitle',
          message: 'vendSuccessMessage',
        },
      };

    case getType(actions.showGlobalErrorModal):
    case getType(actions.fetchUserDataFailure):
    case getType(actions.fetchAppDataFailure):
    case getType(actions.fetchVendorProductsFailure):
    case getType(actions.createReservationFailure):
    case getType(actions.vendFailure):
      return {
        ...state,
        takeModal: TakeModalInitial,
        globalModal: { show: true, type: 'failure', ...action.payload },
      };

    case getType(actions.openDrawer):
      return { ...state, isDrawerOpen: true };
    case getType(actions.closeDrawer):
      return { ...state, isDrawerOpen: false };

    case getType(actions.fetchAppDataSuccess):
      return { ...state, vendors: vendors, products: action.payload.products };
    //return { ...state, vendors: action.payload.vendors, products: action.payload.products };

    case getType(actions.setSelectedVendor):
      return { ...state, selectedVendor: action.payload };
    case getType(actions.setFees):
      return { ...state, fees: action.payload };
    case getType(actions.setLocationEnabled):
      return { ...state, locationEnabled: action.payload };
    case getType(actions.showLanguageModal):
      return { ...state, showLanguageModal: true };
    case getType(actions.hideLanguageModal):
      return { ...state, showLanguageModal: false };
    case getType(actions.setAppLanguage):
      return { ...state, language: action.payload };
    default:
      return state;
  }
}
