import React, { Component } from 'react';
import { StyleSheet, View, ScrollView, TouchableOpacity, RefreshControl } from 'react-native';
import { Link, RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import * as Animatable from 'react-native-animatable';
import Dissatisfied from '@material-ui/icons/SentimentDissatisfied';
import { withTranslation, WithTranslation } from 'react-i18next';
import { CircularProgress } from '@material-ui/core';

import Message from '../../../components/Message';
import Item from './Item';
import Selected from '../Selected';

import userActions from '../../../actions/user';
import globalActions from '../../../actions/global';
import vendorActions from '../../../actions/vendor';
import { IState } from '../../../StateTypes';

type IVendingMachineProps = WithTranslation &
  RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class VendingMachine extends Component<IVendingMachineProps, { loading: boolean }> {
  state = {
    loading: false,
  };

  componentDidMount() {
    this.setState({ loading: true });
    this.props.fetchVendorData(this.props.id, () => this.setState({ loading: false }));
  }

  refreshData = () => {
    this.props.fetchVendorData(this.props.id);
  };

  getCartProductCount = (productId: number): number => {
    const { cart } = this.props;

    if (cart && cart[productId]) {
      return cart[productId];
    } else {
      return 0;
    }
  };

  onTakeProduct = (productId: number) => {
    this.props.showTakeModal({ vendorId: this.props.id, productId });
  };

  render() {
    const { name, address, vendorProducts, cart, allProducts, addToCart, t } = this.props;
    const { loading } = this.state;

    if (loading) {
      return (
        <View style={styles.wrapper}>
          <View style={styles.loggingBackground}>
            <Animatable.View
              style={styles.loadingWrapper}
              animation='fadeIn'
              easing='ease-out'
              duration={100}
            >
              <CircularProgress />
            </Animatable.View>
          </View>
        </View>
      );
    }

    let cartAmount = 0;
    let cartSum = 0;

    if (cart) {
      for (let productId in cart) {
        const product = allProducts.find((product) => product.id === Number(productId))!;

        cartAmount += cart[productId];
        cartSum += cart[productId] * product.price;
      }
    }

    return (
      <View style={styles.wrapper}>
        {vendorProducts.length === 0 ? (
          <View style={{ flex: 1, paddingTop: 128 }}>
            <Message icon={Dissatisfied} title={t('noProducts')} subtitle={t('chooseOtherStorage')} />
          </View>
        ) : (
          <View style={{ flex: 1, height: 100 }}>
            <ScrollView
            // refreshControl={
            //   <RefreshControl
            //     colors={[Primary]}
            //     refreshing={this.state.loading}
            //     onRefresh={this.refreshData}
            //   />
            // }
            >
              {vendorProducts.map((item) => (
                <Item
                  key={item.id}
                  count={this.getCartProductCount(item.id)}
                  cartAmount={cartAmount}
                  cartSum={cartSum}
                  addToCart={addToCart}
                  onTakeProduct={this.onTakeProduct}
                  layoutType='vendor'
                  onAddToCart={() => this.props.history.push('/home/cart')}
                  {...item}
                />
              ))}
            </ScrollView>
          </View>
        )}
        <TouchableOpacity activeOpacity={0.8}>
          <Link
            to='/home/cart'
            style={{
              pointerEvents: cartAmount <= 0 ? 'none' : 'auto',
              textDecoration: 'none',
            }}
          >
            <Selected type='cart' title={name} subtitle={address} cartAmount={cartAmount} />
          </Link>
        </TouchableOpacity>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 58,
    height: 58,
    backgroundColor: 'white',
    borderRadius: 58 / 2,
  },
});

const mapStateToProps = (state: IState) => ({
  id: state.globalReducer.selectedVendor!.id,
  name: state.globalReducer.selectedVendor!.name,
  address: state.globalReducer.selectedVendor!.address,
  vendorProducts: state.vendorReducer.products,
  takeModal: state.globalReducer.takeModal,
  cart: state.userReducer.carts[state.globalReducer.selectedVendor!.id],
  allProducts: state.globalReducer.products,
  globalModal: state.globalReducer.globalModal,
});

const localActions = {
  showTakeModal: globalActions.showTakeModal,
  hideTakeModal: globalActions.hideTakeModal,
  hideGlobalModal: globalActions.hideGlobalModal,
  addToCart: userActions.addProductToCart,
  removeFromCart: userActions.removeProductFromCart,
  fetchVendorData: vendorActions.fetchVendorData,
};

export default withTranslation('vendingMachine')(connect(mapStateToProps, localActions)(VendingMachine));
