import React, { Component } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import ArrowForward from '@material-ui/icons/ArrowForward';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Green } from '../../Colors';

interface ISelectedProps {
  type: 'vendor' | 'cart' | 'vault';
  title: string;
  subtitle?: string;
  cartAmount?: number;
}

class Selected extends Component<ISelectedProps & WithTranslation> {
  render() {
    const { type, title, subtitle, cartAmount, t } = this.props;

    return (
      <View style={styles.wrapper}>
        <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center' }}>
          <View>
            <Text style={styles.title}>{title}</Text>
            {subtitle ? <Text style={styles.subtitle}>{subtitle}</Text> : null}
          </View>
        </View>

        {type === 'vendor' && (
          <>
            <Text style={styles.text}>{t('select').toUpperCase()}</Text>
            <ArrowForward style={{ color: 'white', fontSize: 26 }} />
          </>
        )}

        {(type === 'cart' || type === 'vault') && cartAmount! > 0 && (
          <>
            <View>
              <Text style={styles.text}>{t('next').toUpperCase()}</Text>
            </View>

            <ArrowForward style={{ color: 'white', fontSize: 26 }} />
          </>
        )}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: 64,
    paddingHorizontal: 16,
    backgroundColor: Green,
  },
  title: {
    color: 'hsla(0, 0%, 100%, 0.9)',
    fontSize: 16,
  },
  subtitle: {
    color: 'hsla(0, 0%, 100%, 0.65)',
  },
  text: {
    marginRight: 4,
    fontSize: 24,
    fontWeight: 'bold',
    color: 'hsl(0, 0%, 100%)',
  },
});

export default withTranslation('selected')(Selected);
