import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  TouchableWithoutFeedback,
  TouchableHighlight,
  View,
  ViewStyle,
  Dimensions,
  ScrollView,
  StatusBar,
} from 'react-native';
import * as Animatable from 'react-native-animatable';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import CheckCircle from '@material-ui/icons/CheckCircle';
import ErrorOutline from '@material-ui/icons/ErrorOutline';
import { withTranslation, WithTranslation } from 'react-i18next';
import { CircularProgress } from '@material-ui/core';

import globalActions from './actions/global';
import { Black, DarkGray, Primary, Green, Red, Blue } from './Colors';
import { IState } from './StateTypes';
import { connect } from 'react-redux';
import { getPhoneOrientation } from './helpers';

type IModalProps = WithTranslation &
  RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;
interface IModalState {
  width: number;
}

class Modal extends Component<IModalProps, IModalState> {
  state = {
    width: 280,
  };

  componentDidMount() {
    Dimensions.addEventListener('change', this.onOrientationChange);

    this.onOrientationChange();
  }

  onOrientationChange = () => {
    if (getPhoneOrientation() === 'portrait') {
      this.setState({ width: Dimensions.get('window').width - 48 });
    } else {
      if (Dimensions.get('window').width - 128 > 500) {
        this.setState({ width: 500 });
      } else {
        this.setState({ width: Dimensions.get('window').width - 128 });
      }
    }
  };

  onClose = () => {
    if (this.props.type !== 'init') {
      this.props.hideModal();
    }
  };

  getHeaderStyle = (backgroundColor: string): ViewStyle => {
    return {
      height: 96,
      width: 280,
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor,
      borderTopLeftRadius: 3,
      borderTopRightRadius: 3,
    };
  };

  goToVault = () => {
    this.props.hideModal();
    this.props.history.push('/home/vault');
  };

  componentWillUnmount() {
    Dimensions.removeEventListener('change', this.onOrientationChange);
  }

  render() {
    const { title, type, message, t } = this.props;
    const { width } = this.state;

    if (type === 'info_reservation') {
      return (
        <TouchableWithoutFeedback onPress={this.onClose}>
          <Animatable.View
            style={styles.background}
            animation='fadeIn'
            easing='ease-out'
            duration={150}
            useNativeDriver
          >
            <ScrollView style={{ marginTop: StatusBar.currentHeight }}>
              <TouchableWithoutFeedback onPress={() => {}}>
                <View>
                  <View style={{ ...this.getHeaderStyle(Blue), width }}>
                    <ErrorOutline style={{ color: 'white', fontSize: 80 }} />
                  </View>

                  <Animatable.View
                    style={{
                      width: width || 280,
                      padding: 24,
                      backgroundColor: 'white',
                      borderBottomLeftRadius: 3,
                      borderBottomRightRadius: 3,
                      elevation: 10,
                      paddingTop: 24,
                    }}
                    animation='fadeIn'
                    easing='ease-out'
                    duration={100}
                    useNativeDriver
                  >
                    <Text style={styles.title}>{t(title!)}</Text>

                    <Text
                      style={{
                        color: DarkGray,
                        fontSize: 16,
                        lineHeight: 22,
                        textAlign: 'left',
                        marginBottom: 16,
                      }}
                    >
                      {t(message)}
                    </Text>

                    <View
                      style={{
                        flexDirection: 'row',
                        justifyContent: 'flex-end',
                        marginRight: -12,
                        marginBottom: -16,
                      }}
                    >
                      <TouchableHighlight
                        style={styles.button}
                        activeOpacity={0.8}
                        onPress={this.onClose}
                        underlayColor='hsl(0, 0%, 96%)'
                      >
                        <Text style={{ color: Primary }}>{t('common:ok').toUpperCase()}</Text>
                      </TouchableHighlight>
                    </View>
                  </Animatable.View>
                </View>
              </TouchableWithoutFeedback>
            </ScrollView>
          </Animatable.View>
        </TouchableWithoutFeedback>
      );
    }

    return (
      <TouchableWithoutFeedback onPress={this.onClose}>
        <Animatable.View
          style={styles.background}
          animation='fadeIn'
          easing='ease-out'
          duration={150}
          useNativeDriver
        >
          <TouchableWithoutFeedback onPress={() => {}}>
            <View>
              {type === 'init' && (
                <View style={this.getHeaderStyle('white')}>
                  <CircularProgress size={50} />
                </View>
              )}

              {type === 'success' && (
                <View style={this.getHeaderStyle(Green)}>
                  <CheckCircle style={{ color: 'white', fontSize: 80 }} />
                </View>
              )}

              {type === 'failure' && (
                <View style={this.getHeaderStyle(Red)}>
                  <ErrorOutline style={{ color: 'white', fontSize: 80 }} />
                </View>
              )}

              {type === 'info' && (
                <View style={{ ...this.getHeaderStyle(Blue) }}>
                  <ErrorOutline style={{ color: 'white', fontSize: 80 }} />
                </View>
              )}

              <Animatable.View
                style={{
                  width: 280,
                  padding: 24,
                  backgroundColor: 'white',
                  borderBottomLeftRadius: 3,
                  borderBottomRightRadius: 3,
                  paddingTop: type === 'init' ? 0 : 24,
                  elevation: type === 'init' ? 0 : 5,
                }}
                animation='fadeIn'
                easing='ease-out'
                duration={100}
                useNativeDriver
              >
                {title && <Text style={styles.title}>{t(title)}</Text>}
                {message && (
                  <Text
                    style={{
                      ...styles.body,
                      textAlign: type === 'init' ? 'center' : 'left',
                      marginBottom: type === 'init' ? 8 : 16,
                    }}
                  >
                    {t(message)}
                  </Text>
                )}
                {type !== 'init' && (
                  <View
                    style={{
                      flexDirection: 'row',
                      justifyContent: 'flex-end',
                      marginRight: -12,
                      marginBottom: -16,
                    }}
                  >
                    <TouchableHighlight
                      style={styles.button}
                      activeOpacity={0.8}
                      onPress={this.onClose}
                      underlayColor='hsl(0, 0%, 96%)'
                    >
                      <Text style={{ color: Primary }}>{t('common:ok').toUpperCase()}</Text>
                    </TouchableHighlight>
                  </View>
                )}
              </Animatable.View>
            </View>
          </TouchableWithoutFeedback>
        </Animatable.View>
      </TouchableWithoutFeedback>
    );
  }
}

const styles = StyleSheet.create({
  background: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)',
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    borderBottomLeftRadius: 3,
    borderBottomRightRadius: 3,
    elevation: 10,
  },
  title: {
    marginBottom: 12,
    color: Black,
    fontWeight: 'bold',
    fontSize: 20,
  },
  body: {
    color: DarkGray,
    fontSize: 16,
    lineHeight: 22,
  },
  button: {
    alignSelf: 'flex-end',
    paddingVertical: 10,
    paddingHorizontal: 12,
  },
});

const mapStateToProps = (state: IState) => ({
  ...state.globalReducer.globalModal,
});

const localActions = {
  hideModal: globalActions.hideGlobalModal,
};

export default withTranslation('globalModal')(connect(mapStateToProps, localActions)(withRouter(Modal)));
