import React, { Component } from 'react';
import { StyleSheet, View, TouchableWithoutFeedback, Text, TouchableHighlight } from 'react-native';
import * as Animatable from 'react-native-animatable';

import { Black, DarkGray, Gray, Red } from '../Colors';

interface IWarningModal {
  message: string;
  onClose: () => void;
  onLeave: () => void;
}

class WarningModal extends Component<IWarningModal> {
  render() {
    const { message, onClose, onLeave } = this.props;

    return (
      <TouchableWithoutFeedback onPress={onClose}>
        <Animatable.View
          style={styles.background}
          animation='fadeIn'
          easing='ease-out'
          duration={150}
          useNativeDriver
        >
          <TouchableWithoutFeedback onPress={() => {}}>
            <View style={styles.message}>
              <Text style={styles.title}>Dmesio</Text>
              <Text style={styles.body}>{message}</Text>
              <View style={styles.buttonWrapper}>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={onClose}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Gray }}>GRTI</Text>
                </TouchableHighlight>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={onLeave}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Red }}>NUTRAUKTI</Text>
                </TouchableHighlight>
              </View>
            </View>
          </TouchableWithoutFeedback>
        </Animatable.View>
      </TouchableWithoutFeedback>
    );
  }
}

const styles = StyleSheet.create({
  background: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)'
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    elevation: 5,
    borderRadius: 3
  },
  title: {
    marginBottom: 12,
    color: Black,
    fontWeight: 'bold',
    fontSize: 20
  },
  body: {
    marginBottom: 16,
    color: DarkGray,
    fontSize: 16,
    lineHeight: 22
  },
  buttonWrapper: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    marginRight: -12,
    marginBottom: -16
  },
  button: {
    alignSelf: 'flex-end',
    paddingVertical: 10,
    paddingHorizontal: 12
  }
});

export default WarningModal;
