import React, { Component } from 'react';
import { Dimensions, StyleSheet, View, Text, ScrollView } from 'react-native';
import { RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import Item from './Item';
import PrimaryButton from '../../../../components/PrimaryButton';
import VaultDebtProduct from '../../../../components/VaultDebtProduct';
import userActions from '../../../../actions/user';
import globalActions from '../../../../actions/global';

import { Gray, Primary, LightGray, Black } from '../../../../Colors';
import { formatCurrency } from '../../../../helpers';
import { IState, IVaultProduct } from '../../../../StateTypes';

type IVaultHomeProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class VaultHome extends Component<IVaultHomeProps, { vaultLoading: boolean }> {
  state = {
    vaultLoading: true,
  };

  componentDidMount() {
    this.props.getVault(() => this.setState({ vaultLoading: false }));
  }

  showAllProducts = () => {
    this.props.history.push('/home/vault/items');
  };

  render() {
    const { allProducts, vault, t } = this.props;
    const { vaultLoading } = this.state;

    if (vaultLoading) {
      return null;
    }

    const vaultProducts: IVaultProduct[] = [];
    const productIds: { [key: number]: string } = {};
    const vaultApprovedGrouped = [];
    const vaultPendingGrouped = [];
    const vaultFormatted = [];

    for (let item of vault.vault) {
      productIds[item.product.id] = '';
      vaultProducts.push({ ...item.product, amount: 1, status: item.status });
    }

    const vaultApproved = vaultProducts.filter((item) => item.status === 'approved');
    const vaultPending = vaultProducts.filter((item) => item.status === 'pending');

    for (let id in productIds) {
      const vaultFiltered = vaultApproved.filter((item) => item.id === Number(id));

      if (vaultFiltered.length !== 0) {
        vaultApprovedGrouped.push(vaultFiltered);
      }
    }

    for (let array of vaultApprovedGrouped) {
      vaultFormatted.push({ ...array[0], amount: array.length });
    }

    for (let id in productIds) {
      const vaultFiltered = vaultPending.filter((item) => item.id === Number(id));

      if (vaultFiltered.length !== 0) {
        vaultPendingGrouped.push(vaultFiltered);
      }
    }

    for (let array of vaultPendingGrouped) {
      vaultFormatted.push({ ...array[0], amount: array.length });
    }

    const orderProducts: IVaultProduct[] = [];
    const orderGrouped = [];
    const orderFormatted = [];
    const orderProductIds: { [key: number]: string } = {};

    if (vault.products) {
      for (let item of vault.products) {
        const product = allProducts.find((product) => product.name === Object.keys(item)[0])!;
        orderProductIds[product.id] = '';
        orderProducts.push({ ...product, amount: 1, status: 'pending' });
      }

      for (let id in orderProductIds) {
        const orderFiltered = orderProducts.filter((item) => item.id === Number(id));
        if (orderFiltered.length !== 0) {
          orderGrouped.push(orderFiltered);
        }
      }

      for (let array of orderGrouped) {
        orderFormatted.push({ ...array[0], amount: array.length });
      }
    }

    return (
      <View style={{ flex: 1, height: Dimensions.get('window').height - 56 }}>
        <ScrollView>
          <View style={{ paddingTop: 16 }}>
            <Text style={{ marginLeft: 16, marginBottom: 8 }}>{t('myVault')}</Text>
            <View style={{ paddingBottom: 60 }}>
              {vaultFormatted.length === 0 ? (
                <Text style={styles.empty}>{t('empty')}</Text>
              ) : (
                <View
                  style={{
                    flexDirection: 'row',
                    justifyContent: 'space-evenly',
                    flexWrap: 'wrap',
                    marginBottom: 8,
                  }}
                >
                  {vaultFormatted.map((item, index) => (
                    <Item key={`${item.id}${index}`} item={item} />
                  ))}
                </View>
              )}

              {vault.debt ? (
                <>
                  <View style={styles.unpaidTitle}>
                    <Text>{t('unpaid')}</Text>

                    <Text style={{ fontSize: 12, color: Gray }}>
                      {t('vaultNumber')}:{' '}
                      <Text style={{ fontSize: 20, color: Primary, fontWeight: 'bold' }}>
                        {vault.order_id}
                      </Text>
                    </Text>
                  </View>

                  {orderFormatted.map((item, index) => (
                    <VaultDebtProduct key={`${item.id}${index}`} item={item} />
                  ))}

                  <View style={{ marginBottom: 16 }}>
                    <View style={styles.totalSumWrapper}>
                      <Text style={styles.totalSumLabel}>Viso:</Text>
                      <Text style={styles.totalSum}>{formatCurrency(vault.debt)}</Text>
                    </View>
                  </View>

                  <View style={styles.paymentInfo}>
                    <View style={{ marginBottom: 16 }}>
                      <Text style={{ textAlign: 'center' }}>{t('paymentBank')}</Text>

                      <Text style={{ color: Black, fontWeight: 'bold', textAlign: 'center' }}>
                        {t('specifyVaultNumber', { number: vault.order_id })}
                      </Text>
                    </View>

                    {vault.distributor!.banks.map((item) => (
                      <View key={item.number} style={{ marginBottom: 16, alignItems: 'center' }}>
                        <Text style={{ fontSize: 20, color: Primary, fontWeight: 'bold' }}>
                          {item.number}
                        </Text>
                        <Text style={{ fontSize: 12 }}>{item.name}</Text>
                      </View>
                    ))}
                  </View>

                  <View
                    style={{
                      marginBottom: 16,
                      marginHorizontal: 36,
                      borderBottomColor: LightGray,
                      borderBottomWidth: 1,
                      paddingTop: 0,
                    }}
                  />

                  <View style={styles.paymentInfo}>
                    <Text style={{ marginBottom: 8 }}>
                      {t('serves')}{' '}
                      <Text style={{ color: Black, fontWeight: 'bold' }}>{vault.distributor!.name}</Text>
                    </Text>

                    <Text>{t('companyNumber', { number: vault.distributor!.unique_number })}</Text>
                    <Text>{t('vatNumber', { number: vault.distributor!.vat_number })}</Text>
                    <Text>{t('address', { address: vault.distributor!.address })}</Text>
                  </View>

                  <View
                    style={{
                      paddingTop: 16,
                      marginHorizontal: 36,
                      borderBottomColor: LightGray,
                      borderBottomWidth: 1,
                      marginBottom: 24,
                    }}
                  />

                  <PrimaryButton width={190} onPress={this.props.showVaultModal}>
                    {t('cancel').toUpperCase()}
                  </PrimaryButton>
                </>
              ) : (
                <PrimaryButton width={190} onPress={this.showAllProducts}>
                  {t('addToVault').toUpperCase()}
                </PrimaryButton>
              )}
            </View>
          </View>
        </ScrollView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  empty: {
    marginTop: 24,
    marginBottom: 36,
    textAlign: 'center',
    fontSize: 18,
    color: Gray,
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 68,
    height: 68,
    marginTop: -24,
    backgroundColor: 'white',
    borderRadius: 68 / 2,
    elevation: 3,
  },
  totalSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8,
    paddingHorizontal: 16,
  },
  totalSumLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    color: Black,
  },
  totalSum: {
    fontSize: 26,
    color: Black,
    fontWeight: 'bold',
  },
  paymentInfo: {
    alignItems: 'center',
    marginHorizontal: 36,
  },
  unpaidTitle: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'baseline',
    marginHorizontal: 16,
  },
  divider: {
    paddingTop: 16,
    marginBottom: 16,
    marginHorizontal: 36,
    borderBottomColor: LightGray,
    borderBottomWidth: 1,
  },
});

const mapStateToProps = (state: IState) => ({
  vault: state.userReducer.vault,
  allProducts: state.globalReducer.products,
});

const localActions = {
  getVault: userActions.getVault,
  showVaultModal: globalActions.showVaultModal,
};

export default withTranslation('vault')(connect(mapStateToProps, localActions)(VaultHome));
