import { RouterState } from 'connected-react-router';
import { ICardValuesFormatted } from './pages/Home/CardModal';
import { IAuthInfo } from './api/types';

export interface IPosition {
  lat: number;
  lng: number;
}

export interface ILocation {
  latitude: number;
  longitude: number;
}

export interface IRegion extends ILocation {
  latitudeDelta: number;
  longitudeDelta: number;
}

export interface IProduct {
  id: number;
  name: string;
  type: string;
  proof: number;
  volume: number;
  price: number;
  cover: string;
  product_code: number;
  storage_fee: number;
  reservation_fee: number;
}

export interface IVendorProduct extends IProduct {
  stock: number;
}

export interface ICartProduct extends IProduct {
  amount: number;
}

export interface IVaultProduct extends ICartProduct {
  status: 'approved' | 'pending';
}

export interface IVendor {
  id: number;
  name: string;
  address: string;
  latitude: number;
  longitude: number;
  products: number[];
}

export type IMarker = IVendor;

export interface ICartProducts {
  [productId: number]: number;
}

export interface ICarts {
  [vendorId: string]: ICartProducts;
}

export interface ICredentials {
  username: string;
  client_id: string;
  client_secret: string;
  verify_url?: string;
  registration_fee_paid: boolean;
}

export type PaymentType = 'registration' | 'service' | 'debt' | 'vault';

// Login
export interface IToken {
  access_token: string;
  refresh_token: string;
  expires_in: number;
  token_type: string;
  scope: string;
}

export interface ILoginError {
  email?: string;
  auth?: string;
}

export interface ILoginState {
  loggedIn: boolean;
  loggingIn: boolean;
  token: IToken | null;
  reidentification: boolean;
  error: ILoginError | undefined;
}

export const LoginInitial: ILoginState = {
  loggedIn: false,
  loggingIn: false,
  token: null,
  reidentification: false,
  error: undefined,
};

// User
export interface IUserMembership {
  id: number;
  name: string;
  credit: number;
  max_items: number;
  ttc: number;
  debt: number;
  distributor: {
    banks: { name: string; number: string }[];
    name: string;
    address: string;
    unique_number: string;
    vat_number: string;
  };
  products?: {
    [key: string]: number;
  }[];
  order_id?: string;
}

export interface IUserVault {
  vault: IVaultItem[];
  distributor?: {
    banks: { name: string; number: string }[];
    name: string;
    address: string;
    unique_number: string;
    vat_number: string;
  };
  debt?: number;
  products?: {
    [key: string]: number;
  }[];
  order_id?: string;
}

export interface IReservation {
  id: number;
  created_at: string;
  last_modified: string;
  products: { product: number; quantity: number }[];
  user: number;
  status: number;
  vendor: number;
  vended: string | null;
  errors: string | null;
}

export interface ICreateUserError {
  email?: string;
  password?: string;
}

export interface IVaultItem {
  product: IProduct;
  status: 'approved' | 'pending';
}

export interface IUserState {
  location: ILocation;
  membership: IUserMembership | null;
  email: string;
  username: string;
  reservations: IReservation[];
  carts: ICarts;
  creatingUser: boolean;
  error: ICreateUserError | undefined;
  identified: boolean;
  registrationFeePayed: boolean;
  vault: IUserVault;
  vaultCart: ICartProducts;
}

export const UserInitial: IUserState = {
  location: {
    latitude: 0,
    longitude: 0,
  },
  email: '',
  username: '',
  membership: null,
  reservations: [],
  carts: {},
  creatingUser: false,
  error: undefined,
  identified: true,
  registrationFeePayed: false,
  vault: {} as IUserVault,
  vaultCart: {},
};

// Global
export interface ICodeModal {
  show: boolean;
  code: string;
}

export const CodeModalInitial: ICodeModal = {
  show: false,
  code: '',
};

export interface ITakeModalInfo {
  vendorId: number;
  productId: number;
}

export interface ITakeModal {
  show: boolean;
  info: ITakeModalInfo;
}

export const TakeModalInitial: ITakeModal = {
  show: false,
  info: { vendorId: 0, productId: 0 },
};

export type IGlobalModalType = 'init' | 'success' | 'failure' | 'info' | 'info_reservation';

export interface IGlobalModalBody {
  title?: string;
  message: string;
}

export interface IGlobalModal extends IGlobalModalBody {
  show: boolean;
  type: IGlobalModalType;
}

export const GlobalModalInitial: IGlobalModal = {
  show: false,
  type: 'init',
  title: undefined,
  message: '',
};

export interface IFees {
  debt_extra_percents: number;
  debt_extra_fixed: number;
}

export interface IToastMessage {
  show: boolean;
  message: string;
  duration: number;
}

export const ToastMessageInitial: IToastMessage = {
  show: false,
  message: '',
  duration: 0,
};

export interface IVaultModal {
  show: boolean;
}

export const VaultModalInitial: IVaultModal = {
  show: false,
};

export type AppLanguage = 'lt' | 'en';

export interface IGlobalState {
  vaultModal: IVaultModal;
  takeModal: ITakeModal;
  globalModal: IGlobalModal;
  codeModal: ICodeModal;
  vendors: IVendor[];
  products: IProduct[];
  selectedVendor: IVendor | null;
  isDrawerOpen: boolean;
  fees: IFees | undefined;
  userConfirmName: {
    firstName: string;
    lastName: string;
  };
  toastMessage: IToastMessage;
  locationEnabled: boolean;
  showLanguageModal: boolean;
  language: AppLanguage;
}

export const GlobalInitial: IGlobalState = {
  takeModal: TakeModalInitial,
  vaultModal: VaultModalInitial,
  globalModal: GlobalModalInitial,
  codeModal: CodeModalInitial,
  vendors: [],
  products: [],
  selectedVendor: null,
  isDrawerOpen: false,
  fees: undefined,
  userConfirmName: {
    firstName: '',
    lastName: '',
  },
  toastMessage: ToastMessageInitial,
  locationEnabled: false,
  showLanguageModal: false,
  language: 'lt',
};

// Vendor
export interface IVendorState {
  products: IVendorProduct[];
}

export const VendorInitial: IVendorState = {
  products: [],
};

// Payment
export interface ICardModal {
  show: boolean;
  amount: number;
  onConfirm: (values: ICardValuesFormatted | IPaymentInstrument) => void;
}

export const CardModalInitial = {
  show: false,
  amount: 0,
  onConfirm: () => {},
};

export interface IPaymentInstrument {
  exp_month: number;
  exp_year: number;
  holder: string;
  pan: string;
  card_brand: string;
  payment_id: string;
}

export interface IPaymentState {
  cardModal: ICardModal;
  currentReservationId: number | null;
  pastPayments: IPaymentInstrument[];
}

export const PaymentInitial: IPaymentState = {
  cardModal: CardModalInitial,
  currentReservationId: null,
  pastPayments: [],
};

export interface IAuthorizeState {
  html: string;
  info: IAuthInfo | null;
}

export const AuthorizeInitial: IAuthorizeState = {
  html: '',
  info: null,
};

// All
export interface IState {
  loginReducer: ILoginState;
  userReducer: IUserState;
  globalReducer: IGlobalState;
  vendorReducer: IVendorState;
  paymentReducer: IPaymentState;
  authorizeReducer: IAuthorizeState;
  router: RouterState;
}
