import React, { Component } from 'react';
import { StyleSheet, View, StatusBar } from 'react-native';

import Header from './Header';
import Menu from './Menu';
import { LightGray } from '../../../../Colors';

class NavigationView extends Component<{ persistent: boolean }> {
  render() {
    const { persistent } = this.props;

    return (
      <View
        style={{
          flex: 1,
          width: 280,
          paddingTop: 16 + (StatusBar.currentHeight || 0),
          backgroundColor: '#fff',
          borderRightWidth: persistent ? 1 : 0,
          borderRightColor: LightGray,
        }}
      >
        {!persistent && <Header />}
        <Menu persistent={persistent} />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1, width: 280, paddingTop: 16 + (StatusBar.currentHeight || 0), backgroundColor: '#fff' },
});

export default NavigationView;
