import * as React from 'react';
import { Component } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';

import { IVaultProduct, ICartProduct } from '../StateTypes';
import { View, Image, Text, StyleSheet } from 'react-native';
import { formatCurrency, shouldUseContain } from '../helpers';
import { Black, LightGray, Primary, DarkGray, Gray } from '../Colors';

interface IVaultDebtProductOwnProps {
  item: IVaultProduct | ICartProduct;
}

interface IVaultDebtProductState {
  useContain: boolean;
}

type VaultDebtProductProps = IVaultDebtProductOwnProps & WithTranslation;

class VaultDebtProduct extends Component<VaultDebtProductProps, IVaultDebtProductState> {
  state = {
    useContain: true,
  };

  async componentDidMount() {
    const useContain = await shouldUseContain(this.props.item.cover);
    this.setState({ useContain });
  }

  render() {
    const { item, t } = this.props;
    const { useContain } = this.state;

    return (
      <View style={styles.itemWrapper}>
        <View style={{ flex: 1, flexDirection: 'row', marginLeft: -8, marginRight: 16 }}>
          <View style={{ width: 50, height: 70, marginRight: 12 }}>
            <Image
              style={{ flex: 1 }}
              resizeMode={useContain ? 'contain' : 'cover'}
              source={{ uri: item.cover }}
            />
          </View>
          <View style={{ flex: 1, paddingTop: 8 }}>
            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.name}>{item.name}</Text>
            </View>

            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.type}>{item.type.toUpperCase()}</Text>
              <Text style={styles.proof}>{item.proof}%</Text>
            </View>
          </View>
        </View>

        <View
          style={{
            paddingTop: 12,
            marginRight: 16,
          }}
        >
          <Text style={{ fontSize: 16, color: Black }}>
            <Text style={{ fontSize: 16, fontWeight: 'bold' }}>{item.amount}</Text> {t('quantity')}
          </Text>
        </View>

        <View style={{ paddingTop: 8 }}>
          <View
            style={{
              alignItems: 'flex-end',
              width: 80,
            }}
          >
            <View>
              <Text style={styles.totalPrice}>{formatCurrency(item.amount * item.price)}</Text>
              <Text style={{ fontSize: 10, color: Gray, textAlign: 'center' }}>
                {formatCurrency(item.price)}
              </Text>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  sectionTitle: {
    marginLeft: 16,
    color: DarkGray,
    marginBottom: 8,
  },
  empty: {
    marginTop: 24,
    marginBottom: 36,
    textAlign: 'center',
    fontSize: 18,
    color: Gray,
  },
  info: {
    marginHorizontal: 36,
    textAlign: 'center',
    color: Gray,
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 68,
    height: 68,
    marginTop: -24,
    backgroundColor: 'white',
    borderRadius: 68 / 2,
    elevation: 3,
  },
  totalDebt: {
    alignSelf: 'center',
    alignItems: 'center',
    padding: 4,
    paddingHorizontal: 16,
    borderWidth: 4,
    borderColor: Primary,
    borderRadius: 10,
  },

  itemWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    paddingHorizontal: 16,
    paddingVertical: 12,
    borderBottomWidth: 1,
    borderBottomColor: LightGray,
  },
  imageWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 50,
    height: 70,
    marginRight: 12,
    borderWidth: 1,
  },
  type: {
    fontSize: 10,
    letterSpacing: 0.5,
    marginRight: 4,
  },
  proof: {
    fontSize: 10,
    letterSpacing: 0.5,
  },
  name: {
    flex: 1,
    flexWrap: 'wrap',
    fontSize: 16,
    color: Black,
    fontWeight: 'bold',
  },
  totalPrice: {
    fontSize: 20,
    color: Black,
    textAlign: 'right',
  },
  totalSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8,
    paddingHorizontal: 16,
  },
  totalSumLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    color: Black,
  },
  totalSum: {
    fontSize: 26,
    color: Black,
    fontWeight: 'bold',
  },
});

export default withTranslation()(VaultDebtProduct);
