import React, { Component } from 'react';
import { StyleSheet, View, TouchableWithoutFeedback, Text, AsyncStorage } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { connect } from 'react-redux';

import { Black, DarkGray, Red } from './Colors';
import { IState, AppLanguage } from './StateTypes';
import { withTranslation, WithTranslation } from 'react-i18next';
import globalActions from './actions/global';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

type ILanguageModalProps = WithTranslation & ReturnType<typeof mapStateToProps> & typeof localActions;

class LanguageModal extends Component<ILanguageModalProps> {
  languages: string[];

  constructor(props: ILanguageModalProps) {
    super(props);
    this.languages = props.i18n.languages;
  }

  onClose = () => {
    this.props.hideLanguageModal();
  };

  onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { i18n, setAppLanguage, hideLanguageModal } = this.props;

    i18n.changeLanguage(e.target.value);
    setAppLanguage(e.target.value as AppLanguage);
    hideLanguageModal();
    AsyncStorage.setItem('language', e.target.value);
  };

  render() {
    const { show, t, language } = this.props;

    return (
      <div style={{ display: show ? 'flex' : 'none' }}>
        <TouchableWithoutFeedback onPress={this.onClose}>
          <Animatable.View
            style={styles.wrapper}
            animation={show ? 'fadeIn' : 'fadeOut'}
            easing='ease-out'
            duration={150}
            useNativeDriver
          >
            <TouchableWithoutFeedback onPress={() => {}}>
              <View style={styles.message}>
                <Text style={{ fontSize: 16, marginBottom: 4, color: Black }}>{t('language')}</Text>
                <RadioGroup value={language} onChange={this.onChange}>
                  {this.languages.map((language) => (
                    <FormControlLabel
                      key={language}
                      value={language}
                      control={<Radio color='primary' />}
                      label={t(language)}
                    />
                  ))}
                </RadioGroup>
              </View>
            </TouchableWithoutFeedback>
          </Animatable.View>
        </TouchableWithoutFeedback>
      </div>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)',
    zIndex: 9999,
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    elevation: 10,
    borderRadius: 3,
  },
  code: {
    color: Black,
    fontSize: 80,
    textAlign: 'center',
    letterSpacing: -5,
    lineHeight: 80,
  },
  body: {
    marginBottom: 16,
    color: DarkGray,
    fontSize: 14,
    lineHeight: 18,
    textAlign: 'center',
  },
  countdown: {
    height: 30,
    justifyContent: 'center',
    alignItems: 'center',
  },
  time: {
    color: Red,
    fontSize: 22,
  },
});

const mapStateToProps = (state: IState) => ({
  show: state.globalReducer.showLanguageModal,
  language: state.globalReducer.language,
});

const localActions = {
  setAppLanguage: globalActions.setAppLanguage,
  hideLanguageModal: globalActions.hideLanguageModal,
};

export default withTranslation('language')(connect(mapStateToProps, localActions)(LanguageModal));
