import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import { IVendorState, VendorInitial } from '../StateTypes';

export default function vendorReducer(state = VendorInitial, action: Action): IVendorState {
  switch (action.type) {
    case getType(actions.fetchVendorProductsSuccess):
      return { ...state, products: action.payload };
    case getType(actions.fetchVendorProductsFailure):
      return { ...state, products: [] };
    default:
      return state;
  }
}
