import React, { Component } from 'react';
import { Dimensions, StyleSheet, View, TouchableOpacity, ViewStyle } from 'react-native';
import { RouteComponentProps, withRouter } from 'react-router';
import Menu from '@material-ui/icons/Menu';
import { connect } from 'react-redux';

import HeaderLogo from '../HeaderLogo';
import Skola from './Skola';
import globalActions from '../../actions/global';
import { Primary } from '../../Colors';
import { IState } from '../../StateTypes';

interface IAppBarState {
  screenWidth: number;
}

type IAppBarProps = RouteComponentProps & ReturnType<typeof mapStateToProps> & typeof localActions;

class AppBar extends Component<IAppBarProps, IAppBarState> {
  state = {
    screenWidth: Dimensions.get('window').width,
  };

  componentDidMount() {
    Dimensions.addEventListener('change', this.onOrientationChange);
  }

  onMenuPress = () => {
    const { isDrawerOpen } = this.props;

    if (isDrawerOpen) {
      this.props.closeDrawer();
    } else {
      this.props.openDrawer();
    }
  };

  getLogoStyle = (screenWidth: number): ViewStyle => ({
    position: 'absolute',
    left: '50%',
    transform: [{ translateX: -90 }],
  });

  onOrientationChange = () => {
    this.setState({ screenWidth: Dimensions.get('window').width });
  };

  componentWillUnmount() {
    Dimensions.removeEventListener('change', this.onOrientationChange);
  }

  render() {
    const { debt, location } = this.props;
    const { screenWidth } = this.state;

    if (location.pathname === '/' || location.pathname === '/login' || location.pathname === '/register') {
      return null;
    }

    return (
      <View style={{ backgroundColor: Primary }}>
        <View style={styles.wrapper}>
          {screenWidth < 1000 ? (
            <TouchableOpacity
              style={{ padding: 8, marginLeft: -8 }}
              activeOpacity={0.8}
              onPress={this.onMenuPress}
            >
              <Menu style={{ color: 'white', fontSize: 24 }} />
            </TouchableOpacity>
          ) : (
            <View />
          )}

          <View style={this.getLogoStyle(screenWidth)}>
            <HeaderLogo />
          </View>

          {debt !== undefined && debt > 0 && <Skola />}
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    width: '100%',
    maxWidth: 1000,
    height: 56,
    paddingHorizontal: 16,
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    alignSelf: 'center',
  },
});

const mapStateToProps = (state: IState) => ({
  isDrawerOpen: state.globalReducer.isDrawerOpen,
  debt: state.userReducer.membership?.debt,
});

const localActions = {
  openDrawer: globalActions.openDrawer,
  closeDrawer: globalActions.closeDrawer,
};

export default withRouter(connect(mapStateToProps, localActions)(AppBar));
