import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import { ILoginState, LoginInitial } from '../StateTypes';

export default function loginReducer(state = LoginInitial, action: Action): ILoginState {
  switch (action.type) {
    case getType(actions.resetLoginFormErrors):
      return { ...state, error: undefined };
    case getType(actions.loginInit):
      return { ...state, loggingIn: true };
    case getType(actions.loginSuccess):
      return {
        ...state,
        loggingIn: state.reidentification ? false : state.loggedIn,
        loggedIn: true,
        error: undefined,
        token: action.payload.token
      };
    case getType(actions.fetchAppDataSuccess):
      return { ...state, loggingIn: false };
    case getType(actions.loginFailure):
      return {
        ...state,
        loggingIn: false,
        error: action.payload
      };
    case getType(actions.logout):
      return LoginInitial;
    case getType(actions.createUserSuccess):
      return { ...state, error: undefined };
    case getType(actions.setReidentification):
      return { ...state, reidentification: action.payload };
    default:
      return state;
  }
}
