import React, { Component } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import VendorItem from './Item';
import { getDistance } from '../../../../helpers';
import { IState } from '../../../../StateTypes';

type IVendorListProps = ReturnType<typeof mapStateToProps> & WithTranslation;

class VendorList extends Component<IVendorListProps> {
  getDistanceFromUser = (vendorLat: number, vendorLong: number) => {
    const { latitude, longitude } = this.props.userLocation;
    return getDistance(latitude, longitude, vendorLat, vendorLong);
  };

  renderVendorList = () => {
    return this.props.vendors
      .slice()
      .sort(
        (a, b) =>
          this.getDistanceFromUser(a.latitude, a.longitude) -
          this.getDistanceFromUser(b.latitude, b.longitude)
      )
      .map((vendor) => (
        <VendorItem
          key={vendor.id}
          distance={this.getDistanceFromUser(vendor.latitude, vendor.longitude)}
          {...vendor}
        />
      ));
  };

  render() {
    const { t } = this.props;
    return (
      <View>
        <Text style={styles.title}>{t('vendorList.chooseNearestStorage')}</Text>
        <View style={styles.wrapper}>{this.renderVendorList()}</View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {},
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    padding: 16,
  },
  title: {
    fontSize: 16,
    padding: 16,
  },
});

const mapStateToProps = (state: IState) => ({
  vendors: state.globalReducer.vendors,
  userLocation: state.userReducer.location,
});

export default withTranslation('vendorMap')(connect(mapStateToProps)(VendorList));
