import React, { Component } from 'react';
import { StatusBar, StyleSheet, View, AsyncStorage, Dimensions } from 'react-native';
import { Switch, Route, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';
import { connect } from 'react-redux';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core';
import { WithTranslation, withTranslation } from 'react-i18next';
import * as Updates from 'expo-updates';

import Splash from './pages/Splash';
import Login from './pages/Login';
import Register from './pages/Register';
import Identification from './pages/Identification';
import Home from './pages/Home';
import Authorize from './pages/Authorize';

import GlobalModal from './GlobalModal';
import CodeModal from './CodeModal';
import LanguageModal from './LanguageModal';
import TakeModal from './TakeModal';
import VaultModal from './VaultModal';

import AppBar from './components/AppBar';

import globalActions from './actions/global';
import paymentActions from './actions/payment';
import { configureStore, history } from './store/configureStore';
import { IState, AppLanguage } from './StateTypes';
import { Primary, Accent } from './Colors';

const store = configureStore();

const theme = createMuiTheme({
  palette: {
    primary: {
      main: Primary,
    },
    secondary: {
      main: Accent,
    },
  },
});

type AppProps = WithTranslation & ReturnType<typeof mapStateToProps> & typeof localActions;

class App extends Component<AppProps> {
  async componentDidMount() {
    const { i18n, setAppLanguage } = this.props;

    StatusBar.setBackgroundColor('rgba(0, 0, 0, 0.2)');
    StatusBar.setTranslucent(true);

    try {
      const language = await AsyncStorage.getItem('language');

      if (language) {
        i18n.changeLanguage(language);
        setAppLanguage(language as AppLanguage);
      }
    } catch (e) {
      console.error(e);
    }

    history.listen(this.handleBackPress);
  }

  handleBackPress = (location: any, action: 'PUSH' | 'POP' | 'REPLACE') => {
    const { takeModal, globalModal, cardModal, vaultModal } = this.props;
    const { hideTakeModal, hideGlobalModal, hideVaultModal, hideCardModal } = this.props;

    if (cardModal.show) {
      hideCardModal();
    }

    if (vaultModal.show) {
      hideVaultModal();
    }

    if (takeModal.show) {
      hideTakeModal();
    }

    if (globalModal.show) {
      //hideGlobalModal();
    }
  };

  render() {
    const { showGlobalModal, showCodeModal, vaultModal } = this.props;

    return (
      <View style={styles.wrapper}>
        <ConnectedRouter history={history}>
          <MuiThemeProvider theme={theme}>
            <View style={{ width: '100%' }}>
              <AppBar />
            </View>

            <View style={styles.container}>
              <StatusBar backgroundColor='rgba(0, 0, 0, 0.2)' translucent />

              <Switch>
                <Route exact path='/' component={Splash} />
                <Route path='/login' component={Login} />
                <Route path='/register' component={Register} />
                <Route path='/idenfy' component={Identification} />
                <Route path='/home' component={Home} />
                <Route path='/authorize' component={Authorize} />
                <Redirect to='/' />
              </Switch>
            </View>
            {showGlobalModal && <GlobalModal />}
            {showCodeModal && <CodeModal />}
            {vaultModal.show && <VaultModal />}
            <LanguageModal />
            <TakeModal />
          </MuiThemeProvider>
        </ConnectedRouter>
      </View>
    );
  }
}

const withStore = (Component: React.ComponentType<any>) => () => (
  <Provider store={store}>
    <Component />
  </Provider>
);

const mapStateToProps = (state: IState) => ({
  showGlobalModal: state.globalReducer.globalModal.show,
  showCodeModal: state.globalReducer.codeModal.show,
  cardModal: state.paymentReducer.cardModal,
  vaultModal: state.globalReducer.vaultModal,
  takeModal: state.globalReducer.takeModal,
  globalModal: state.globalReducer.globalModal,
  codeModal: state.globalReducer.codeModal,
});

const localActions = {
  setAppLanguage: globalActions.setAppLanguage,
  hideTakeModal: globalActions.hideTakeModal,
  hideGlobalModal: globalActions.hideGlobalModal,
  hideVaultModal: globalActions.hideVaultModal,
  hideCardModal: paymentActions.hideCardModal,
};

const styles = StyleSheet.create({
  wrapper: {
    flex: 1,
    alignItems: 'center',
    backgroundColor: '#fafafa',
  },
  container: {
    position: 'relative',
    flex: 1,
    width: '100%',
    maxWidth: 1000,
    overflow: 'hidden',
    backgroundColor: '#fff',
  },
});

export default withStore(connect(mapStateToProps, localActions)(withTranslation()(App)));
