import React, { Component, ReactComponentElement, ReactElement } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import { OverridableComponent } from '@material-ui/core/OverridableComponent';
import { SvgIconTypeMap } from '@material-ui/core';
import { Gray, Primary } from '../Colors';

interface IMessageProps {
  icon?: OverridableComponent<SvgIconTypeMap<{}, 'svg'>>;
  title: string;
  subtitle?: string;
}

class Message extends Component<IMessageProps> {
  render() {
    const { icon: Icon, title, subtitle } = this.props;

    return (
      <View style={styles.wrapper}>
        {Icon && <Icon style={{ color: Primary, fontSize: 96 }} />}
        <Text style={styles.title}>{title}</Text>
        {subtitle !== undefined && <Text style={styles.subtitle}>{subtitle}</Text>}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1, alignItems: 'center' },
  icon: {
    color: Primary,
  },
  title: {
    fontSize: 20,
  },
  subtitle: {
    fontSize: 14,
    color: Gray,
  },
});

export default Message;
