import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import { ltLocale, enLocale } from '../assets/locales';

i18n.use(initReactI18next).init({
  debug: true,
  resources: { lt: ltLocale, en: enLocale },
  lng: 'lt',
  fallbackLng: ['lt', 'en'],
  whitelist: ['lt', 'en'],
  defaultNS: 'common',

  interpolation: {
    escapeValue: false, // not needed for react as it escapes by default
  },
});

export default i18n;
