import React, { Component } from 'react';
import { StyleSheet, View, StatusBar, Platform, Text } from 'react-native';
import { RouteComponentProps } from 'react-router';
import { WebView } from 'react-native-webview';
import { connect } from 'react-redux';
import * as WebBrowser from 'expo-web-browser';
import { withTranslation, WithTranslation } from 'react-i18next';
import AccountCircle from '@material-ui/icons/AccountCircle';

import WarningModal from '../../../components/WarningModal';
import userActions from '../../../actions/user';
import loginActions from '../../../actions/login';
import { IState } from '../../../StateTypes';
import AppBar from '../../../components/AppBar';
import { Black, DarkGray, Primary } from '../../../Colors';
import Message from '../../../components/Message';
import Marker from '../../../components/Marker';

interface IIdenfyState {
  showWarning: boolean;
}

type IIdenfyProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class Idenfy extends Component<IIdenfyProps, IIdenfyState> {
  state = {
    showWarning: false,
  };

  pollInterval: number | undefined;

  async componentDidMount() {
    const { username, url } = this.props.location.state;

    this.pollInterval = setInterval(() => this.props.pollUser(username), 1000);

    //BackHandler.addEventListener('hardwareBackPress', this.handleBackPress);

    if (Platform.OS === 'web') {
      await WebBrowser.openBrowserAsync(url);
    }
  }

  handleBackPress = () => {
    const { showWarning } = this.state;

    if (showWarning) {
      this.setState({ showWarning: false });
    } else {
      this.setState({ showWarning: true });
    }

    return true;
  };

  onLeave = () => {
    const { history, reidentification, setReidentification } = this.props;

    if (reidentification) {
      setReidentification(false);
    }

    history.goBack();
  };

  componentWillUnmount() {
    if (this.pollInterval !== undefined) {
      clearInterval(this.pollInterval);
    }

    //BackHandler.removeEventListener('hardwareBackPress', this.handleBackPress);
  }

  render() {
    const { t } = this.props;
    const { showWarning } = this.state;
    const { url } = this.props.location.state;

    return (
      <View style={styles.wrapper}>
        <View style={{ height: StatusBar.currentHeight, backgroundColor: 'hsl(204, 100%, 57%)' }} />
        {Platform.OS === 'web' ? (
          <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center', paddingTop: 128 }}>
            <View
              style={{
                width: 96,
                height: 96,
                borderRadius: 96 / 2,
                backgroundColor: Primary,
                marginBottom: 8,
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Marker width={82} height={82} type='light' />
            </View>
            <Message title={`${t('itentificationPending')}...`} subtitle={t('identificationCanceled')} />
          </View>
        ) : (
          <WebView
            style={{ flex: 1 }}
            mediaPlaybackRequiresUserAction={false}
            domStorageEnabled={true}
            allowsInlineMediaPlayback={true}
            source={{ uri: url }}
          />
        )}

        {showWarning && (
          <WarningModal
            message='Ar tikrai norite nutraukti identifikavimo proces? J pratsti galsite vliau'
            onClose={() => this.setState({ showWarning: false })}
            onLeave={this.onLeave}
          />
        )}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flex: 1,
  },
});

const mapStateToProps = (state: IState) => ({
  reidentification: state.loginReducer.reidentification,
});

const localActions = {
  pollUser: userActions.poll,
  setReidentification: loginActions.setReidentification,
};

export default withTranslation('idenfy')(connect(mapStateToProps, localActions)(Idenfy));
