import { createAction } from 'typesafe-actions';
import { push, replace } from 'connected-react-router';

import * as ActionTypes from '../ActionTypes';
import { ThunkAction } from './helpers';
import { IToken, ICredentials, ILoginError } from '../StateTypes';
import globalActions from './global';
import userActions from './user';
import { loginUser, getUserCredentials } from '../api';

const login = (email: string, password: string): ThunkAction => {
  return async (dispatch, getState) => {
    const { reidentification } = getState().loginReducer;

    dispatch(actions.loginInit());
    try {
      const credentials = await getUserCredentials(getState, email, password);

      if (credentials.verify_url) {
        dispatch(userActions.setIdentified(false));
      } else {
        dispatch(userActions.setIdentified(true));
      }

      if (credentials.registration_fee_paid) {
        dispatch(userActions.setPayed(true));
      }

      const body = createBody(credentials, password);
      const token = await loginUser(getState, body);

      dispatch(actions.loginSuccess(token, email, credentials.username));

      if (reidentification) {
        dispatch(replace(`/idenfy`, { username: credentials.username, url: credentials.verify_url }));
      } else {
        dispatch(globalActions.fetchAppData());
      }
    } catch (e) {
      console.error(e.json);

      const loginError: ILoginError = {};

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        dispatch(actions.loginFailure(loginError));
        return;
      }

      if (e.json['Vartotojo profilio klaida']) {
        loginError.email = e.json['Vartotojo profilio klaida'];
      }

      if (e.json.email) {
        loginError.email = e.json.email[0];
      }

      if (e.json.error && e.json.error === 'invalid_grant') {
        loginError.auth = 'Neteisingas slaptaodis';
      }

      dispatch(actions.loginFailure(loginError));
    }
  };
};

const createBody = (credentials: ICredentials, password: string): string => {
  const { client_id, client_secret, username } = credentials;
  const type = 'password';
  const scope = 'read';

  return `grant_type=${type}&client_id=${client_id}&client_secret=${client_secret}&username=${username}&password=${password}&scope=${scope}`;
};

const reidentify = (): ThunkAction => {
  return (dispatch) => {
    dispatch(actions.setReidentification(true));
    dispatch(push('/login'));
  };
};

export const actions = {
  setReidentification: createAction(
    ActionTypes.SET_REIDENTIFICATION,
    (action) => (reidentification: boolean) => action(reidentification)
  ),
  resetLoginFormErrors: createAction(ActionTypes.RESET_LOGIN_FORM_ERRORS),
  loginInit: createAction(ActionTypes.LOGIN_INIT),
  loginSuccess: createAction(
    ActionTypes.LOGIN_SUCCESS,
    (action) => (token: IToken, email: string, username: string) => action({ token, email, username })
  ),
  loginFailure: createAction(ActionTypes.LOGIN_FAILURE, (action) => (error: ILoginError) => action(error)),
  logout: createAction(ActionTypes.LOGOUT),
};

export default { ...actions, login, reidentify };
