import React, { Component } from 'react';
import { StyleSheet, View, ScrollView, Text, AppStateStatus, AppState, Dimensions } from 'react-native';
import { RouteComponentProps } from 'react-router';
import Timer from '@material-ui/icons/Timer';
import LocationOn from '@material-ui/icons/LocationOn';
import { connect } from 'react-redux';
import { differenceInSeconds } from 'date-fns';
import { withTranslation, WithTranslation } from 'react-i18next';

import Item from './Item';

import { IState } from '../../../StateTypes';
import { DarkGray, Black, Gray, Red } from '../../../Colors';
import { formatCountdown, getReservationProducts } from '../../../helpers';
import { getCurrentTime } from '../../../api';
import globalActions from '../../../actions/global';

type ICartCheckoutProps = WithTranslation &
  RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

interface ICartCheckoutState {
  secondsLeft: number;
  show: boolean;
  appState: AppStateStatus;
}

class CartCheckout extends Component<ICartCheckoutProps, ICartCheckoutState> {
  state = {
    secondsLeft: 0,
    show: false,
    appState: AppState.currentState,
  };

  startDate = '';
  interval = 0;

  async componentDidMount() {
    const { token, ttc, createdAt } = this.props;

    AppState.addEventListener('change', this.handleAppStateChange);

    try {
      if (token) {
        const currentDate = (await getCurrentTime(token)).date;
        const secondsLeft = ttc - differenceInSeconds(currentDate, createdAt);

        this.setState(
          {
            secondsLeft: Math.max(0, secondsLeft),
            show: true,
          },
          () => {
            this.interval = setInterval(this.timerCb, 1000);
          }
        );
      }
    } catch (e) {
      console.error(e);
    }
  }

  handleAppStateChange = async (nextAppState: AppStateStatus) => {
    const { token } = this.props;
    const { appState } = this.state;

    // app going into background
    if (appState === 'active' && nextAppState.match(/inactive|background/)) {
      if (token) {
        this.stopCountdown();
        try {
          this.startDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          this.startDate = new Date().toISOString();
        }
      }
    }

    // app back from background
    if (appState.match(/inactive|background/) && nextAppState === 'active') {
      if (token) {
        let currentDate;

        try {
          currentDate = (await getCurrentTime(token)).date;
        } catch (e) {
          console.error(e);
          currentDate = new Date().toISOString();
        }

        const secondsInBackground = differenceInSeconds(currentDate, this.startDate);

        this.setState(
          (state) => ({
            secondsLeft: Math.max(0, state.secondsLeft - secondsInBackground),
          }),
          () => {
            this.interval = setInterval(this.timerCb, 1000);
          }
        );
      }
    }

    this.setState({ appState: nextAppState });
  };

  timerCb = () => {
    const { secondsLeft } = this.state;

    if (secondsLeft > 0) {
      this.setState((state) => ({ secondsLeft: state.secondsLeft - 1 }));
    } else {
      this.stopCountdown();
    }
  };

  onTakeProduct = (productId: number) => {
    this.props.showTakeModal({ vendorId: this.props.vendor.id, productId });
  };

  stopCountdown() {
    clearInterval(this.interval);
  }

  componentWillUnmount() {
    this.stopCountdown();
    AppState.removeEventListener('change', this.handleAppStateChange);
  }

  render() {
    const { vendor, reservationProducts, isVending, t } = this.props;
    const { secondsLeft, show } = this.state;

    return (
      <View style={{ flex: 1, height: Dimensions.get('window').height - 56 }}>
        <ScrollView>
          <View style={{ paddingTop: 16, paddingBottom: 36 }}>
            <Text style={styles.sectionTitle}>{t('reservation')}</Text>

            <View style={{ marginTop: 16, marginBottom: 24 }}>
              <View
                style={{
                  flexDirection: 'row',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <Timer style={{ marginRight: 12, color: Black, fontSize: 70 }} />

                <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                  <LocationOn style={{ marginRight: 8, color: Black, fontSize: 24 }} />
                  <View>
                    <Text style={styles.name}>{vendor.name}</Text>
                    <Text style={styles.address}>{vendor.address}</Text>
                  </View>
                </View>
              </View>

              <View style={styles.countdown}>
                {show ? (
                  <Text
                    style={{
                      fontSize: 40,
                      color: show && secondsLeft < 60 ? Red : Black,
                    }}
                  >
                    {formatCountdown(secondsLeft)}
                  </Text>
                ) : (
                  <Text>{t('loading')}...</Text>
                )}
              </View>

              <Text style={{ marginHorizontal: 36, textAlign: 'center', color: Gray, marginTop: 12 }}>
                {t('info')}
              </Text>
            </View>

            <Text style={styles.sectionTitle}>{t('productServe')}</Text>

            {reservationProducts.map((product) => (
              <Item
                key={product.id}
                vendor={vendor}
                item={product}
                onTakeProduct={this.onTakeProduct}
                isVending={isVending}
              />
            ))}
          </View>
        </ScrollView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {},
  imageWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 50,
    height: 70,
    marginRight: 16,
    borderWidth: 1,
  },
  sectionTitle: {
    marginLeft: 16,
    color: DarkGray,
  },
  countdown: {
    height: 50,
    justifyContent: 'center',
    alignItems: 'center',
  },
  time: {
    color: Black,
    fontSize: 40,
  },
  info: {
    marginHorizontal: 36,
    textAlign: 'center',
    color: Gray,
  },
  icon: {
    marginRight: 8,
    color: Black,
  },
  name: {
    color: Black,
    fontSize: 16,
  },
  address: {
    color: DarkGray,
  },
});

const mapStateToProps = (state: IState) => {
  const reservation = state.userReducer.reservations[0];
  const vendor = state.globalReducer.vendors.find((vendor) => vendor.id === reservation.vendor)!;
  return {
    vendor,
    reservationProducts: getReservationProducts(reservation, state.globalReducer.products),
    takeModal: state.globalReducer.takeModal,
    ttc: state.userReducer.membership!.ttc,
    isVending: reservation.status === 3,
    createdAt: reservation.created_at,
    token: state.loginReducer.token?.access_token,
  };
};

const localActions = {
  showTakeModal: globalActions.showTakeModal,
  hideTakeModal: globalActions.hideTakeModal,
};

export default withTranslation('cartCheckout')(connect(mapStateToProps, localActions)(CartCheckout));
