import React, { Component } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import { RouteComponentProps } from 'react-router';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import SuccessLogo from './SuccessLogo';
import loginActions from '../../../actions/login';
import PrimaryButton from '../../../components/PrimaryButton';
import { Black, Gray } from '../../../Colors';

type ISuccessProps = WithTranslation & RouteComponentProps & typeof localActions;

class Success extends Component<ISuccessProps> {
  onPress = () => {
    const { history, setReidentification } = this.props;

    setReidentification(false);
    history.replace('/login');
  };

  render() {
    const { t } = this.props;

    return (
      <View style={styles.wrapper}>
        <View style={styles.logoWrapper}>
          <SuccessLogo />
        </View>

        <Text style={styles.title}>{t('success.identityApproved')}</Text>
        <Text style={styles.text}>{t('success.loginAgain')}</Text>

        <PrimaryButton width={180} onPress={this.onPress}>
          {t('success.login').toUpperCase()}
        </PrimaryButton>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 36,
  },
  logoWrapper: {
    alignSelf: 'center',
    marginTop: 16,
  },
  title: {
    marginBottom: 4,
    color: Black,
    fontSize: 20,
    textAlign: 'center',
  },
  text: {
    marginBottom: 36,
    color: Gray,
    fontSize: 16,
    textAlign: 'center',
  },
});

const localActions = {
  setReidentification: loginActions.setReidentification,
};

export default withTranslation('idenfy')(connect(null, localActions)(Success));
