import React, { Component } from 'react';
import { StyleSheet, View, ScrollView } from 'react-native';
import { RouteComponentProps } from 'react-router';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';
import { LinearProgress } from '@material-ui/core';

import Agreement from './Agreement';
import userActions from '../../actions/user';
import ChangeLanguage from '../../ChangeLanguage';
import MainLogo from '../../components/MainLogo';
import TextField from '../../components/TextFIeld';
import EyeIcon from '../../components/EyeIcon';
import PrimaryButton from '../../components/PrimaryButton';
import { isRegisterFormValid } from '../../helpers';
import { IState } from '../../StateTypes';

type IRegisterProps = RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions &
  WithTranslation;

interface IRegisterState {
  email: string;
  password: string;
  password2: string;
  showPassword: boolean;
  showPassword2: boolean;
  passwordsMatch: boolean;
  showAgreement: boolean;
}

class Register extends Component<IRegisterProps, IRegisterState> {
  state = {
    email: '',
    password: '',
    password2: '',
    showPassword: false,
    showPassword2: false,
    passwordsMatch: true,
    showAgreement: true,
  };

  onRegisterPress = async () => {
    const { resetErrors, createUser } = this.props;
    const { email, password, password2 } = this.state;

    resetErrors();

    if (password !== password2) {
      return this.setState({ passwordsMatch: false });
    } else {
      this.setState({ passwordsMatch: true });
    }

    if (isRegisterFormValid(email, password, password2)) {
      createUser({ email, password });
    }
  };

  componentWillUnmount() {
    const { error, resetErrors } = this.props;

    if (error) {
      resetErrors();
    }
  }

  render() {
    const { error, creatingUser, t } = this.props;
    const {
      email,
      password,
      showPassword,
      password2,
      showPassword2,
      passwordsMatch,
      showAgreement,
    } = this.state;

    const isFormValid = isRegisterFormValid(email, password, password2);
    const emailError = error && error.email;
    let passwordError = error && error.password;

    if (!passwordError && !passwordsMatch) {
      passwordError = t('passDontMatch');
    }

    if (showAgreement) {
      return <Agreement onAccept={() => this.setState({ showAgreement: false })} />;
    }

    return (
      <View style={{ flex: 1 }}>
        {creatingUser && (
          <View style={{ position: 'absolute', top: 0, left: 0, right: 0, zIndex: 1000 }}>
            <LinearProgress />
          </View>
        )}

        <ScrollView style={styles.wrapper} keyboardShouldPersistTaps='handled'>
          <View style={{ paddingBottom: 8 }}>
            <View style={styles.logoWrapper}>
              <MainLogo width={152} height={96} />
            </View>

            <View style={{ marginBottom: 16 }}>
              <TextField
                label={t('login:email')}
                type='email'
                value={email}
                onChange={(e) => this.setState({ email: e.target.value })}
                autoCapitalize='none'
                errorText={emailError}
              />

              <View>
                <TextField
                  label={t('login:password')}
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => this.setState({ password: e.target.value })}
                  autoCapitalize='none'
                  helperText={t('passHelper')}
                  errorText={passwordError}
                />
                <EyeIcon
                  style={{ position: 'absolute', top: 14, right: 8, padding: 4 }}
                  on={showPassword}
                  onPress={() => this.setState({ showPassword: !showPassword })}
                />
              </View>

              <View>
                <TextField
                  label={t('repeatPassword')}
                  type={showPassword2 ? 'text' : 'password'}
                  value={password2}
                  onChange={(e) => this.setState({ password2: e.target.value })}
                  autoCapitalize='none'
                  errorText={passwordError}
                />
                <EyeIcon
                  style={{ position: 'absolute', top: 14, right: 8, padding: 4 }}
                  on={showPassword2}
                  onPress={() => this.setState({ showPassword2: !showPassword2 })}
                />
              </View>
            </View>

            <PrimaryButton width={152} disabled={!isFormValid} onPress={this.onRegisterPress}>
              {t('register').toUpperCase()}
            </PrimaryButton>
          </View>
        </ScrollView>
        <ChangeLanguage />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 36,
    alignSelf: 'center',
    width: '100%',
    maxWidth: 450,
  },
  logoWrapper: {
    alignSelf: 'center',
    marginTop: 64,
    marginBottom: 64,
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 68,
    height: 68,
    marginTop: -24,
    backgroundColor: 'white',
    borderRadius: 68 / 2,
    elevation: 3,
  },
});

const mapStateToProps = (state: IState) => {
  return {
    error: state.userReducer.error,
    creatingUser: state.userReducer.creatingUser,
  };
};

const localActions = {
  createUser: userActions.create,
  resetErrors: userActions.resetRegisterFormErrors,
};

export default withTranslation('register')(connect(mapStateToProps, localActions)(Register));
