import React, { Component } from 'react';
import { DrawerLayoutAndroid, StyleSheet, View, TouchableOpacity, Dimensions } from 'react-native';
import { connect } from 'react-redux';
import DrawerComp from '@material-ui/core/SwipeableDrawer';
import * as Animatable from 'react-native-animatable';

import NavigationView from './NavigationView';
import globalActions from '../../../actions/global';
import { IState } from '../../../StateTypes';

type IDrawerProps = ReturnType<typeof mapStateToProps> & typeof localActions;

const AnimTouchable = Animatable.createAnimatableComponent(TouchableOpacity);

interface IDrawerState {
  screenWidth: number;
}

class Drawer extends Component<IDrawerProps, IDrawerState> {
  state = {
    screenWidth: Dimensions.get('window').width,
  };

  componentDidMount() {
    Dimensions.addEventListener('change', this.onOrientationChange);
  }

  onOrientationChange = () => {
    this.setState({ screenWidth: Dimensions.get('window').width });
  };

  componentWillUnmount() {
    Dimensions.removeEventListener('change', this.onOrientationChange);
  }

  render() {
    const { isDrawerOpen, openDrawer, closeDrawer } = this.props;

    if (this.state.screenWidth < 1000) {
      return (
        <>
          {isDrawerOpen && (
            <AnimTouchable
              style={{
                position: 'absolute',
                left: 0,
                top: 0,
                right: 0,
                bottom: 0,
                backgroundColor: 'rgba(0, 0, 0, 0.5)',
                zIndex: 1000,
              }}
              animation='fadeIn'
              easing='ease-out'
              duration={200}
              onPress={() => {
                this.props.closeDrawer();
              }}
              activeOpacity={1}
            ></AnimTouchable>
          )}
          {isDrawerOpen && (
            <AnimTouchable
              style={{ position: 'absolute', height: '100%', width: 280, zIndex: 2000 }}
              animation='slideInLeft'
              easing='ease-out'
              duration={200}
              activeOpacity={1}
              onPress={() => {}}
            >
              <NavigationView persistent={false} />
            </AnimTouchable>
          )}
        </>
      );
    } else {
      return (
        <View>
          <NavigationView persistent={true} />
        </View>
      );
    }

    return (
      <DrawerComp open={isDrawerOpen} onOpen={openDrawer} onClose={closeDrawer}>
        <NavigationView />
      </DrawerComp>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
});

const mapStateToProps = (state: IState) => ({
  isDrawerOpen: state.globalReducer.isDrawerOpen,
});

const localActions = {
  openDrawer: globalActions.openDrawer,
  closeDrawer: globalActions.closeDrawer,
};

export default connect(mapStateToProps, localActions)(Drawer);
