import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity } from 'react-native';
import { DarkGray, Primary, LightGray, Gray } from '../../../../Colors';

interface ICounterProps {
  count: number;
  subtractDisabled: boolean;
  addDisabled: boolean;
  onSubtract: () => void;
  onAdd: () => void;
}

class Counter extends Component<ICounterProps> {
  render() {
    const { count, subtractDisabled, addDisabled, onSubtract, onAdd } = this.props;

    return (
      <View style={styles.wrapper}>
        <TouchableOpacity
          activeOpacity={0.8}
          disabled={subtractDisabled}
          onPress={onSubtract}
          style={{
            justifyContent: 'center',
            alignItems: 'center',
            width: 36,
            height: 36,
            borderRadius: 36 / 2,
            backgroundColor: subtractDisabled ? LightGray : Primary,
          }}
        >
          <Text style={{ marginTop: -2, fontSize: 24, color: subtractDisabled ? DarkGray : 'white' }}>-</Text>
        </TouchableOpacity>

        <View style={styles.count}>
          <Text style={styles.countValue}>{count}</Text>
        </View>

        <TouchableOpacity
          activeOpacity={0.8}
          onPress={onAdd}
          style={{
            justifyContent: 'center',
            alignItems: 'center',
            width: 36,
            height: 36,
            borderRadius: 36 / 2,
            backgroundColor: addDisabled ? LightGray : Primary,
          }}
        >
          <Text style={{ marginTop: -2, fontSize: 24, color: addDisabled ? DarkGray : 'white' }}>+</Text>
        </TouchableOpacity>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    backgroundColor: 'white',
    borderRadius: 50,
  },
  button: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 36,
    height: 36,
    borderRadius: 36 / 2,
  },
  icon: {
    marginTop: -2,
    fontSize: 24,
  },
  count: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 30,
  },
  countValue: {
    color: Gray,
    fontWeight: 'bold',
  },
});

export default Counter;
