import * as React from 'react';
import { Component } from 'react';
import { BackHandler, View, StatusBar, Platform } from 'react-native';
import WebView from 'react-native-webview';
import { connect } from 'react-redux';
import { RouteComponentProps, Redirect } from 'react-router';
import { withTranslation, WithTranslation } from 'react-i18next';

import WarningModal from '../../components/WarningModal';
import Marker from '../../components/Marker';
import Message from '../../components/Message';
import authorizeActions from '../../actions/authorize';
import paymentActions from '../../actions/payment';
import { IState } from '../../StateTypes';
import { Primary } from '../../Colors';
import AppBar from '../../components/AppBar';

type IAuthorizeProps = RouteComponentProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;
type IAuthorizeState = {
  showWarning: boolean;
};

class Authorize extends Component<IAuthorizeProps, IAuthorizeState> {
  state = {
    showWarning: false,
  };

  formRef = React.createRef<HTMLFormElement>();

  componentDidMount() {
    const { loggedIn } = this.props;

    if (loggedIn) {
      StatusBar.setBackgroundColor(Primary);
      BackHandler.addEventListener('hardwareBackPress', this.handleBackPress);

      if (Platform.OS === 'web') {
        this.formRef.current!.submit();
      }
    }
  }

  handleBackPress = () => {
    const { showWarning } = this.state;

    if (showWarning) {
      this.setState({ showWarning: false });
    } else {
      this.setState({ showWarning: true });
    }

    return true;
  };

  onLeave = () => {
    this.props.clearPaymentPoll();
    this.props.cancelPayment();
    this.props.history.goBack();
  };

  componentWillUnmount() {
    this.props.clearPaymentPoll();
    this.props.cancelPayment();

    StatusBar.setBackgroundColor('rgba(0, 0, 0, 0.2)');
    BackHandler.removeEventListener('hardwareBackPress', this.handleBackPress);
  }

  render() {
    const { html, info, loggedIn, t } = this.props;
    const { showWarning } = this.state;

    if (!loggedIn) {
      return <Redirect to='/' />;
    }

    if (!info) {
      return null;
    }

    return (
      <View style={{ flex: 1 }}>
        {Platform.OS === 'web' ? (
          <>
            <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center', paddingTop: 128 }}>
              <View
                style={{
                  width: 96,
                  height: 96,
                  borderRadius: 96 / 2,
                  backgroundColor: Primary,
                  marginBottom: 8,
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <Marker width={82} height={82} type='light' />
              </View>
              <Message title={`${t('authorizationPending')}...`} subtitle={t('dontCloseWindow')} />
            </View>
            <form target='_blank' ref={this.formRef} method='POST' action={info.url}>
              {/* <button type='submit'>{t('clickHere')}</button> */}
              <input type='hidden' name='PaReq' value={info.data} />
              <input type='hidden' name='TermUrl' value={info.TermUrl} />
              <input type='hidden' name='MD' value={info.md} />
            </form>
          </>
        ) : (
          <WebView source={{ html }} />
        )}

        {showWarning && (
          <WarningModal
            message='Ar tikrai norite nutraukti mokjim?'
            onClose={() => this.setState({ showWarning: false })}
            onLeave={this.onLeave}
          />
        )}
      </View>
    );
  }
}

const mapStateToProps = (state: IState) => ({
  html: state.authorizeReducer.html,
  info: state.authorizeReducer.info,
  loggedIn: state.loginReducer.loggedIn,
});

const localActions = {
  pollPayment: authorizeActions.pollPayment,
  clearPaymentPoll: authorizeActions.clearPaymentPoll,
  cancelPayment: paymentActions.cancelPayment,
};

export default withTranslation('authorize')(connect(mapStateToProps, localActions)(Authorize));
