import React, { Component, ChangeEvent, FocusEvent } from 'react';
import { StyleSheet, Text, View, Dimensions, ScrollView, StatusBar, TouchableOpacity } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { connect } from 'react-redux';
import Cards from 'react-credit-cards';
import 'react-credit-cards/es/styles-compiled.css';
import * as valid from 'card-validator';
import { withTranslation, WithTranslation } from 'react-i18next';

import MiniCreditCard from '../../components/MiniCreditCard';
import PrimaryButton from '../../components/PrimaryButton';

import paymentActions from '../../actions/payment';
import globalActions from '../../actions/global';
import { getPhoneOrientation, formatCurrency, OrientationType } from '../../helpers';
import { IState } from '../../StateTypes';
import { Red, Primary, LightGray } from '../../Colors';
import TextField from '@material-ui/core/TextField';

export interface ICardValuesFormatted {
  pan: string;
  exp_month: number;
  exp_year: number;
  cvc: string;
  holder: string;
}

type ICardModalProps = WithTranslation & ReturnType<typeof mapStateToProps> & typeof localActions;

type Focused = 'name' | 'number' | 'expiry' | 'cvc';

interface ICardModalState {
  cvc: string;
  expiry: string;
  focus: Focused;
  name: string;
  number: string;
  numberValid: boolean;
  expiryValid: boolean;
  cvcValid: boolean;
  nameValid: boolean;
  orientation: OrientationType;
  showCardDetails: boolean;
  selectedCard: string | undefined;
}

class CardModal extends Component<ICardModalProps, ICardModalState> {
  state = {
    cvc: '',
    expiry: '',
    focus: 'name' as Focused,
    name: '',
    number: '',
    numberValid: true,
    expiryValid: true,
    cvcValid: true,
    nameValid: true,
    orientation: getPhoneOrientation(),
    showCardDetails: false,
    selectedCard: undefined,
  };

  cvcSize = 3;

  numberRef = React.createRef<HTMLInputElement>();
  expiryRef = React.createRef<HTMLInputElement>();
  cvcRef = React.createRef<HTMLInputElement>();
  nameRef = React.createRef<HTMLInputElement>();

  componentDidMount() {
    this.numberRef.current?.focus();
    Dimensions.addEventListener('change', this.onOrientationChange);

    this.onOrientationChange();
  }

  handleInputFocus = (e: FocusEvent<HTMLInputElement>) => {
    this.setState({ focus: e.target.name as Focused });
  };

  handleInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    if (name === 'number') {
      let newValue = value.replace(/[A-Za-z]| /g, '');

      const { card, isPotentiallyValid, isValid } = valid.number(newValue);

      if (card) {
        this.cvcSize = card.code.size;

        if (newValue.length > Math.max(...card.lengths)) {
          return;
        }

        for (const ky in card.gaps) {
          const key = Number(ky);

          if (newValue.length > card.gaps[key] + key) {
            newValue = `${newValue.slice(0, card.gaps[key] + key)} ${newValue.slice(card.gaps[key] + key)}`;
          }
        }
      } else {
        if (newValue.length > 16) {
          return;
        }

        const defaultGaps = [4, 8, 12];

        for (const ky in defaultGaps) {
          const key = Number(ky);

          if (newValue.length > defaultGaps[key] + key) {
            newValue = `${newValue.slice(0, defaultGaps[key] + key)} ${newValue.slice(
              defaultGaps[key] + key
            )}`;
          }
        }
      }

      if (!isPotentiallyValid) {
        this.setState({ [name]: newValue, numberValid: false });
      } else {
        this.setState({ [name]: newValue, numberValid: true }, () => {
          if (isValid && !this.isFormValid()) {
            this.expiryRef.current?.focus();
            this.setState({ focus: 'expiry' });
          }
        });
      }
    }

    if (name === 'name') {
      let newValue = value.replace(/[0-9]/g, '');

      this.setState({ [name]: newValue, nameValid: true });
    }

    if (name === 'expiry') {
      let newValue = value.replace(/[A-Za-z]| |\//g, '');

      const { isPotentiallyValid, isValid } = valid.expirationDate(newValue);

      if (newValue.length > 6) {
        return;
      }

      if (newValue.length > 2) {
        newValue = `${newValue.slice(0, 2)}/${newValue.slice(2)}`;
      }

      if (!isPotentiallyValid) {
        this.setState({ [name]: newValue, expiryValid: false });
      } else {
        this.setState({ [name]: newValue, expiryValid: true }, () => {
          if (isValid && newValue.length > 4 && !this.isFormValid()) {
            this.cvcRef.current?.focus();
            this.setState({ focus: 'cvc' });
          }
        });
      }
    }

    if (name === 'cvc') {
      const { isPotentiallyValid, isValid } = valid.cvv(value, this.cvcSize);

      if (value.length > this.cvcSize) {
        return;
      }

      if (!isPotentiallyValid) {
        this.setState({ [name]: value, cvcValid: false });
      } else {
        this.setState({ [name]: value, cvcValid: true }, () => {
          if (isValid && !this.isFormValid()) {
            this.nameRef.current?.focus();
            this.setState({ focus: 'name' });
          }
        });
      }
    }
  };

  onConfirm = () => {
    const { number, expiry, cvc, name } = this.state;

    if (this.isFormValid()) {
      const exp = expiry.split('/');
      let exp_month = exp[0];
      let exp_year = exp[1];
      if (exp_month.startsWith('0')) {
        exp_month = exp_month.slice(1);
      }
      exp_year = `20${exp_year}`;

      const values = {
        pan: number.split(' ').join(''),
        holder: name.trim().toUpperCase(),
        exp_month: Number(exp_month),
        exp_year: Number(exp_year),
        cvc,
      };
      this.props.onConfirm(values);
    } else {
      if (!valid.number(number).isValid) {
        this.setState({ numberValid: false });
      }

      if (!valid.expirationDate(expiry).isValid) {
        this.setState({ expiryValid: false });
      }

      if (!valid.cvv(cvc).isValid) {
        this.setState({ cvcValid: false });
      }

      if (!(name.trim().length > 0)) {
        this.setState({ nameValid: false });
      }
    }
  };

  payRecurrent = () => {
    const { pastPayments, onConfirm, showToastMessage } = this.props;
    const { selectedCard } = this.state;
    if (selectedCard) {
      const selectedPayment = pastPayments.find((item) => item.payment_id === selectedCard)!;
      onConfirm(selectedPayment);
    } else {
      showToastMessage(`Pasirinkite kortel`);
    }
  };

  onOrientationChange = () => this.setState({ orientation: getPhoneOrientation() });

  onCancel = () => this.props.hideCardModal();

  componentWillUnmount() {
    const { currentReservationId, cancelPayment } = this.props;

    if (currentReservationId) {
      cancelPayment();
    }

    Dimensions.removeEventListener('change', this.onOrientationChange);
  }

  isFormValid = () => {
    const { isValid: numberValid } = valid.number(this.state.number);
    const { isValid: expiryValid } = valid.expirationDate(this.state.expiry);
    const { isValid: cvcValid } = valid.cvv(this.state.cvc);
    const nameValid = this.state.name.trim().length > 0;

    return numberValid && expiryValid && cvcValid && nameValid;
  };

  render() {
    const { amount, pastPayments, t } = this.props;
    const { orientation, showCardDetails, selectedCard } = this.state;
    const { focus, number, name, expiry, cvc, numberValid, expiryValid, cvcValid, nameValid } = this.state;

    const statusBarHeight = StatusBar.currentHeight || 0;
    const isPortrait = orientation === 'portrait';

    const valid = this.isFormValid();

    return (
      <Animatable.View
        style={styles.wrapper}
        animation='fadeIn'
        easing='ease-out'
        duration={150}
        useNativeDriver
      >
        <ScrollView style={{ alignSelf: 'stretch' }} keyboardShouldPersistTaps='handled'>
          <View
            style={{
              justifyContent: 'space-between',
              paddingTop: isPortrait ? statusBarHeight + 24 : statusBarHeight + 12,
              paddingBottom: 36,
              marginHorizontal: 16,
            }}
          >
            {pastPayments.length > 0 && !showCardDetails ? (
              <>
                <Text style={{ marginBottom: 16 }}>
                  Pasirinkite vien i anksiau naudot korteli arba veskite naujus duomenis
                </Text>
                <View
                  style={{
                    marginBottom: 16,
                  }}
                >
                  {pastPayments.map((item) => (
                    <View key={item.payment_id}>
                      <MiniCreditCard
                        item={item}
                        selected={selectedCard === item.payment_id}
                        onSelect={() => this.setState({ selectedCard: item.payment_id })}
                      />
                      <View
                        style={{
                          borderBottomWidth: 1,
                          borderBottomColor: LightGray,
                        }}
                      ></View>
                    </View>
                  ))}
                </View>

                <PrimaryButton disabled={!selectedCard} width={300} height={46} onPress={this.payRecurrent}>
                  <Text style={{ fontSize: 16 }}>
                    MOKTI <Text style={{ fontWeight: 'bold' }}>{formatCurrency(amount)}</Text>
                  </Text>
                </PrimaryButton>

                <Text style={{ textAlign: 'center', fontSize: 14, marginVertical: 24 }}>arba</Text>

                {selectedCard ? (
                  <TouchableOpacity
                    activeOpacity={0.8}
                    style={styles.enterCardDetails}
                    onPress={() => this.setState({ showCardDetails: true })}
                  >
                    <Text style={{ color: Primary }}>VESTI NAUJUS KORTELS DUOMENIS</Text>
                  </TouchableOpacity>
                ) : (
                  <PrimaryButton
                    width={300}
                    height={46}
                    onPress={() => this.setState({ showCardDetails: true })}
                  >
                    VESTI NAUJUS KORTELS DUOMENIS
                  </PrimaryButton>
                )}
              </>
            ) : (
              <>
                <View style={{ marginBottom: 12 }}>
                  <Cards
                    cvc={cvc}
                    expiry={expiry}
                    focused={focus}
                    name={name}
                    number={number}
                    locale={{
                      valid: 'VALID THRU',
                    }}
                    placeholders={{
                      name: 'Vardas pavard',
                    }}
                  />

                  <View style={{ alignSelf: 'center', width: '100%', maxWidth: 600, marginTop: 24 }}>
                    <TextField
                      label='Kortels numeris'
                      type='text'
                      name='number'
                      value={number}
                      onFocus={this.handleInputFocus}
                      onChange={this.handleInputChange}
                      autoCapitalize='none'
                      variant='outlined'
                      style={{ marginBottom: 12 }}
                      error={!numberValid}
                      helperText={!numberValid && 'Netinkamas kortels numeris'}
                      inputRef={this.numberRef}
                    />

                    <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                      <View style={{ width: '45%' }}>
                        <TextField
                          label='Galiojimo data'
                          type='text'
                          name='expiry'
                          value={expiry}
                          onFocus={this.handleInputFocus}
                          onChange={this.handleInputChange}
                          autoCapitalize='none'
                          variant='outlined'
                          style={{ marginBottom: 12 }}
                          error={!expiryValid}
                          helperText={!expiryValid && 'Netinakama galiojimo data'}
                          inputRef={this.expiryRef}
                        />
                      </View>

                      <View style={{ width: '45%' }}>
                        <TextField
                          label='Saugos kodas'
                          type='number'
                          name='cvc'
                          value={cvc}
                          onFocus={this.handleInputFocus}
                          onChange={this.handleInputChange}
                          autoCapitalize='none'
                          variant='outlined'
                          style={{ marginBottom: 12 }}
                          error={!cvcValid}
                          helperText={!cvcValid && 'Netinakama CVC saugos kodas'}
                          inputRef={this.cvcRef}
                        />
                      </View>
                    </View>

                    <TextField
                      label='Kortels savininkas'
                      type='text'
                      name='name'
                      value={name}
                      onFocus={this.handleInputFocus}
                      onChange={this.handleInputChange}
                      variant='outlined'
                      style={{ marginBottom: 12 }}
                      error={!nameValid}
                      helperText={!nameValid && 'Upildykite lauk'}
                      inputRef={this.nameRef}
                    />
                  </View>
                </View>

                <PrimaryButton disabled={!valid} width={300} height={46} onPress={this.onConfirm}>
                  <Text style={{ fontSize: 16 }}>
                    MOKTI <Text style={{ fontWeight: 'bold' }}>{formatCurrency(amount)}</Text>
                  </Text>
                </PrimaryButton>
              </>
            )}

            <TouchableOpacity activeOpacity={0.8} style={styles.cancel} onPress={this.onCancel}>
              <Text style={{ color: Red }}>{t('cancel').toUpperCase()}</Text>
            </TouchableOpacity>
          </View>
        </ScrollView>
      </Animatable.View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    position: 'absolute',

    right: 0,

    left: 0,
    backgroundColor: 'hsl(0, 0%, 96%)',
    height: Dimensions.get('window').height - 56,
  },
  incomplete: {
    paddingVertical: 12,
    marginHorizontal: -16,
    marginBottom: 16,
    backgroundColor: Red,
  },
  incompleteText: {
    color: 'white',
    textAlign: 'center',
    fontSize: 14,
  },
  enterCardDetails: {
    justifyContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    borderRadius: 25,
    width: 300,
    height: 46,
    backgroundColor: undefined,
    borderWidth: 1,
    borderColor: Primary,
  },
  cancel: {
    justifyContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    borderRadius: 25,
    width: 150,
    height: 36,
    marginTop: 24,
  },
});

const mapStateToProps = (state: IState) => ({
  amount: state.paymentReducer.cardModal.amount,
  onConfirm: state.paymentReducer.cardModal.onConfirm,
  pastPayments: state.paymentReducer.pastPayments,
  currentReservationId: state.paymentReducer.currentReservationId,
});

const localActions = {
  cancelPayment: paymentActions.cancelPayment,
  showToastMessage: globalActions.initToastMessage,
  hideCardModal: paymentActions.hideCardModal,
};

export default withTranslation()(connect(mapStateToProps, localActions)(CardModal));
