import {
  IApiLoginUser,
  IApiGetUserCredentials,
  IApiGetUserMembership,
  IApiGetVendorProducts,
  IApiVend,
  IApiCreateUser,
  IApiPollUser,
  IApiGetPaymentDetails,
  IApiGetPolicies,
  IApiGetTime,
  IApiGetFees,
  IApiGetVault,
  IApiPay,
  IApiPollPayment,
} from './types';
import { IState, IReservation, IVendor, IProduct, IPaymentInstrument } from '../StateTypes';
import { ICardValuesFormatted } from '../pages/Home/CardModal';

let domainName = 'artimiausias.impressbox.eu';

// if (__DEV__) {
//   domainName = 'artimiausias.impressbox.eu';
// } else {
//   domainName = 'www.artimiausias.lt';
// }

export type GetState = () => IState;

export class ApiError extends Error {
  json: object;

  constructor(json: object, ...args: any[]) {
    super(...args);
    this.json = json;
  }
}

const makeFetch = (url: string, getState: GetState, fetchConfig: any = {}): Promise<any> => {
  const token = getState().loginReducer.token;

  if (!fetchConfig.headers) {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');

    if (url !== '/api/policies/' && url !== '/api/credentials/' && !url.startsWith('/api/users/?username=')) {
      const access_token = token && token.access_token;
      headers.append('Authorization', `Bearer ${access_token}`);
    }

    headers.append('Accept-Language', 'lt');
    fetchConfig.headers = headers;
  }

  return fetch(`https://${domainName}${url}`, fetchConfig).then(async (res) => {
    //console.log(res);

    if (res.ok) {
      let jsonRes;

      if (url === '/api/init/') {
        jsonRes = await res.text();
      } else {
        jsonRes = await res.json();
      }

      return jsonRes;
    } else if (res.status >= 500) {
      throw new ApiError({
        title: 'unexpectedErrorTitle',
        message: 'unexpectedErrorMessage',
        statusCode: res.status,
      });
    } else {
      const err = await res.json();
      err.statusCode = res.status;
      throw new ApiError(err);
    }
  });
};

// Policies
export const getPolicies = (): Promise<IApiGetPolicies> => {
  const headers = new Headers();
  headers.append('Content-Type', 'application/json');
  headers.append('Accept-Language', 'lt');

  return fetch(`https://${domainName}/api/policies/`, {
    headers,
  }).then(async (res) => {
    //console.log(res);

    if (res.ok) {
      return await res.json();
    } else if (res.status >= 500) {
      throw new ApiError({
        title: 'unexpectedErrorTitle',
        message: 'unexpectedErrorMessage',
        statusCode: res.status,
      });
    } else {
      const err = await res.json();
      err.statusCode = res.status;
      throw new ApiError(err);
    }
  });
};

// Init
export const apiInit = (getState: GetState): Promise<IApiLoginUser> => makeFetch('/api/init/', getState);

// Login
export const loginUser = (getState: GetState, body: any): Promise<IApiLoginUser> => {
  return makeFetch('/o/token/', getState, {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Accept-Language': 'lt',
    },
    method: 'POST',
    body,
  });
};

export const getUserCredentials = (
  getState: GetState,
  email: string,
  password: string
): Promise<IApiGetUserCredentials> => {
  return makeFetch('/api/credentials/', getState, {
    method: 'POST',
    body: JSON.stringify({ email, password }),
  });
};

// User
export const createUser = (
  getState: GetState,
  user: { email: string; password: string }
): Promise<IApiCreateUser> => {
  return makeFetch('/api/users/', getState, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(user),
  });
};

export const pollUser = (getState: GetState, username: string): Promise<IApiPollUser> => {
  return makeFetch(`/api/users/?username=${username}`, getState);
};

export const getUserMembership = (getState: GetState): Promise<IApiGetUserMembership> => {
  return makeFetch('/api/membership/?self', getState);
};

export const getUserReservations = (getState: GetState): Promise<IReservation[]> => {
  return makeFetch('/api/reservation/', getState);
};

// Global
export const getAllVendors = (getState: GetState): Promise<IVendor[]> => {
  return makeFetch('/api/vendor/', getState);
};

export const getAllProducts = (getState: GetState): Promise<IProduct[]> => {
  return makeFetch('/api/product/', getState);
};

// Vendor
export const getVendorProducts = (getState: GetState, vendorId: number): Promise<IApiGetVendorProducts> => {
  return makeFetch(`/api/reservation/${vendorId}/`, getState);
};

export const createReservation = (
  getState: GetState,
  vendorId: number,
  data: { [productId: number]: number },
  instaVend: boolean
): Promise<IReservation[]> => {
  return makeFetch(`/api/reservation/${vendorId}/${instaVend ? '1/' : '0/'}`, getState, {
    method: 'POST',
    body: JSON.stringify(data),
  });
};

export const vend = (getState: GetState, productId: number): Promise<IApiVend> => {
  return makeFetch(`/api/vend/?${productId}`, getState);
};

// Fees
export const getFees = (getState: GetState): Promise<IApiGetFees> => {
  return makeFetch(`/api/fees/`, getState);
};

// Vault
export const getVault = (getState: GetState): Promise<IApiGetVault> => {
  return makeFetch(`/api/vault/`, getState);
};

export const addToVault = (
  getState: GetState,
  body: string,
  token: string
): Promise<IApiGetPaymentDetails> => {
  return makeFetch(`/api/vault/`, getState, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Accept-Language': 'lt',
      Authorization: `Bearer ${token}`,
    },
    body,
  });
};

export const deleteLastVaultOrder = (getState: GetState): Promise<IApiGetVault> => {
  return makeFetch(`/api/vault/`, getState, {
    method: 'DELETE',
  });
};

// Payment
export const getPaymentDetails = (getState: GetState): Promise<IApiGetPaymentDetails> => {
  return makeFetch(`/api/paymentctx/`, getState);
};

export const pollPayment = (getState: GetState, orderId: string): Promise<IApiPollPayment> => {
  return makeFetch(`/api/paymentctx/?order_id=${orderId}`, getState);
};

export const pay = (
  getState: GetState,
  type: string,
  data: {
    amount: number;
    order_id: string;
    reservation: null | number;
    payment_instrument: ICardValuesFormatted | IPaymentInstrument;
  }
): Promise<IApiPay> => {
  return makeFetch(`/api/paymentctx/${type}/`, getState, {
    method: 'POST',
    body: JSON.stringify(data),
  });
};

export const onPaymentFailure = (getState: GetState, reservationId: number): Promise<{}> => {
  return makeFetch(`/api/paymentctx/`, getState, {
    method: 'DELETE',
    body: JSON.stringify({ reservation: reservationId }),
  });
};

// Time
export const getCurrentTime = (token: string): Promise<IApiGetTime> => {
  const headers = new Headers();
  headers.append('Content-Type', 'application/json');
  headers.append('Accept-Language', 'lt');
  headers.append('Authorization', `Bearer ${token}`);

  return fetch(`https://${domainName}/api/time/`, {
    headers,
  }).then(async (res) => {
    console.log(res);

    if (res.ok) {
      return await res.json();
    } else if (res.status >= 500) {
      throw new ApiError({
        title: 'unexpectedErrorTitle',
        message: 'unexpectedErrorMessage',
        statusCode: res.status,
      });
    } else {
      const err = await res.json();
      err.statusCode = res.status;
      throw new ApiError(err);
    }
  });
};
