import React, { Component } from 'react';
import { StyleSheet, View, ScrollView, Text, Image } from 'react-native';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { withTranslation, WithTranslation } from 'react-i18next';
import Mood from '@material-ui/icons/Mood';

import Message from '../../../components/Message';

import { IState, ICartProduct } from '../../../StateTypes';
import { DarkGray, Black, Gray, Primary, LightGray } from '../../../Colors';
import { formatCurrency } from '../../../helpers';
import VaultDebtProduct from '../../../components/VaultDebtProduct';

type IDebtProps = RouteComponentProps & WithTranslation & ReturnType<typeof mapStateToProps>;

class Debt extends Component<IDebtProps> {
  render() {
    const {
      membership: { products, debt, distributor, order_id },
      allProducts,
      t,
    } = this.props;

    const cartProducts: ICartProduct[] = [];

    if (products) {
      for (let item of products) {
        const productName = Object.keys(item)[0];
        const product = allProducts.find((product) => product.name === productName)!;
        const cartProduct = { ...product, amount: item[productName] / product.price };

        cartProducts.push(cartProduct);
      }
    }

    return (
      <View style={{ flex: 1, paddingTop: 16 }}>
        {debt === 0 ? (
          <View style={{ paddingTop: 128 }}>
            <Message icon={Mood} title={t('notInDebt')} subtitle={t('canTakeProduct')} />
          </View>
        ) : (
          <ScrollView>
            <View style={{ paddingBottom: 36 }}>
              <View style={styles.title}>
                <Text>{t('myDebt')}</Text>

                <Text>
                  {t('debtNumber')}:{' '}
                  <Text style={{ fontSize: 20, color: Primary, fontWeight: 'bold' }}>{order_id}</Text>
                </Text>
              </View>

              {cartProducts.map((item) => (
                <VaultDebtProduct key={item.id} item={item} />
              ))}

              <View style={{ marginBottom: 16 }}>
                <View style={styles.totalSumWrapper}>
                  <Text style={styles.totalSumLabel}>{t('total')}:</Text>
                  <Text style={styles.totalSum}>{formatCurrency(debt)}</Text>
                </View>
              </View>

              <View style={styles.paymentInfo}>
                <View style={{ marginBottom: 16 }}>
                  <Text style={{ textAlign: 'center' }}>{t('paymentBank')}</Text>

                  <Text style={{ color: Black, fontWeight: 'bold', textAlign: 'center' }}>
                    {t('specifyDebtNumber', { order_id })}
                  </Text>
                </View>

                {distributor.banks.map((item) => (
                  <View key={item.number} style={{ marginBottom: 16, alignItems: 'center' }}>
                    <Text style={{ fontSize: 20, color: Primary, fontWeight: 'bold' }}>{item.number}</Text>
                    <Text style={{ fontSize: 12 }}>{item.name}</Text>
                  </View>
                ))}
              </View>

              <View
                style={{
                  marginBottom: 16,
                  marginHorizontal: 36,
                  borderBottomColor: LightGray,
                  borderBottomWidth: 1,
                  paddingTop: 0,
                }}
              />

              <View style={{ alignItems: 'center' }}>
                <Text style={{ marginBottom: 8 }}>
                  {t('serves')} <Text style={{ color: Black, fontWeight: 'bold' }}>{distributor.name}</Text>
                </Text>

                <Text>{t('companyNumber', { number: distributor.unique_number })}</Text>
                <Text>{t('vatNumber', { number: distributor.vat_number })}</Text>
                <Text>{t('address', { address: distributor.address })}</Text>
              </View>
            </View>
          </ScrollView>
        )}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  title: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'baseline',
    marginHorizontal: 16,
  },
  totalSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8,
    paddingHorizontal: 16,
  },
  totalSumLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    color: Black,
  },
  totalSum: {
    fontSize: 26,
    color: Black,
    fontWeight: 'bold',
  },
  paymentInfo: {
    alignItems: 'center',
    marginHorizontal: 36,
  },
  divider: {
    paddingTop: 16,
    marginBottom: 16,
    marginHorizontal: 36,
    borderBottomColor: LightGray,
    borderBottomWidth: 1,
  },
});

const mapStateToProps = (state: IState) => ({
  membership: state.userReducer.membership!,
  allProducts: state.globalReducer.products,
});

export default withTranslation('debt')(connect(mapStateToProps)(Debt));
