import React, { Component } from 'react';
import { StyleSheet, View, Text } from 'react-native';
import { RouteComponentProps } from 'react-router';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import FailureLogo from './FailureLogo';
import PrimaryButton from '../../../components/PrimaryButton';
import loginActions from '../../../actions/login';
import { Black, Gray } from '../../../Colors';

type IFailureProps = WithTranslation & RouteComponentProps & typeof localActions;

class Failure extends Component<IFailureProps> {
  onPress = () => {
    const { history, setReidentification } = this.props;

    setReidentification(false);
    history.replace('/home');
  };

  render() {
    const { t } = this.props;

    return (
      <View style={styles.wrapper}>
        <View style={styles.logoWrapper}>
          <FailureLogo />
        </View>

        <Text style={styles.title}>{t('failure.identityFailed')}</Text>
        <Text style={styles.text}>{t('failure.canGoBack')}</Text>

        <PrimaryButton width={180} onPress={this.onPress}>
          {t('failure.back').toUpperCase()}
        </PrimaryButton>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 36,
  },
  logoWrapper: {
    alignSelf: 'center',
    marginTop: 16,
  },
  title: {
    marginBottom: 8,
    color: Black,
    fontSize: 20,
    textAlign: 'center',
  },
  text: {
    marginBottom: 36,
    color: Gray,
    fontSize: 16,
    textAlign: 'center',
  },
});

const localActions = {
  setReidentification: loginActions.setReidentification,
};

export default withTranslation('idenfy')(connect(null, localActions)(Failure));
