import React, { Component } from 'react';
import { StyleSheet, TouchableOpacity } from 'react-native';
import Icon from 'react-native-vector-icons/MaterialIcons';

interface IEyeIcon {
  on: boolean;
  onPress: () => void;
  style?: object;
}
class EyeIcon extends Component<IEyeIcon> {
  render() {
    const { on, onPress, style, ...rest } = this.props;
    return (
      <TouchableOpacity activeOpacity={0.8} style={style} onPress={onPress} {...rest}>
        {on ? <Icon name='visibility' size={24} /> : <Icon name='visibility-off' size={24} />}
      </TouchableOpacity>
    );
  }
}

const styles = StyleSheet.create({});

export default EyeIcon;
