import React, { Component } from 'react';
import { View, Dimensions } from 'react-native';
import { CircularProgress } from '@material-ui/core';
import { RouteComponentProps } from 'react-router';
import * as Animatable from 'react-native-animatable';

import Ellipse from './Ellipse';
import MainLogo from '../../components/MainLogo';
import { Green } from '../../Colors';
import { getPhoneOrientation, OrientationType } from '../../helpers';

interface ISplashState {
  orientation: OrientationType;
  screenWidth: number;
}

class Splash extends Component<RouteComponentProps, ISplashState> {
  state = {
    orientation: getPhoneOrientation(),
    screenWidth: Dimensions.get('window').width,
  };

  async componentDidMount() {
    const { history } = this.props;

    Dimensions.addEventListener('change', this.onOrientationChange);
    setTimeout(() => history.replace('/login'), 500);
  }

  onOrientationChange = () => {
    this.setState({ orientation: getPhoneOrientation(), screenWidth: Dimensions.get('window').width });
  };

  componentWillUnmount() {
    Dimensions.removeEventListener('change', this.onOrientationChange);
  }

  render() {
    const { orientation } = this.state;
    let { screenWidth } = this.state;

    screenWidth = Math.min(1000, screenWidth);
    const isInPortrait = orientation === 'portrait';
    const narrow = isInPortrait && screenWidth < 600 && true;

    return (
      <View style={{ flex: 1, backgroundColor: narrow ? Green : '#fff', zIndex: 10 }}>
        <View style={{ marginTop: narrow ? 120 : 0, height: narrow ? 'auto' : '100%' }}>
          {narrow && <Ellipse width={screenWidth} />}
          <View
            style={{
              position: 'absolute',
              left: '50%',
              top: narrow ? '50%' : '40%',
              transform: [
                { translateX: narrow ? -125 : -250 },
                { translateY: narrow ? -79 : -(Math.max(160, screenWidth / 5) / 2) },
              ],
            }}
          >
            <Animatable.View animation={narrow ? undefined : 'zoomIn'} easing='ease-out' duration={200}>
              {narrow ? <MainLogo /> : <MainLogo width={500} height={Math.max(160, screenWidth / 5)} />}
            </Animatable.View>
          </View>
        </View>

        <View
          style={{
            flex: 1,
            justifyContent: 'flex-end',
            alignItems: 'center',
          }}
        >
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              width: 58,
              height: 58,
              marginBottom: 36,
              backgroundColor: 'white',
              borderRadius: 58 / 2,
              boxShadow: '0 1px 3px rgba(0, 0, 0, 0.3)',
            }}
          >
            <CircularProgress />
          </div>
        </View>
      </View>
    );
  }
}

export default Splash;
