import * as React from 'react';
import { Component } from 'react';
import { StyleSheet, View, Text, Linking, TouchableOpacity, Platform } from 'react-native';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Primary, Black, DarkGray } from '../../../Colors';

let domainName = 'artimiausias.impressbox.eu';

// if (__DEV__) {
//   domainName = 'artimiausias.impressbox.eu';
// } else {
//   domainName = 'www.artimiausias.lt';
// }

class Help extends Component<WithTranslation> {
  onPress = (path: string) => {
    const url = `https://${domainName}${path}`;

    if (Platform.OS === 'web') {
      window.open(url, '_blank', 'noopener');
    } else {
      Linking.canOpenURL(url).then((supported) => {
        if (supported) {
          Linking.openURL(url);
        }
      });
    }
  };

  render() {
    const { t } = this.props;

    return (
      <View style={styles.wrapper}>
        <Text style={styles.sectionTitle}>{t('help')}</Text>
        <View style={{ marginTop: 16, marginBottom: 24 }}>
          <TouchableOpacity activeOpacity={0.8} onPress={() => this.onPress('/')}>
            <View style={styles.link}>
              <Text style={styles.linkText}>{t('about').toUpperCase()}</Text>
            </View>
          </TouchableOpacity>

          <TouchableOpacity activeOpacity={0.8} onPress={() => this.onPress('/privatumo-politika/')}>
            <View style={styles.link}>
              <Text style={styles.linkText}>{t('privacyPolicy').toUpperCase()}</Text>
            </View>
          </TouchableOpacity>

          <TouchableOpacity activeOpacity={0.8} onPress={() => this.onPress('/taisykles/')}>
            <View style={styles.link}>
              <Text style={styles.linkText}>{t('rules').toUpperCase()}</Text>
            </View>
          </TouchableOpacity>
        </View>

        <Text style={{ fontSize: 16, textAlign: 'center', marginBottom: 8 }}>{t('emailHelp')}</Text>

        <TouchableOpacity
          activeOpacity={0.8}
          onPress={() => window.open('mailto:info@artimiausias.lt', '_blank')}
        >
          <View style={styles.link}>
            <Text style={styles.linkText}>info@artimiausias.lt</Text>
          </View>
        </TouchableOpacity>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flex: 1,
    padding: 16,
  },
  sectionTitle: {
    color: DarkGray,
  },
  link: {
    alignSelf: 'center',
    alignItems: 'center',
    padding: 4,
    paddingTop: 0,
    marginBottom: 16,
    paddingHorizontal: 8,
    borderBottomWidth: 4,
    borderBottomColor: Primary,
  },
  linkText: {
    fontSize: 22,
    color: Black,
    fontWeight: 'bold',
  },
});

export default withTranslation('help')(Help);
