import React, { Component } from 'react';
import { StyleSheet, View, Text, Image, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';
import DeleteForever from '@material-ui/icons/DeleteForever';
import { withTranslation, WithTranslation } from 'react-i18next';

import { ICartProduct, IState } from '../../../StateTypes';
import { Black, Gray, LightGray, DarkGray } from '../../../Colors';
import { formatCurrency, getMaxItemsInfoSuffix, shouldUseContain } from '../../../helpers';
import globalActions from '../../../actions/global';
import Counter from '../VendingMachine/Item/Counter';

interface ICartConfirmationOwnProps {
  item: ICartProduct;
  type: 'reservation' | 'vault';
  cartAmount: number;
  addToCart: (vendorId: number, productId: number, count: number) => void;
  removeFromCart: (vendorId: number, productId: number, count: number) => void;
}

type ICartConfirmationProps = ICartConfirmationOwnProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

interface ICartConfirmItemState {
  useContain: boolean;
}

class CartConfirmItem extends Component<ICartConfirmationProps, ICartConfirmItemState> {
  state = {
    useContain: true,
  };

  async componentDidMount() {
    const useContain = await shouldUseContain(this.props.item.cover);
    this.setState({ useContain });
  }

  addCount = () => {
    const {
      addToCart,
      vendorId,
      item,
      maxCartItems,
      inReservation,
      cartFull,
      allStockInCart,
      showToastMessage,
      t,
    } = this.props;

    if (inReservation && cartFull) {
      showToastMessage(t('item.plan', { maxCartItems, suffix: getMaxItemsInfoSuffix(maxCartItems) }));
      return;
    }

    if (inReservation && allStockInCart) {
      showToastMessage(t('item.maxTaken'));
      return;
    }

    addToCart(vendorId, item.id, 1);
  };

  subtractCount = () => {
    const { vendorId, item, removeFromCart } = this.props;

    removeFromCart(vendorId, item.id, 1);
  };

  deleteFromCart = () => {
    const { vendorId, item, removeFromCart } = this.props;

    removeFromCart(vendorId, item.id, item.amount);
  };

  render() {
    const { item, inVault, inReservation, cartFull, allStockInCart, t } = this.props;
    const { useContain } = this.state;

    const subtractDisabled = item.amount <= 1;
    const addDisabled = inReservation && (cartFull || allStockInCart);

    return (
      <View style={styles.wrapper}>
        <View style={{ flex: 1, flexDirection: 'row', marginLeft: -8, marginRight: 16 }}>
          <View style={{ width: 50, height: 70, marginRight: 12 }}>
            <Image
              style={{ flex: 1 }}
              resizeMode={useContain ? 'contain' : 'cover'}
              source={{ uri: item.cover }}
            />
          </View>
          <View style={{ flex: 1, paddingTop: 8 }}>
            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.name}>{item.name}</Text>
            </View>

            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.type}>{item.type.toUpperCase()}</Text>
              <Text style={styles.proof}>{item.proof}%</Text>
            </View>
          </View>
        </View>

        <View style={{ alignSelf: 'stretch', paddingTop: 8 }}>
          <Counter
            count={item.amount}
            addDisabled={addDisabled}
            subtractDisabled={subtractDisabled}
            onAdd={this.addCount}
            onSubtract={this.subtractCount}
          />

          <TouchableOpacity
            onPress={this.deleteFromCart}
            activeOpacity={0.8}
            style={{ flexDirection: 'row', alignSelf: 'center', marginTop: 12 }}
          >
            <Text style={{ textAlign: 'center', marginLeft: 8, color: DarkGray }}>{t('item.remove')}</Text>
            <DeleteForever style={{ color: DarkGray }} />
          </TouchableOpacity>
        </View>

        <View style={{ paddingTop: 8 }}>
          <View
            style={{
              alignItems: 'flex-end',
              width: 80,
            }}
          >
            {inReservation && (
              <View>
                <Text style={styles.totalPrice}>{formatCurrency(item.amount * item.storage_fee)}</Text>
                <Text style={{ fontSize: 10, color: Gray, textAlign: 'center' }}>
                  {formatCurrency(item.storage_fee)}
                </Text>
              </View>
            )}

            {inVault && (
              <View>
                <Text style={styles.totalPrice}>{formatCurrency(item.amount * item.price)}</Text>
                <Text style={{ fontSize: 10, color: Gray, textAlign: 'center' }}>
                  {formatCurrency(item.price)}
                </Text>
              </View>
            )}
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    paddingHorizontal: 16,
    paddingVertical: 12,
    borderBottomWidth: 1,
    borderBottomColor: LightGray,
  },
  imageWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 50,
    height: 70,
    marginRight: 12,
    borderWidth: 1,
  },
  type: {
    fontSize: 10,
    letterSpacing: 1,
    marginRight: 4,
  },
  proof: {
    fontSize: 10,
    letterSpacing: 1,
  },
  name: {
    flex: 1,
    flexWrap: 'wrap',
    fontSize: 16,
    color: Black,
    fontWeight: 'bold',
  },
  totalPrice: {
    fontSize: 20,
    color: Black,
    textAlign: 'right',
  },
});

const mapStateToProps = (state: IState, ownProps: ICartConfirmationOwnProps) => {
  const vendorId = state.globalReducer.selectedVendor ? state.globalReducer.selectedVendor.id : -1;
  const maxCartItems = state.userReducer.membership!.max_items;
  const inReservation = ownProps.type === 'reservation';
  const vendorProduct = state.vendorReducer.products.find((item) => item.id === Number(ownProps.item.id));
  const stock = inReservation ? vendorProduct!.stock : 999;

  return {
    stock,
    vendorId,
    maxCartItems,
    inReservation,
    inVault: ownProps.type === 'vault',
    cartFull: ownProps.cartAmount >= maxCartItems,
    allStockInCart: ownProps.item.amount >= stock,
  };
};

const localActions = {
  showToastMessage: globalActions.initToastMessage,
};

export default withTranslation('cartConfirmation')(connect(mapStateToProps, localActions)(CartConfirmItem));
