import React, { Component } from 'react';
import { StyleSheet, View, ScrollView, Text, Picker, TouchableOpacity, Dimensions } from 'react-native';
import { RouteComponentProps } from 'react-router';
import LocationOn from '@material-ui/icons/LocationOn';
import { connect } from 'react-redux';
import { withTranslation, Trans, WithTranslation } from 'react-i18next';

import Item from './Item';

import { IState, ICartProduct } from '../../../StateTypes';
import { DarkGray, Black, Gray, LightGray, Primary, Red, Blue } from '../../../Colors';
import PrimaryButton from '../../../components/PrimaryButton';
import vendorActions from '../../../actions/vendor';
import userActions from '../../../actions/user';
import globalActions from '../../../actions/global';
import { formatCurrency, formatTimeWords } from '../../../helpers';

type ICartProps = WithTranslation &
  RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class Cart extends Component<ICartProps> {
  showReservationModal = () => this.props.showReservationInfoModal();

  onReservAnother = () => this.props.history.goBack();

  onCancel = () => {
    this.props.resetCart();
    this.props.history.goBack();
  };

  onConfirm = () => {
    const { selectedVendor } = this.props;

    const cart = Object.assign({}, this.props.cart);

    for (let productId of selectedVendor.products) {
      if (!cart[productId]) {
        cart[productId] = 0;
      }
    }
    this.props.createReservation(selectedVendor.id, cart);
  };

  render() {
    const { cart, selectedVendor, ttc, addToCart, removeFromCart, t } = this.props;

    let cartAmount = 0;
    let cartSum = 0;
    let reservationFee = 0;

    const cartProducts: ICartProduct[] = [];

    for (let productId in cart) {
      const product = this.props.allProducts.find((product) => product.id === Number(productId))!;
      const cartProduct = { ...product, amount: cart[productId] };

      cartProducts.push(cartProduct);
      cartAmount += cart[productId];
      cartSum += cartProduct.storage_fee * cartProduct.amount;
      reservationFee += cartProduct.amount * cartProduct.reservation_fee;
    }

    return (
      <View style={{ flex: 1, height: Dimensions.get('window').height - 56 }}>
        <ScrollView>
          <View style={{ paddingTop: 16, paddingBottom: 36 }}>
            <Text style={styles.sectionTitle}>{t('reservationConfirm')}</Text>

            <View style={{ marginTop: 16, marginBottom: 8 }}>
              <div
                style={{
                  justifyContent: 'center',
                  alignItems: 'center',
                  alignSelf: 'center',
                  width: 245,
                  height: 42,
                  backgroundColor: Blue,
                  borderRadius: 25,
                  boxShadow: '0px 1px 2px rgba(0, 0, 0, 0.3)',
                }}
              >
                <TouchableOpacity
                  activeOpacity={0.8}
                  style={styles.whatIsReservation}
                  onPress={this.showReservationModal}
                >
                  <Text style={{ fontSize: 18, color: 'white' }}>{t('whatIsReservation').toUpperCase()}</Text>
                </TouchableOpacity>
              </div>
            </View>

            {cartAmount === 0 ? (
              <>
                <Text style={styles.empty}>{t('reservationEmpty')}</Text>

                <PrimaryButton width={200} onPress={this.onReservAnother}>
                  {t('reserveProduct').toUpperCase()}
                </PrimaryButton>
              </>
            ) : (
              <>
                {cartProducts.map((product) => (
                  <Item
                    key={product.id}
                    item={product}
                    type='reservation'
                    addToCart={addToCart}
                    removeFromCart={removeFromCart}
                    cartAmount={cartAmount}
                  />
                ))}

                <View style={{ marginBottom: 16 }}>
                  <View style={styles.addFees}>
                    <View
                      style={{
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        paddingHorizontal: 16,
                        marginBottom: 4,
                      }}
                    >
                      <Text>{t('reservationTime')}:</Text>
                      <Text style={styles.endSum}>{formatTimeWords(ttc)}</Text>
                    </View>

                    <View style={styles.endSumWrapper}>
                      <Text>{t('rservationPrice')}:</Text>
                      <Text style={styles.endSum}>{formatCurrency(reservationFee)}</Text>
                    </View>
                  </View>

                  <View style={styles.totalSumWrapper}>
                    <Text style={styles.totalSumLabel}>{t('total')}:</Text>
                    <Text style={styles.totalSum}>{formatCurrency(cartSum + reservationFee)}</Text>
                  </View>
                </View>

                <Text style={styles.info}>{t('info')}</Text>

                <View style={styles.vendorInfo}>
                  <LocationOn style={{ marginRight: 8, color: Black, fontSize: 24 }} />
                  <View>
                    <Text style={styles.name}>{selectedVendor.name}</Text>
                    <Text style={styles.address}>{selectedVendor.address}</Text>
                  </View>
                </View>

                <View style={{ marginBottom: 24 }}>
                  <PrimaryButton width={170} onPress={this.onConfirm}>
                    {t('reserve').toUpperCase()}
                    {'  '}
                    <Text style={{ fontWeight: 'bold' }}>{formatCurrency(cartSum + reservationFee)}</Text>
                  </PrimaryButton>
                </View>

                <View style={{ marginBottom: 16 }}>
                  <TouchableOpacity
                    activeOpacity={0.8}
                    style={styles.reserveAnother}
                    onPress={this.onReservAnother}
                  >
                    <Text style={{ color: Primary }}>{t('reserveOtherProduct').toUpperCase()}</Text>
                  </TouchableOpacity>
                </View>

                <TouchableOpacity activeOpacity={0.8} style={styles.cancel} onPress={this.onCancel}>
                  <Text style={{ color: Red }}>{t('cancel').toUpperCase()}</Text>
                </TouchableOpacity>
              </>
            )}
          </View>
        </ScrollView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1, paddingTop: 16 },
  sectionTitle: {
    marginLeft: 16,
    color: DarkGray,
  },
  whatIsReservation: {
    justifyContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    width: 245,
    height: 42,
    backgroundColor: Blue,
    borderRadius: 25,
    elevation: 3,
  },
  empty: {
    marginTop: 36,
    marginBottom: 16,
    textAlign: 'center',
    fontSize: 18,
    color: Gray,
  },
  link: {
    alignSelf: 'center',
    alignItems: 'center',
    paddingHorizontal: 8,
    paddingBottom: 4,
    marginTop: 12,
    marginBottom: 4,
    borderBottomWidth: 4,
    borderBottomColor: Primary,
  },
  linkText: {
    fontSize: 22,
    color: Black,
    fontWeight: 'bold',
  },
  info: {
    marginHorizontal: 36,
    textAlign: 'center',
    color: Gray,
  },
  addFees: {
    paddingVertical: 12,
    borderBottomWidth: 1,
    borderBottomColor: LightGray,
  },
  reservation: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginHorizontal: 16,
  },
  reservationPrice: {
    color: Black,
    fontWeight: 'bold',
    fontSize: 20,
  },
  endSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
  },
  endSum: {
    fontSize: 18,
    color: Black,
  },
  totalSumWrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8,
    paddingHorizontal: 16,
  },
  totalSumLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    color: Black,
  },
  totalSum: {
    fontSize: 26,
    color: Black,
    fontWeight: 'bold',
  },
  vendorInfo: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    marginVertical: 24,
  },
  icon: {
    marginRight: 8,
    color: Black,
  },
  name: {
    color: Black,
    fontSize: 16,
  },
  address: {
    color: Gray,
  },
  reserveAnother: {
    justifyContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    borderRadius: 25,
    width: 230,
    height: 36,
    backgroundColor: undefined,
    borderWidth: 1,
    borderColor: Primary,
  },
  cancel: {
    justifyContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    borderRadius: 25,
    width: 150,
    height: 36,
  },
});

const mapStateToProps = (state: IState) => ({
  selectedVendor: state.globalReducer.selectedVendor!,
  cart: state.userReducer.carts[state.globalReducer.selectedVendor!.id],
  allProducts: state.globalReducer.products,
  ttc: state.userReducer.membership!.ttc,
});

const localActions = {
  createReservation: vendorActions.createReservation,
  addToCart: userActions.addProductToCart,
  removeFromCart: userActions.removeProductFromCart,
  resetCart: userActions.resetCart,
  showReservationInfoModal: globalActions.showReservationInfoModal,
};

export default withTranslation('cartConfirmation')(connect(mapStateToProps, localActions)(Cart));
