import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import { IPaymentState, PaymentInitial, CardModalInitial } from '../StateTypes';

export default function paymentReducer(state = PaymentInitial, action: Action): IPaymentState {
  switch (action.type) {
    case getType(actions.showCardModal):
      return { ...state, cardModal: { show: true, ...action.payload } };
    case getType(actions.hideCardModal):
    case getType(actions.registrationPaymentSuccess):
    case getType(actions.reservationPaymentSuccess):
    case getType(actions.paymentFailure):
      return { ...state, cardModal: CardModalInitial };
    case getType(actions.setCurrentReservationId):
      return { ...state, currentReservationId: action.payload };
    case getType(actions.setPastPayments):
      return { ...state, pastPayments: action.payload };
    default:
      return state;
  }
}
