import React, { Component } from 'react';
import { StyleSheet, View, Text, ScrollView } from 'react-native';
import { Link, RouteComponentProps, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { LinearProgress } from '@material-ui/core';
import { withTranslation, WithTranslation } from 'react-i18next';

import ChangeLanguage from '../../ChangeLanguage';
import MainLogo from '../../components/MainLogo';
import TextField from '../../components/TextFIeld';
import EyeIcon from '../../components/EyeIcon';
import PrimaryButton from '../../components/PrimaryButton';
import { Primary, Black } from '../../Colors';
import loginActions from '../../actions/login';
import { isLoginFormValid } from '../../helpers';
import { IState } from '../../StateTypes';

const localActions = {
  login: loginActions.login,
  resetErrors: loginActions.resetLoginFormErrors,
};

type ILoginProps = RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions &
  WithTranslation;

interface ILoginState {
  email: string;
  password: string;
  showPassword: boolean;
}

class Login extends Component<ILoginProps, ILoginState> {
  state = {
    email: '',
    password: '',
    showPassword: false,
  };

  componentDidMount() {
    this.props.login('mano@mano.mano', 'mano');
  }

  onLoginPress = () => {
    const { email, password } = this.state;

    if (isLoginFormValid(email, password)) {
      this.props.login(email, password);
    }
  };

  componentWillUnmount() {
    const { error, resetErrors } = this.props;

    if (error) {
      resetErrors();
    }
  }

  render() {
    const { error, loggingIn, t } = this.props;
    const { email, password, showPassword } = this.state;

    const isFormValid = isLoginFormValid(email, password);
    const emailError = error && error.email;
    const authError = error && error.auth;

    return (
      <View style={{ flex: 1 }}>
        {loggingIn && (
          <View style={{ position: 'absolute', top: 0, left: 0, right: 0, zIndex: 1000 }}>
            <LinearProgress />
          </View>
        )}

        <ScrollView style={styles.wrapper} keyboardShouldPersistTaps='handled'>
          <View style={styles.logoWrapper}>
            <MainLogo width={152} height={96} />
          </View>

          <View style={{ marginBottom: 8 }}>
            <TextField
              label={t('email')}
              type='email'
              value={email}
              onChange={(e) => this.setState({ email: e.target.value })}
              autoCapitalize='none'
              variant='filled'
              errorText={emailError}
            />

            <View>
              <TextField
                label={t('password')}
                type={showPassword ? 'text' : 'password'}
                value={password}
                onChange={(e) => this.setState({ password: e.target.value })}
                autoCapitalize='none'
                variant='filled'
                errorText={authError}
              />
              <EyeIcon
                style={{ position: 'absolute', top: 14, right: 8, padding: 4 }}
                on={showPassword}
                onPress={() => this.setState({ showPassword: !showPassword })}
              />
            </View>
          </View>

          <View style={{ marginBottom: 24 }}>
            <PrimaryButton width={152} disabled={!isFormValid || loggingIn} onPress={this.onLoginPress}>
              {t('login').toUpperCase()}
            </PrimaryButton>
          </View>

          <Text style={{ textAlign: 'center', color: Black, fontSize: 16 }}>{t('noUser')}</Text>
          <View style={styles.registerButton}>
            <Link to='/register' style={{ textDecoration: 'none' }}>
              <Text style={styles.registerButtonText}>{t('register')}</Text>
            </Link>
          </View>
        </ScrollView>
        <ChangeLanguage />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 36,
    alignSelf: 'center',
    width: '100%',
    maxWidth: 450,
  },
  logoWrapper: {
    alignSelf: 'center',
    marginTop: 64,
    marginBottom: 64,
  },
  loginButton: {
    width: 150,
    height: 36,
    alignSelf: 'center',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: 24,
    backgroundColor: Primary,
    borderRadius: 25,
  },
  loginButtonText: {
    color: 'white',
    fontWeight: '500',
  },
  registerButton: {
    alignSelf: 'center',
    paddingVertical: 4,
    paddingHorizontal: 12,
    marginBottom: 8,
    opacity: 0.8,
  },
  registerButtonText: {
    fontSize: 15,
    color: Primary,
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    width: 68,
    height: 68,
    marginTop: -24,
    backgroundColor: 'white',
    borderRadius: 68 / 2,
    elevation: 3,
  },
});

const mapStateToProps = (state: IState) => {
  return {
    error: state.loginReducer.error,
    loggingIn: state.loginReducer.loggingIn,
  };
};

export default withTranslation('login')(connect(mapStateToProps, localActions)(Login));
