import React, { Component } from 'react';
import { StyleSheet, View, Text, Image } from 'react-native';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Black, LightGray, Blue } from '../../../../Colors';
import { IVaultProduct } from '../../../../StateTypes';
import { shouldUseContain } from '../../../../helpers';

interface IVaultItemProps {
  item: IVaultProduct;
}

interface IVaultItemState {
  useContain: boolean;
}

class VaultItem extends Component<IVaultItemProps & WithTranslation, IVaultItemState> {
  state = {
    useContain: true,
  };

  async componentDidMount() {
    const useContain = await shouldUseContain(this.props.item.cover);
    this.setState({ useContain });
  }

  render() {
    const { item, t } = this.props;
    const { useContain } = this.state;

    const pending = item.status === 'pending';

    return (
      <View style={{ marginBottom: 16 }}>
        <View
          style={{
            flexDirection: 'row',
            borderWidth: 1,
            borderColor: LightGray,
            borderRadius: 3,
            opacity: pending ? 0.3 : 1,
            backgroundColor: 'white',
          }}
        >
          <View style={{ alignSelf: 'center', width: 50, height: 90 }}>
            <Image
              style={{ flex: 1 }}
              resizeMode={useContain ? 'contain' : 'cover'}
              source={{ uri: item.cover }}
            />
          </View>
          <View
            style={{
              justifyContent: 'center',
              width: 110,
              paddingVertical: 16,
              paddingHorizontal: 8,
              backgroundColor: 'white',
            }}
          >
            <View>
              <Text style={styles.name}>{item.name}</Text>
            </View>
            <View style={{ flexDirection: 'row', marginBottom: 8 }}>
              <Text style={styles.type}>{item.type.toUpperCase()}</Text>
              <Text style={styles.proof}>{item.proof}%</Text>
            </View>
            <View>
              <Text style={{ fontSize: 16, color: Black }}>
                <Text style={{ fontWeight: 'bold' }}>{item.amount}</Text> {t('item.quantity')}
              </Text>
            </View>
          </View>
        </View>
        {pending && (
          <View style={styles.pendingBackground}>
            <View style={styles.pendingMessage}>
              <Text style={{ color: 'white', textAlign: 'center' }}>{t('item.pending').toUpperCase()}</Text>
            </View>
          </View>
        )}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    borderWidth: 1,
    borderColor: LightGray,
    borderRadius: 3,
  },
  imageWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 50,
    height: 70,
    marginRight: 12,
    borderWidth: 1,
  },
  type: {
    fontSize: 10,
    letterSpacing: 0.5,
    marginRight: 4,
  },
  proof: {
    fontSize: 10,
    letterSpacing: 0.5,
  },
  name: {
    flex: 1,
    flexWrap: 'wrap',
    fontSize: 16,
    color: Black,
  },
  totalPrice: {
    fontSize: 20,
    color: Black,
    fontWeight: 'bold',
    textAlign: 'right',
  },
  pendingBackground: {
    position: 'absolute',
    left: 8,
    right: 8,
    top: 0,
    bottom: 0,
    justifyContent: 'center',
    alignItems: 'center',
  },
  pendingMessage: {
    paddingHorizontal: 12,
    paddingVertical: 8,
    backgroundColor: Blue,
    borderRadius: 3,
  },
});

export default withTranslation('vault')(VaultItem);
