import React, { Component } from 'react';
import { StyleSheet, ScrollView, View, Text, TouchableOpacity } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { Link, RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import * as Location from 'expo-location';
import * as Permissions from 'expo-permissions';
import { CircularProgress } from '@material-ui/core';
import { withTranslation, WithTranslation } from 'react-i18next';

import Selected from '../Selected';
import Map from './Leaflet';
import VendorList from './VendorList';
import { IState } from '../../../StateTypes';
import { DarkGray } from '../../../Colors';
import userActions from '../../../actions/user';
import globalActions from '../../../actions/global';

interface IVendorMapState {
  locationEnabledAnswered: boolean;
}

type IVendorMapProps = WithTranslation &
  RouteComponentProps &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class VendorMap extends Component<IVendorMapProps, IVendorMapState> {
  state = {
    locationEnabledAnswered: false,
  };

  locationWatcher: any = null;

  async componentDidMount() {
    await this.promptForLocationEnable();
  }

  promptForLocationEnable = async () => {
    const { status } = await Permissions.askAsync(Permissions.LOCATION);

    // this.onLocationEnabled();
    // return;

    if (status === 'granted') {
      this.onLocationEnabled();
    } else {
      this.onLocationDisabled();
    }
  };

  onLocationEnabled = async () => {
    const { setLocationEnabled, setUserLocation } = this.props;

    // const latitude = 54.674299;
    // const longitude = 25.273759;
    // setUserLocation({ latitude, longitude });
    // setLocationEnabled(true);
    // this.setState({ locationEnabledAnswered: true });
    // return;

    this.locationWatcher = await Location.watchPositionAsync(
      { accuracy: Location.Accuracy.High, timeInterval: 10000, distanceInterval: 20 },
      ({ coords }) => {
        // console.log(coords);
        const latitude = coords.latitude!;
        const longitude = coords.longitude!;
        setUserLocation({ latitude, longitude });
        setLocationEnabled(true);
        this.setState({ locationEnabledAnswered: true });
      }
    );
  };

  onLocationDisabled = () => {
    this.props.setLocationEnabled(false);
    this.setState({ locationEnabledAnswered: true });
  };

  componentWillUnmount() {
    if (this.locationWatcher) {
      this.locationWatcher.remove();
    }
  }

  render() {
    const { selectedVendor, locationEnabled, t } = this.props;
    const { locationEnabledAnswered } = this.state;

    if (!locationEnabledAnswered) {
      return (
        <View style={styles.wrapper}>
          <View style={styles.loggingBackground}>
            <Animatable.View
              style={styles.loadingWrapper}
              animation='fadeIn'
              easing='ease-out'
              duration={100}
            >
              <CircularProgress />
            </Animatable.View>
          </View>
        </View>
      );
    }

    if (locationEnabledAnswered && !locationEnabled) {
      return (
        <View style={styles.wrapper}>
          <View style={styles.locationError}>
            <Text style={styles.locationErrorText}>{t('locationDisabled')}</Text>
          </View>
        </View>
      );
    }

    if (locationEnabledAnswered && locationEnabled) {
      return (
        <View style={styles.wrapper}>
          <Map />
          <View style={{ flex: 1, height: 100 }}>
            <ScrollView>
              <VendorList />
            </ScrollView>
          </View>

          {selectedVendor && (
            <Animatable.View animation='slideInUp' easing='ease-out' duration={200} useNativeDriver>
              <TouchableOpacity activeOpacity={0.8}>
                <Link to='/home/machine' style={{ textDecoration: 'none' }}>
                  <Selected type='vendor' title={selectedVendor.name} subtitle={selectedVendor.address} />
                </Link>
              </TouchableOpacity>
            </Animatable.View>
          )}
        </View>
      );
    }

    return null;
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
  locationError: {
    flex: 1,
    paddingTop: 128,
    alignItems: 'center',
  },
  locationErrorText: {
    width: '80%',
    fontSize: 16,
    color: DarkGray,
    marginBottom: 16,
    textAlign: 'center',
  },
  loggingBackground: {
    position: 'absolute',
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 58,
    height: 58,
    backgroundColor: 'white',
    borderRadius: 58 / 2,
  },
});

const mapStateToProps = (state: IState) => ({
  vendors: state.globalReducer.vendors,
  selectedVendor: state.globalReducer.selectedVendor,
  locationEnabled: state.globalReducer.locationEnabled,
  userLocation: state.userReducer.location,
});

const localActions = {
  setUserLocation: userActions.setUserLocation,
  setLocationEnabled: globalActions.setLocationEnabled,
};

export default withTranslation('vendorMap')(connect(mapStateToProps, localActions)(VendorMap));
