import React, { Component } from 'react';
import { StyleSheet, View, Text, Image } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import Basket from './Basket';
import Info from './Info';
import Counter from './Counter';

import { IVendorProduct, IState } from '../../../../StateTypes';
import { Black, Primary } from '../../../../Colors';
import PrimaryButton from '../../../../components/PrimaryButton';
import globalActions from '../../../../actions/global';
import { formatCurrency, isUserNearby, getMaxItemsInfoSuffix, shouldUseContain } from '../../../../helpers';

interface IVendingItemOwnProps extends IVendorProduct {
  cartSum: number;
  count: number;
  cartAmount: number;
  layoutType: 'vendor' | 'vault';
  addToCart: (vendorId: number, productId: number, count: number) => void;
  removeFromCart: (vendorId: number, productId: number, count: number) => void;
  onTakeProduct: (productId: number) => void;
  onAddToCart: () => void;
}

type IVendingItemProps = IVendingItemOwnProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

interface IVendingItemState {
  useContain: boolean;
}

class VendingItem extends Component<IVendingItemProps, IVendingItemState> {
  state = {
    useContain: true,
  };

  async componentDidMount() {
    const useContain = await shouldUseContain(this.props.cover);
    this.setState({ useContain });
  }

  onReserve = () => {
    const {
      selectedVendor,
      maxCartItems,
      stockEmpty,
      allStockInCart,
      cartFull,
      onAddToCart,
      showToastMessage,
      t,
    } = this.props;

    if (stockEmpty) {
      showToastMessage(t('item.noInStock'));
      return;
    }

    if (allStockInCart) {
      showToastMessage(t('item.maxTaken'));
      return;
    }

    if (cartFull) {
      showToastMessage(t('item.plan', { maxCartItems, suffix: getMaxItemsInfoSuffix(maxCartItems) }));
      return;
    }

    this.props.addToCart(selectedVendor.id, this.props.id, 1);
    onAddToCart();
  };

  addCount = () => {
    this.props.addToCart(-1, this.props.id, 1);
  };

  subtractCount = () => {
    this.props.removeFromCart(-1, this.props.id, 1);
  };

  onTakeNow = () => {
    const { stockEmpty, id, showToastMessage, t } = this.props;

    if (stockEmpty) {
      showToastMessage(t('item.noInStock'));
      return;
    }

    if (this.isUserNearby()) {
      this.props.onTakeProduct(id);
    } else {
      showToastMessage(t('item.onlyNearby'));
    }
  };

  isUserNearby = () => {
    const { selectedVendor, userLocation } = this.props;
    return isUserNearby(selectedVendor, userLocation);
  };

  render() {
    const {
      id,
      count,
      price,
      stock,
      type,
      proof,
      name,
      volume,
      cover,
      vault,
      inVendor,
      stockEmpty,
      allStockInCart,
      cartFull,
      t,
    } = this.props;
    const { useContain } = this.state;

    const reserveDisabled = inVendor && (stockEmpty || allStockInCart || cartFull);
    const takeNowDisabled = inVendor && (stockEmpty || !this.isUserNearby());

    let productInVault = false;

    for (let item of vault) {
      if (item.status === 'approved' && item.product.id === id) {
        productInVault = true;
        break;
      }
    }

    return (
      <div style={{ boxShadow: '0 1px 2px rgba(0,0,0,0.2)', marginBottom: 24 }}>
        <View style={styles.wrapper}>
          <View style={{ flex: 1, height: 240, paddingVertical: !inVendor ? 16 : 0 }}>
            <Image
              style={{ flex: 1 }}
              resizeMode={useContain ? 'contain' : 'cover'}
              source={{ uri: cover }}
            />
          </View>

          <View style={styles.productInfo}>
            <View style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
              <View>
                <Text style={styles.name}>{name}</Text>
                <Text style={{ fontSize: productInVault ? 18 : 20 }}>
                  {inVendor && productInVault ? (
                    <Text style={{ color: Primary, fontWeight: 'bold' }}>{t('item.haveInVault')}</Text>
                  ) : (
                    formatCurrency(price)
                  )}
                </Text>
              </View>
              {inVendor && count > 0 && <Basket count={count} />}
            </View>
            <Info type={type} proof={proof} volume={volume} stock={stock} />

            <View style={{ alignItems: inVendor ? 'center' : 'flex-start' }}>
              {inVendor ? (
                <PrimaryButton disabled={reserveDisabled} width={135} onPress={this.onReserve}>
                  {t('item.reserve').toUpperCase()}
                </PrimaryButton>
              ) : (
                <Counter
                  count={stock === 0 ? 0 : count}
                  subtractDisabled={count <= 0}
                  addDisabled={false}
                  onAdd={this.addCount}
                  onSubtract={this.subtractCount}
                />
              )}
            </View>

            {inVendor && (
              <>
                <Text style={{ marginVertical: 12, textAlign: 'center' }}>{t('item.or').toLowerCase()}</Text>
                <PrimaryButton disabled={takeNowDisabled} width={130} onPress={this.onTakeNow}>
                  {t('item.takeNow').toUpperCase()}
                </PrimaryButton>
              </>
            )}
          </View>
        </View>
      </div>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    alignItems: 'center',
    backgroundColor: 'hsl(0, 100%, 100%)',

    //elevation: 3,
  },
  name: {
    fontSize: 22,
    color: Black,
    fontWeight: 'bold',
  },
  productInfo: {
    flex: 2,
    alignSelf: 'stretch',
    backgroundColor: 'hsl(0, 0%, 96%)',
    padding: 16,
  },
});

const mapStateToProps = (state: IState, ownProps: IVendingItemOwnProps) => {
  const maxCartItems = state.userReducer.membership!.max_items;

  return {
    maxCartItems,
    selectedVendor: state.globalReducer.selectedVendor!,
    userLocation: state.userReducer.location,
    vault: state.userReducer.vault.vault,
    inVendor: ownProps.layoutType === 'vendor',
    stockEmpty: ownProps.stock === 0,
    cartFull: ownProps.cartAmount >= maxCartItems,
    allStockInCart: ownProps.count >= ownProps.stock,
  };
};

const localActions = {
  showToastMessage: globalActions.initToastMessage,
};

export default withTranslation('vendingMachine')(connect(mapStateToProps, localActions)(VendingItem));
