import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity, ScrollView } from 'react-native';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { withTranslation, WithTranslation } from 'react-i18next';

import ChangeLanguage from '../../ChangeLanguage';
import MainLogo from '../../components/MainLogo';
import PrimaryButton from '../../components/PrimaryButton';
import { Primary, Black, DarkGray } from '../../Colors';
import { getPolicies } from '../../api';

interface IAgreementProps {
  onAccept: () => void;
}

class Agreement extends Component<WithTranslation & IAgreementProps> {
  state = {
    showPrivacyPolicy: true,
    showUserAgreement: true,
    privacyPolicy: '',
    userAgreement: '',
    policiesLoaded: false,
  };

  async componentDidMount() {
    const policies = await getPolicies();

    if (Array.isArray(policies)) {
      const [privacyPolicy, userAgreement] = policies;
      this.setState({
        privacyPolicy: privacyPolicy.text,
        userAgreement: userAgreement.text,
        policiesLoaded: true,
      });
    } else {
      this.setState({ policiesLoaded: true });
    }
  }

  render() {
    const { t } = this.props;
    const { showPrivacyPolicy, privacyPolicy, showUserAgreement, userAgreement, policiesLoaded } = this.state;

    if (!policiesLoaded) {
      return null;
    }

    return (
      <View style={{ flex: 1 }}>
        <ScrollView style={styles.wrapper}>
          <View style={{ paddingBottom: 36 }}>
            <View style={styles.logoWrapper}>
              <MainLogo width={152} height={96} />
            </View>

            <View style={{ marginBottom: showUserAgreement ? 36 : 16 }}>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => this.setState({ showUserAgreement: !showUserAgreement })}
              >
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={styles.termsTitle}>{t('userAgreement')}</Text>
                  {showUserAgreement ? (
                    <ExpandLess
                      style={{
                        position: 'absolute',
                        right: 0,
                        top: -5,
                        alignSelf: 'center',
                        padding: 8,
                        color: DarkGray,
                        fontSize: 24,
                      }}
                    />
                  ) : (
                    <ExpandMore
                      style={{
                        position: 'absolute',
                        right: 0,
                        top: -5,
                        alignSelf: 'center',
                        padding: 8,
                        color: DarkGray,
                        fontSize: 24,
                      }}
                    />
                  )}
                </View>
              </TouchableOpacity>
              {showUserAgreement && <Text style={styles.termsText}>{userAgreement}</Text>}
            </View>

            <View style={{ marginBottom: 36 }}>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => this.setState({ showPrivacyPolicy: !showPrivacyPolicy })}
              >
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={styles.termsTitle}>{t('privacyPolicy')}</Text>
                  {showPrivacyPolicy ? (
                    <ExpandLess
                      style={{
                        position: 'absolute',
                        right: 0,
                        top: -5,
                        alignSelf: 'center',
                        padding: 8,
                        color: DarkGray,
                        fontSize: 24,
                      }}
                    />
                  ) : (
                    <ExpandMore
                      style={{
                        position: 'absolute',
                        right: 0,
                        top: -5,
                        alignSelf: 'center',
                        padding: 8,
                        color: DarkGray,
                        fontSize: 24,
                      }}
                    />
                  )}
                </View>
              </TouchableOpacity>
              {showPrivacyPolicy && <Text style={styles.termsText}>{privacyPolicy}</Text>}
            </View>

            <PrimaryButton width={96} onPress={this.props.onAccept}>
              {t('agree').toUpperCase()}
            </PrimaryButton>
          </View>
        </ScrollView>
        <ChangeLanguage />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 36,
    backgroundColor: '#fff',
  },
  logoWrapper: {
    alignSelf: 'center',
    marginTop: 64,
    marginBottom: 36,
  },
  termsTitle: {
    marginBottom: 8,
    fontSize: 16,
    color: Black,
  },
  termsText: {
    color: DarkGray,
    lineHeight: 23,
  },
  acceptButton: {
    width: 96,
    height: 36,
    alignSelf: 'center',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: 36,
    backgroundColor: Primary,
    borderRadius: 25,
  },
  acceptButtonText: {
    color: 'white',
    fontWeight: '500',
  },
});

const webStyles = {
  icon: {
    position: 'absolute',
    right: 0,
    top: -5,
    alignSelf: 'center',
    padding: 8,
    color: DarkGray,
    fontSize: 24,
  },
};

export default withTranslation('agreement')(Agreement);
