import React, { Component } from 'react';
import { StyleSheet, View, Text, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';

import globalActions from '../../../../actions/global';
import { Black, Gray } from '../../../../Colors';
import { IVendor, IState } from '../../../../StateTypes';

interface IVendorItemOwnProps extends IVendor {
  distance: number;
  disabled?: boolean;
}

type IVendorItemProps = IVendorItemOwnProps & ReturnType<typeof mapStateToProps> & typeof localActions;

class VendorItem extends Component<IVendorItemProps> {
  onPress = () => this.props.setSelectedVendorById(this.props.id);

  render() {
    const { name, address, distance, selected, disabled = false } = this.props;

    return (
      <TouchableOpacity
        style={{
          paddingHorizontal: 16,
          paddingVertical: 16,
          backgroundColor: selected && !disabled ? 'hsl(0, 0%, 95%)' : undefined,
        }}
        disabled={disabled}
        onPress={this.onPress}
        activeOpacity={0.8}
      >
        <Text style={styles.distance}>{distance}M</Text>
        <Text style={styles.name}>{name}</Text>
        <Text style={styles.address}>{address}</Text>

        {disabled && <View style={styles.disabled} />}
      </TouchableOpacity>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    paddingHorizontal: 16,
    paddingVertical: 16,
  },
  distance: {
    fontSize: 12,
    fontWeight: 'bold',
    color: Black,
  },
  name: {
    fontSize: 16,
    color: Black,
  },
  address: {
    color: Gray,
  },
  disabled: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'hsla(0, 79%, 74%, 0.7)',
  },
});

const mapStateToProps = (state: IState, props: IVendorItemOwnProps) => {
  const { selectedVendor } = state.globalReducer;
  return { selected: selectedVendor && selectedVendor.id === props.id ? true : false };
};

const localActions = {
  setSelectedVendorById: globalActions.setSelectedVendorById,
};

export default connect(mapStateToProps, localActions)(VendorItem);
