import React, { Component } from 'react';
import { StyleSheet, View } from 'react-native';
import TextInput from '@material-ui/core/TextField';
import { TextFieldProps } from '@material-ui/core';

interface IOwnTextFieldProps {
  helperText?: string;
  errorText?: string;
}

type ITextFieldProps = IOwnTextFieldProps & TextFieldProps;

class TextField extends Component<ITextFieldProps> {
  render() {
    const { helperText, errorText, ...rest } = this.props;
    return (
      <View style={styles.wrapper}>
        <TextInput
          helperText={errorText ? errorText : helperText}
          error={errorText ? true : false}
          color='primary'
          variant='filled'
          fullWidth
          {...rest}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    marginBottom: 24,
  },
});

export default TextField;
