import { createAction } from 'typesafe-actions';

import * as ActionTypes from '../ActionTypes';
import { ThunkAction } from './helpers';
import globalActions from './global';
import authorizeActions from './authorize';

import { getPaymentDetails, pay, onPaymentFailure } from '../api';
import { ICardValuesFormatted } from '../pages/Home/CardModal';
import { replace } from 'connected-react-router';
import { IPaymentInstrument } from '../StateTypes';

const cancelPayment = (): ThunkAction => {
  return async (dispatch, getState) => {
    const { currentReservationId } = getState().paymentReducer;

    if (currentReservationId !== null) {
      try {
        await onPaymentFailure(getState, currentReservationId);
        dispatch(actions.setCurrentReservationId(null));
      } catch (e) {
        console.error(e.json);
      }
    }
  };
};

const onError = (e: any, dispatch: any) => {
  e.json && console.error(e.json);

  if (e.json) {
    if (e.json.statusCode >= 500) {
      dispatch(globalActions.showGlobalErrorModal(e.json));
      return;
    }

    if (e.json['Mokjimo klaida']) {
      if (e.json.statusCode === 400) {
        dispatch(
          globalActions.showGlobalErrorModal({
            title: 'paymentFailureTitle',
            message: e.json['Mokjimo klaida'],
          })
        );
      }
      return;
    }

    if (e.json['Vartotojo Klaida']) {
      dispatch(
        globalActions.showGlobalErrorModal({
          title: 'paymentFailureTitle',
          message: e.json['Vartotojo Klaida'],
        })
      );

      return;
    }

    if (e.json.errors && Array.isArray(e.json.errors) && e.json.errors.length > 0) {
      let message = e.json.errors[0].message;

      if (message) {
        if (message === 'Unsupported Card Brand') {
          message = 'unsupportedCard';
        }
      } else {
        message = 'paymentFailureMessage';
      }

      dispatch(cancelPayment());
      dispatch(
        globalActions.showGlobalErrorModal({
          title: 'paymentFailureTitle',
          message,
        })
      );
      return;
    }

    if (e.json.statusCode) {
      dispatch(cancelPayment());
      dispatch(actions.paymentFailure());
    }
  } else {
    dispatch(cancelPayment());
    dispatch(actions.paymentFailure());
  }
};

const beginRegistrationFeePayment = (): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      dispatch(actions.paymentInit());
      const res = await getPaymentDetails(getState);

      dispatch(actions.setCurrentReservationId(null));
      dispatch(actions.setPastPayments(res.payment_instrument));
      dispatch(globalActions.hideGlobalModal());

      dispatch(
        actions.showCardModal(res.amount, async (data: ICardValuesFormatted | IPaymentInstrument) => {
          try {
            dispatch(actions.paymentInit());
            const payRes = await pay(getState, 'registration', { ...res, payment_instrument: data });

            if (payRes.status === 'approved') {
              dispatch(actions.registrationPaymentSuccess());
            } else if (payRes.status === 'declined') {
              dispatch(actions.paymentFailure());
            } else if (payRes.status === 'pending' && payRes.authorization_information) {
              dispatch(authorizeActions.setParameters(payRes.authorization_information));
              dispatch(globalActions.hideGlobalModal());
              dispatch(actions.hideCardModal());

              dispatch(replace('/authorize'));
              dispatch(
                authorizeActions.pollPayment(
                  payRes.order_id,
                  () => {
                    dispatch(replace('/home'));
                    dispatch(actions.registrationPaymentSuccess());
                  },
                  () => {
                    dispatch(replace('/home'));
                    dispatch(actions.paymentFailure());
                  }
                )
              );
            }
          } catch (e) {
            onError(e, dispatch);
          }
        })
      );
    } catch (e) {
      onError(e, dispatch);
    }
  };
};

const beginReservationPayment = (onSuccess: () => void, onFailure: () => void): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const res = await getPaymentDetails(getState);

      dispatch(actions.setCurrentReservationId(res.reservation));
      dispatch(actions.setPastPayments(res.payment_instrument));
      dispatch(globalActions.hideGlobalModal());

      dispatch(
        actions.showCardModal(res.amount, async (data: ICardValuesFormatted | IPaymentInstrument) => {
          try {
            dispatch(actions.paymentInit());
            const payRes = await pay(getState, 'service', { ...res, payment_instrument: data });

            if (payRes.status === 'approved') {
              dispatch(actions.setCurrentReservationId(null));
              dispatch(globalActions.hideGlobalModal());
              dispatch(actions.reservationPaymentSuccess());
              onSuccess();
            } else if (payRes.status === 'declined') {
              dispatch(cancelPayment());
              dispatch(actions.paymentFailure());
            } else if (payRes.status === 'pending' && payRes.authorization_information) {
              dispatch(authorizeActions.setParameters(payRes.authorization_information));
              dispatch(globalActions.hideGlobalModal());
              dispatch(actions.hideCardModal());

              dispatch(replace('/authorize'));
              dispatch(
                authorizeActions.pollPayment(
                  payRes.order_id,
                  () => {
                    dispatch(actions.setCurrentReservationId(null));
                    dispatch(actions.reservationPaymentSuccess());
                    onSuccess();
                  },
                  () => {
                    dispatch(actions.paymentFailure());
                    dispatch(cancelPayment());
                    onFailure();
                  }
                )
              );
            }
          } catch (e) {
            onError(e, dispatch);
          }
        })
      );
    } catch (e) {
      onError(e, dispatch);
    }
  };
};

export const actions = {
  paymentInit: createAction(ActionTypes.PAYMENT_INIT),
  paymentSuccess: createAction(ActionTypes.PAYMENT_SUCCESS),
  paymentFailure: createAction(ActionTypes.PAYMENT_FAILURE),
  registrationPaymentSuccess: createAction(ActionTypes.REGISTRATION_PAYMENT_SUCCESS),
  reservationPaymentSuccess: createAction(ActionTypes.RESERVATION_PAYMENT_SUCCESS),
  showCardModal: createAction(
    ActionTypes.SHOW_CARD_MODAL,
    (action) => (amount: number, onConfirm: (data: ICardValuesFormatted | IPaymentInstrument) => void) =>
      action({ amount, onConfirm })
  ),
  hideCardModal: createAction(ActionTypes.HIDE_CARD_MODAL),
  setCurrentReservationId: createAction(
    ActionTypes.SET_CURRENT_RESERVATION_ID,
    (action) => (id: number | null) => action(id)
  ),
  setPastPayments: createAction(ActionTypes.SET_PAST_PAYMENTS, (action) => (payments: IPaymentInstrument[]) =>
    action(payments)
  ),
};

export default {
  ...actions,
  cancelPayment,
  beginRegistrationFeePayment,
  beginReservationPayment,
};
