//import AsyncStorage from '@react-native-community/async-storage';
import { AsyncStorage } from 'react-native';
import { Dimensions, Image } from 'react-native';

import { IReservation, IProduct, ICartProduct, ICartProducts, IVendor, ILocation } from '../StateTypes';
import { GetState } from '../api';

export const getDistance = (lat1, lon1, lat2, lon2) => {
  if (lat1 == lat2 && lon1 == lon2) {
    return 0;
  } else {
    var radlat1 = (Math.PI * lat1) / 180;
    var radlat2 = (Math.PI * lat2) / 180;
    var theta = lon1 - lon2;
    var radtheta = (Math.PI * theta) / 180;
    var dist =
      Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
    if (dist > 1) {
      dist = 1;
    }
    dist = Math.acos(dist);
    dist = (dist * 180) / Math.PI;
    dist = dist * 60 * 1.1515;
    dist = dist * 1.609344;
    return Number((dist * 1000).toFixed(0));
  }
};

export const formatCountdown = (secondsLeft: number): string => {
  let hours = Math.trunc(secondsLeft / 3600);
  let minutes = Math.trunc((secondsLeft / 60) % 60);
  let seconds = secondsLeft % 60;

  const hoursString = hours < 10 ? `0${hours}` : `${hours}`;
  const minutesString = minutes < 10 ? `0${minutes}` : `${minutes}`;
  const secondsString = seconds < 10 ? `0${seconds}` : `${seconds}`;

  if (hours > 0) {
    return `${hoursString}:${minutesString}:${secondsString}`;
  } else {
    return `${minutesString}:${secondsString}`;
  }
};

export const formatTimeWords = (timeInSeconds: number): string => {
  let hours = (timeInSeconds / 3600) | 0;
  let minutes = ((timeInSeconds - hours * 3600) / 60) | 0;
  let seconds = timeInSeconds % 60 | 0;

  if (hours === 0 && minutes === 0 && seconds !== 0) {
    return `${seconds} s`;
  }

  if (hours === 0 && minutes !== 0 && seconds !== 0) {
    return `${minutes} min ${seconds} s`;
  }

  if (hours === 0 && minutes !== 0 && seconds === 0) {
    return `${minutes} min`;
  }

  if (hours !== 0 && minutes === 0 && seconds === 0) {
    return `${hours} val`;
  }

  if (hours !== 0 && minutes !== 0 && seconds === 0) {
    return `${hours} val ${minutes} min`;
  }

  return `${hours} val ${minutes} min ${seconds} s`;
};

export const isLoginFormValid = (email: string, password: string): boolean => {
  if (email.trim().length !== 0 && password.trim().length >= 4) {
    return true;
  } else {
    return false;
  }
};

export const isRegisterFormValid = (email: string, password: string, password2: string): boolean => {
  if (email.trim().length !== 0 && password.trim().length >= 4 && password2.trim().length >= 4) {
    return true;
  } else {
    return false;
  }
};

export const getReservationProducts = (reservation: IReservation, allProducts: IProduct[]) => {
  const reservationProducts: ICartProduct[] = [];
  let vended: { [key: number]: number } = {};
  let errors: { [key: number]: number } = {};

  for (let resProduct of reservation.products) {
    if (resProduct.quantity > 0) {
      const product = allProducts.find((product) => product.id === resProduct.product)!;
      const reservationProduct = { ...product, amount: resProduct.quantity };

      if (reservation.vended) {
        vended = parseCommaList(reservation.vended);
      }

      if (reservation.errors) {
        errors = parseCommaList(reservation.errors);
      }

      if (vended[product.product_code] + errors[product.product_code] !== resProduct.quantity) {
        reservationProducts.push(reservationProduct);
      }
    }
  }
  return reservationProducts;
};

export const getCartSum = (cart: ICartProducts, getState: GetState) => {
  const allProducts = getState().globalReducer.products;
  let sum = 0;
  let reservationFee = 0;

  for (let id in cart) {
    if (cart[id] > 0) {
      const product = allProducts.find((product) => product.id === Number(id))!;
      sum += cart[id] * product.storage_fee;
      reservationFee += cart[id] * product.reservation_fee;
    }
  }

  return { amount: sum, reservationFee };
};

export const getVaultPaymentAmount = (cart: ICartProducts, getState: GetState) => {
  const allProducts = getState().globalReducer.products;
  const { debt_extra_fixed, debt_extra_percents } = getState().globalReducer.fees!;

  let amount = 0;

  for (let id in cart) {
    const product = allProducts.find((product) => product.id === Number(id))!;
    amount += cart[id] * product.price;
  }

  const fee = Math.round(amount * Number(debt_extra_percents / 100) + Number(debt_extra_fixed * 100));

  return amount + fee;
};

export const getVendingCode = (productCode: number) => {
  let stringNumber = String(productCode);

  switch (stringNumber[0]) {
    case '1':
      return `A${stringNumber.substring(1)}`;
    case '2':
      return `B${stringNumber.substring(1)}`;
    case '3':
      return `C${stringNumber.substring(1)}`;
    case '4':
      return `D${stringNumber.substring(1)}`;
    default:
      throw 'Incorrect product code';
  }
};

export const parseCommaList = (list: string | null): { [key: number]: number } => {
  if (list === null) {
    return {};
  }

  const array = list.split(',').filter(Boolean).map(Number);

  const values: { [key: number]: number } = {};

  for (let i = 0; i < array.length; i++) {
    values[array[i]] = (values[array[i]] || 0) + 1;
  }

  return values;
};

export const formatCurrency = (sum: number) => (sum / 100).toFixed(2) + '\u20AC';

export const storeVendingProductId = async (id: number) => {
  try {
    return await AsyncStorage.setItem('vendingProductId', String(id));
  } catch (e) {
    console.error(e);
  }
};

export const getVendingProductId = async () => {
  try {
    const id = await AsyncStorage.getItem('vendingProductId');

    if (id !== null) {
      return Number(id);
    }
  } catch (e) {
    console.error(e);
  }
};

export const removeVendingProductId = async () => {
  try {
    await AsyncStorage.removeItem('vendingProductId');
  } catch (e) {
    console.error(e);
  }
};

export const getWeekday = (index: number) => {
  switch (index) {
    case 0:
      return 'Pirmadieniais';
    case 1:
      return 'Antradieniais';
    case 2:
      return 'Treiadieniais';
    case 3:
      return 'Ketvirtadieniais';
    case 4:
      return 'Penktadieniais';
    case 5:
      return 'etadieniais';
    case 6:
      return 'Sekmadieniais';
  }
};

export const isUserNearby = (vendor: IVendor, userLocation: ILocation) => {
  return getDistance(vendor.latitude, vendor.longitude, userLocation.latitude, userLocation.longitude) < 30;
};

export type OrientationType = 'portrait' | 'landscape';
export const getPhoneOrientation = (): OrientationType => {
  if (Dimensions.get('window').height > Dimensions.get('window').width) {
    return 'portrait';
  } else return 'landscape';
};

export const getMaxItemsInfoSuffix = (maxItems: number): string => {
  switch (maxItems) {
    case 1:
      return 'vienet';
    default:
      return 'vienetus';
  }
};

export const shouldUseContain = (url: string): Promise<boolean> => {
  return new Promise((resolve) => {
    Image.getSize(
      url,
      (width, height) => {
        if (width / height < 0.75) {
          resolve(true);
        } else {
          resolve(false);
        }
      },
      (err) => {}
    );
  });
};
