import { createAction } from 'typesafe-actions';
import { replace } from 'connected-react-router';

import * as ActionTypes from '../ActionTypes';
import {
  IVendor,
  IProduct,
  ITakeModalInfo,
  IGlobalModal,
  IGlobalModalBody,
  IUserMembership,
  IReservation,
  IFees,
  AppLanguage,
} from '../StateTypes';
import { ThunkAction } from './helpers';
import {
  getUserMembership,
  getUserReservations,
  getAllVendors,
  getAllProducts,
  getVault,
  getFees,
  apiInit,
} from '../api';
import vendorActions from './vendor';
import userActions from './user';
import { ToastAndroid } from 'react-native';

export const fetchAppData = (): ThunkAction => {
  return async (dispatch, getState) => {
    dispatch(actions.fetchAppDataInit());
    try {
      await apiInit(getState);

      const [
        membership,
        reservations,
        vendors,
        products,
        vault,
        { debt_extra_fixed, debt_extra_percents },
      ] = await Promise.all([
        getUserMembership(getState),
        getUserReservations(getState),
        getAllVendors(getState),
        getAllProducts(getState),
        getVault(getState),
        getFees(getState),
      ]);

      dispatch(userActions.setVault(vault));
      dispatch(actions.setFees({ debt_extra_fixed, debt_extra_percents }));
      dispatch(actions.fetchAppDataSuccess(vendors, products, membership, reservations));
      dispatch(replace('/home'));

      const lastReservation: IReservation | undefined = reservations[0];
      if (lastReservation && (lastReservation.status === 1 || lastReservation.status === 3)) {
        dispatch(vendorActions.pollReservations(lastReservation));
      }
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.fetchAppDataFailure(e.json));
        return;
      }

      dispatch(
        actions.fetchAppDataFailure({ title: 'unexpectedErrorTitle', message: JSON.stringify(e.json) })
      );
    }
  };
};

const setSelectedVendorById = (id: number): ThunkAction => {
  return (dispatch, getState) => {
    const selectedVendor = getState().globalReducer.vendors.find((vendor) => vendor.id === id);

    if (selectedVendor) {
      dispatch(actions.setSelectedVendor(selectedVendor));
    }
  };
};

const initToastMessage = (message: string): ThunkAction => {
  return (dispatch, getState) => {
    const { show } = getState().globalReducer.toastMessage;

    if (!show) {
      dispatch(actions.showToastMessage({ message, duration: ToastAndroid.SHORT }));
    }
  };
};

export const actions = {
  showTakeModal: createAction(ActionTypes.SHOW_TAKE_MODAL, (action) => (info: ITakeModalInfo) =>
    action(info)
  ),
  hideTakeModal: createAction(ActionTypes.HIDE_TAKE_MODAL),
  showVaultModal: createAction(ActionTypes.SHOW_VAULT_MODAL),
  hideVaultModal: createAction(ActionTypes.HIDE_VAULT_MODAL),
  showGlobalModal: createAction(ActionTypes.SHOW_GLOBAL_MODAL, (action) => (modal: IGlobalModal) =>
    action(modal)
  ),
  hideGlobalModal: createAction(ActionTypes.HIDE_GLOBAL_MODAL),
  showGlobalErrorModal: createAction(
    ActionTypes.SHOW_GLOBAL_ERROR_MODAL,
    (action) => (body: IGlobalModalBody) => action(body)
  ),
  openDrawer: createAction(ActionTypes.OPEN_DRAWER),
  closeDrawer: createAction(ActionTypes.CLOSE_DRAWER),
  fetchAppDataInit: createAction(ActionTypes.FETCH_APP_DATA_INIT),
  fetchAppDataSuccess: createAction(
    ActionTypes.FETCH_APP_DATA_SUCCESS,
    (action) => (
      vendors: IVendor[],
      products: IProduct[],
      membership: IUserMembership,
      reservations: IReservation[]
    ) => action({ vendors, products, membership, reservations })
  ),
  fetchAppDataFailure: createAction(
    ActionTypes.FETCH_APP_DATA_FAILURE,
    (action) => (body: IGlobalModalBody) => action(body)
  ),
  setSelectedVendor: createAction(ActionTypes.SET_SELECTED_VENDOR, (action) => (vendor: IVendor) =>
    action(vendor)
  ),
  showCodeModal: createAction(ActionTypes.SHOW_CODE_MODAL, (action) => (code: string) => action(code)),
  hideCodeModal: createAction(ActionTypes.HIDE_CODE_MODAL),
  setFees: createAction(ActionTypes.SET_FEES, (action) => (fees: IFees) => action(fees)),
  showToastMessage: createAction(
    ActionTypes.SHOW_TOAST_MESSAGE,
    (action) => (params: { message: string; duration: number }) => action(params)
  ),
  hideToastMessage: createAction(ActionTypes.HIDE_TOAST_MESSAGE),
  showReservationInfoModal: createAction(ActionTypes.SHOW_RESERVATION_INFO_MODAL),
  setLocationEnabled: createAction(ActionTypes.SET_LOCATION_ENABLED, (action) => (enabled: boolean) =>
    action(enabled)
  ),
  showLanguageModal: createAction(ActionTypes.SHOW_LANGUAGE_MODAL),
  hideLanguageModal: createAction(ActionTypes.HIDE_LANGUAGE_MODAL),
  setAppLanguage: createAction(ActionTypes.SET_APP_LANGUAGE, (action) => (language: AppLanguage) =>
    action(language)
  ),
};

export default { ...actions, fetchAppData, setSelectedVendorById, initToastMessage };
