import * as React from 'react';
import { Component } from 'react';
import { StyleSheet, TouchableOpacity, View, Text } from 'react-native';
import ShoppingBasket from '@material-ui/icons/ShoppingBasket';
import { withRouter, RouteComponentProps } from 'react-router-dom';

import { DarkGray, Primary } from '../../../../Colors';

interface IBasketProps extends RouteComponentProps {
  count: number;
}

class Basket extends Component<IBasketProps> {
  onPress = () => {
    const { location, history } = this.props;

    if (location.pathname === '/home/machine') {
      history.push('/home/cart');
    } else if (location.pathname === '/home/vault/items') {
      history.push('/home/vault/confirm');
    }
  };

  render() {
    const { count } = this.props;

    return (
      <TouchableOpacity activeOpacity={0.9} onPress={this.onPress}>
        <ShoppingBasket style={{ fontSize: 50, color: DarkGray }} />
        <View style={styles.circleWrapper}>
          <View style={styles.circle}>
            <Text style={styles.count}>{count}</Text>
          </View>
        </View>
      </TouchableOpacity>
    );
  }
}

const styles = StyleSheet.create({
  icon: {
    fontSize: 50,
    color: DarkGray,
  },
  circleWrapper: {
    position: 'absolute',
    left: 10,
    top: 12,
  },
  circle: {
    width: 30,
    height: 30,
    borderRadius: 15,
    backgroundColor: Primary,
    justifyContent: 'center',
    alignItems: 'center',
  },
  count: {
    color: 'white',
    fontSize: 16,
  },
});

export default withRouter(Basket);
