import { createAction } from 'typesafe-actions';

import { pollPayment as pollPaymentAPI } from '../api';
import { IAuthInfo } from '../api/types';
import * as ActionTypes from '../ActionTypes';
import globalActions from './global';
import { ThunkAction } from './helpers';

let pollInterval: number | null = null;

const pollPayment = (orderId: string, onSuccess: () => void, onFailure: () => void): ThunkAction => {
  return (dispatch, getState) => {
    if (!pollInterval) {
      pollInterval = setInterval(async () => {
        try {
          const { status } = await pollPaymentAPI(getState, orderId);

          if (status === 'approved') {
            dispatch(clearPaymentPoll());
            onSuccess();
          } else if (status === 'declined') {
            dispatch(clearPaymentPoll());
            onFailure();
          }
        } catch (e) {
          console.error(e.json);

          if (e.json.statusCode >= 500) {
            dispatch(globalActions.showGlobalErrorModal(e.json));
          }
        }
      }, 1000);
    } else {
      console.error('Payment poll is already running.');
    }
  };
};

const clearPaymentPoll = (): ThunkAction => {
  return async (dispatch, getState) => {
    if (pollInterval) {
      clearInterval(pollInterval);
      pollInterval = null;
    }
  };
};

export const actions = {
  setHTML: createAction(ActionTypes.SET_HTML, (action) => (html: string) => action(html)),
  setParameters: createAction(ActionTypes.SET_PARAMETERS, (action) => (info: IAuthInfo) => action(info)),
};

export default { ...actions, pollPayment, clearPaymentPoll };
