import * as React from 'react';
import { Component } from 'react';
import { View, Text, TouchableOpacity, StyleSheet, AsyncStorage } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import globalActions from './actions/global';
import { IState, AppLanguage } from './StateTypes';
import { DarkPrimary, Gray } from './Colors';

type ChangeLanguageProps = WithTranslation & ReturnType<typeof mapStateToProps> & typeof localActions;

class ChangeLanguage extends Component<ChangeLanguageProps> {
  languages: string[];

  constructor(props: ChangeLanguageProps) {
    super(props);
    this.languages = props.i18n.languages;
  }

  onChange = (language: AppLanguage) => {
    const { i18n, setAppLanguage } = this.props;

    i18n.changeLanguage(language);
    setAppLanguage(language);
    AsyncStorage.setItem('language', language);
  };

  render() {
    const { language: appLanguage } = this.props;

    return (
      <View style={styles.wrapper}>
        {this.languages.map((language) => (
          <TouchableOpacity
            key={language}
            style={{ paddingVertical: 16, paddingHorizontal: 8 }}
            onPress={() => this.onChange(language as AppLanguage)}
            activeOpacity={0.8}
          >
            <Text style={{ color: language === appLanguage ? DarkPrimary : Gray, fontSize: 16 }}>
              {language.toUpperCase()}
            </Text>
          </TouchableOpacity>
        ))}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    position: 'absolute',
    backgroundColor: '#fff',
    right: 8,
    top: 0,
    flexDirection: 'row',
  },
});

const mapStateToProps = (state: IState) => {
  return {
    language: state.globalReducer.language,
  };
};

const localActions = {
  setAppLanguage: globalActions.setAppLanguage,
};

export default withTranslation('language')(connect(mapStateToProps, localActions)(ChangeLanguage));
