import React, { Component } from 'react';
import { StyleSheet, View, Text, Image } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import PrimaryButton from '../../../components/PrimaryButton';

import { ICartProduct, IState, IVendor } from '../../../StateTypes';
import { Black, LightGray, Primary } from '../../../Colors';
import { isUserNearby, shouldUseContain } from '../../../helpers';
import globalActions from '../../../actions/global';

interface ICartCheckoutItemOwnProps {
  vendor: IVendor;
  item: ICartProduct;
  isVending: boolean;
  onTakeProduct: (productId: number) => void;
}

type ICartCheckoutItemProps = ICartCheckoutItemOwnProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

interface ICartCheckoutItemState {
  useContain: boolean;
}

class CartCheckoutItem extends Component<ICartCheckoutItemProps, ICartCheckoutItemState> {
  state = {
    useContain: true,
  };

  async componentDidMount() {
    const useContain = await shouldUseContain(this.props.item.cover);
    this.setState({ useContain });
  }

  onTake = (id: number) => {
    const { onTakeProduct, showToastMessage, t } = this.props;

    if (this.isUserNearby()) {
      onTakeProduct(id);
    } else {
      showToastMessage(t('item.onlyNearby'));
    }
  };

  isUserNearby = () => {
    const { vendor, userLocation } = this.props;
    return isUserNearby(vendor, userLocation);
  };

  render() {
    const { item, isVending, t } = this.props;
    const { useContain } = this.state;

    return (
      <View key={item.id} style={styles.wrapper}>
        <View style={{ flex: 1, flexDirection: 'row', marginRight: 16 }}>
          <View style={{ width: 50, height: 70, marginRight: 12 }}>
            <Image
              style={{ flex: 1 }}
              resizeMode={useContain ? 'contain' : 'cover'}
              source={{ uri: item.cover }}
            />
          </View>
          <View style={{ flex: 1, justifyContent: 'center' }}>
            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.name}>{item.name}</Text>
            </View>
            <View style={{ flexDirection: 'row' }}>
              <Text style={styles.type}>{item.type.toUpperCase()}</Text>
              <Text style={styles.proof}>{item.proof}%</Text>
            </View>
          </View>
        </View>

        <View style={{ marginRight: 16 }}>
          <Text style={{ fontSize: 16, color: Black }}>
            {item.amount} {t('item.quantity')}
          </Text>
        </View>

        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
          {!isVending ? (
            <PrimaryButton width={85} disabled={!this.isUserNearby()} onPress={() => this.onTake(item.id)}>
              {t('item.take').toUpperCase()}
            </PrimaryButton>
          ) : (
            <View style={{ width: 85, paddingVertical: 8 }} />
          )}
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    flex: 1,
    paddingHorizontal: 16,
    paddingVertical: 12,
    borderBottomWidth: 1,
    borderBottomColor: LightGray,
  },
  imageWrapper: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 50,
    height: 70,
    marginRight: 12,
    borderWidth: 1,
  },
  type: { fontSize: 10, letterSpacing: 1, marginRight: 4 },
  proof: {
    fontSize: 10,
    letterSpacing: 1,
  },
  name: {
    flex: 1,
    flexWrap: 'wrap',
    fontSize: 16,
    color: Black,
    fontWeight: 'bold',
  },
  totalPrice: {
    fontSize: 20,
    color: Black,
    fontWeight: 'bold',
    textAlign: 'center',
  },
  takeButton: {
    justifyContent: 'center',
    alignItems: 'center',
    width: 85,
    paddingVertical: 8,
    borderRadius: 40,
    backgroundColor: Primary,
    elevation: 3,
  },
  takeText: {
    color: 'white',
    fontSize: 16,
  },
});

const mapStateToProps = (state: IState) => ({
  userLocation: state.userReducer.location,
});

const localActions = {
  showToastMessage: globalActions.initToastMessage,
};

export default withTranslation('cartCheckout')(connect(mapStateToProps, localActions)(CartCheckoutItem));
