import React, { Component } from 'react';
import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { OverridableComponent } from '@material-ui/core/OverridableComponent';
import { SvgIconTypeMap, FormHelperText } from '@material-ui/core';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Black, Primary, DarkGray } from '../../../../../Colors';
import { IState } from '../../../../../StateTypes';
import globalActions from '../../../../../actions/global';

interface IMenuItemOwnProps {
  to: string;
  icon: OverridableComponent<SvgIconTypeMap<{}, 'svg'>>;
  children: React.ReactNode;
  selected?: boolean;
  exact?: true;
  disabled?: boolean;
}

type IMenuItemProps = IMenuItemOwnProps &
  WithTranslation &
  ReturnType<typeof mapStateToProps> &
  typeof localActions;

class MenuItem extends Component<IMenuItemProps> {
  onPress = (e: any) => {
    const { disabled, closeDrawer } = this.props;

    if (disabled) {
      e.preventDefault();
    } else {
      closeDrawer();
    }
  };

  getIconColor = (selected: boolean, disabled: boolean) => {
    if (disabled) {
      return 'hsl(0, 0%, 82%)';
    }

    if (selected) {
      return Primary;
    } else {
      return DarkGray;
    }
  };

  getTextColor = (selected: boolean, disabled: boolean) => {
    if (disabled) {
      return 'hsl(0, 0%, 82%)';
    }

    if (selected) {
      return Primary;
    } else {
      return Black;
    }
  };

  render() {
    const { to, icon: Icon, children, pathname, exact, disabled, t } = this.props;
    let { selected } = this.props;

    if (selected === undefined) {
      if (exact) {
        selected = pathname === to;
      } else {
        selected = pathname.startsWith(to);
      }
    }

    return (
      <Link
        to={to}
        onClick={this.onPress}
        style={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          padding: 8,
          borderRadius: 4,
          backgroundColor: selected ? 'hsl(28, 87%, 95%)' : undefined,
          textDecoration: 'none',
        }}
      >
        <Icon style={{ marginRight: 30, fontSize: 24, color: this.getIconColor(selected, !!disabled) }} />
        <View style={{ flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
          <Text style={{ color: this.getTextColor(selected, !!disabled) }}>{children}</Text>

          {disabled && <Text style={{ color: 'hsl(0, 0%, 82%)', textAlign: 'right' }}>{t('soon')}...</Text>}
        </View>
      </Link>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flexDirection: 'row', alignItems: 'center', padding: 8, borderRadius: 4 },
});

const mapStateToProps = (state: IState) => ({
  pathname: state.router.location.pathname,
});

const localActions = {
  closeDrawer: globalActions.closeDrawer,
};

export default withTranslation('menu')(connect(mapStateToProps, localActions)(MenuItem));
