import React, { Component } from 'react';
import { Text, TouchableOpacity } from 'react-native';

import { Primary, DarkGray, LightGray } from '../Colors';

interface IPrimaryButtonProps {
  onPress: () => void;
  disabled?: boolean;
  width: number;
  height?: number;
}

class PrimaryButton extends Component<IPrimaryButtonProps> {
  render() {
    const { disabled = false, width, height = 36, onPress, children } = this.props;
    return (
      <div
        style={{
          width,
          height,
          backgroundColor: disabled ? LightGray : Primary,
          justifyContent: 'center',
          alignItems: 'center',
          alignSelf: 'center',
          borderRadius: 25,
          boxShadow: '0px 1px 2px rgba(0, 0, 0, 0.3)',
        }}
      >
        <TouchableOpacity
          activeOpacity={0.8}
          style={{
            width,
            height,
            backgroundColor: disabled ? LightGray : Primary,
            justifyContent: 'center',
            alignItems: 'center',
            alignSelf: 'center',
            borderRadius: 25,
            elevation: 3,
          }}
          onPress={onPress}
        >
          <Text style={{ color: disabled ? DarkGray : 'white' }}>{children}</Text>
        </TouchableOpacity>
      </div>
    );
  }
}

export default PrimaryButton;
