import React, { Component } from 'react';
import { StyleSheet, View, ScrollView, TouchableOpacity } from 'react-native';
import { RouteComponentProps, Link } from 'react-router-dom';
import { connect } from 'react-redux';

import Item from '../VendingMachine/Item';

import { IState, ICartProduct } from '../../../StateTypes';
import Selected from '../Selected';
import userActions from '../../../actions/user';

type IVaultItemsProps = RouteComponentProps & ReturnType<typeof mapStateToProps> & typeof localActions;

class VaultItems extends Component<IVaultItemsProps> {
  getCartProductCount = (productId: number): number => {
    const { cart } = this.props;

    if (cart && cart[productId]) {
      return cart[productId];
    } else {
      return 0;
    }
  };

  render() {
    const { allProducts, cart, addToCart, removeFromCart } = this.props;

    let cartAmount = 0;
    let cartSum = 0;

    const cartProducts: ICartProduct[] = [];

    for (let productId in cart) {
      const product = allProducts.find((product) => product.id === Number(productId))!;
      const cartProduct = { ...product, amount: cart[productId] };

      cartProducts.push(cartProduct);
      cartAmount += cart[productId];
      cartSum += cartProduct.price * cartProduct.amount;
    }

    return (
      <View style={styles.wrapper}>
        <ScrollView>
          {allProducts.map((item) => (
            <Item
              key={item.id}
              cartAmount={cartAmount}
              cartSum={cartSum}
              count={this.getCartProductCount(item.id)}
              addToCart={addToCart}
              removeFromCart={removeFromCart}
              onTakeProduct={() => {}}
              stock={999}
              layoutType='vault'
              {...item}
            />
          ))}
        </ScrollView>
        <TouchableOpacity activeOpacity={0.8}>
          <Link
            to='/home/vault/confirm'
            style={{ pointerEvents: cartAmount <= 0 ? 'none' : 'auto', textDecoration: 'none' }}
          >
            <Selected type='vault' title='Saugyklos papildymas' cartAmount={cartAmount} />
          </Link>
        </TouchableOpacity>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  wrapper: { flex: 1 },
});

const mapStateToProps = (state: IState) => ({
  allProducts: state.globalReducer.products,
  cart: state.userReducer.vaultCart,
});

const localActions = {
  addToCart: userActions.addProductToVaultCart,
  removeFromCart: userActions.removeProductFromVaultCart,
};

export default connect(mapStateToProps, localActions)(VaultItems);
