import * as React from 'react';
import { Component } from 'react';
import { View } from 'react-native';
import { Switch, Route, RouteComponentProps, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import Idenfy from './Idenfy';
import Success from './Success';
import Failure from './Failure';
import { IState } from '../../StateTypes';

type IIdentificationProps = RouteComponentProps & ReturnType<typeof mapStateToProps>;

class Identification extends Component<IIdentificationProps> {
  render() {
    const { loggedIn } = this.props;
    const { path } = this.props.match;

    if (!loggedIn) {
      return <Redirect to='/' />;
    }

    return (
      <View style={{ flex: 1 }}>
        <Switch>
          <Route exact path={path} component={Idenfy} />
          <Route path={`${path}/success`} component={Success} />
          <Route path={`${path}/failure`} component={Failure} />
        </Switch>
      </View>
    );
  }
}

const mapStateToProps = (state: IState) => ({
  loggedIn: state.loginReducer.loggedIn,
});

export default connect(mapStateToProps)(Identification);
