import { createAction } from 'typesafe-actions';
import { replace, push } from 'connected-react-router';

import * as ActionTypes from '../ActionTypes';
import { IVendorProduct, IReservation, IState, IGlobalModalBody, ICartProducts } from '../StateTypes';
import { ThunkAction } from './helpers';
import globalActions from './global';
import userActions from './user';
import paymentActions from './payment';
import {
  getVendorProducts,
  createReservation as createReservationApi,
  vend as vendApi,
  getVault,
} from '../api';
import { ThunkDispatch } from 'redux-thunk';
import {
  getVendingCode,
  parseCommaList,
  storeVendingProductId,
  getVendingProductId,
  removeVendingProductId,
  getCartSum,
} from '../helpers';

let pollInterval: number;
let vended: { [key: number]: number } = {};
let errors: { [key: number]: number } = {};

const fetchVendorData = (id: number, cb?: () => void): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const allProducts = getState().globalReducer.products;
      let vendorProducts: IVendorProduct[] = [];

      const [products, vault] = await Promise.all([getVendorProducts(getState, id), getVault(getState)]);

      for (let product of allProducts) {
        for (let resProduct of products.products_in_vendor) {
          if (product.id === resProduct.product) {
            const vendorProduct: IVendorProduct = { ...product, stock: resProduct.stock };
            vendorProducts.push(vendorProduct);
          }
        }
      }

      dispatch(actions.fetchVendorProductsSuccess(vendorProducts));
      dispatch(userActions.setVault(vault));

      cb && cb();
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.fetchVendorProductsFailure(e.json));
        return;
      }

      dispatch(
        globalActions.showGlobalErrorModal({ title: 'unexpectedErrorTitle', message: JSON.stringify(e.json) })
      );
    }
  };
};

const getProducts = (id: number): ThunkAction => {
  return async (dispatch, getState) => {
    dispatch(actions.fetchVendorProductsInit());
    try {
      const res = await getVendorProducts(getState, id);
      const allProducts = getState().globalReducer.products;

      let vendorProducts: IVendorProduct[] = [];

      for (let product of allProducts) {
        for (let resProduct of res.products_in_vendor) {
          if (product.id === resProduct.product) {
            const vendorProduct: IVendorProduct = { ...product, stock: resProduct.stock };
            vendorProducts.push(vendorProduct);
          }
        }
      }

      dispatch(actions.fetchVendorProductsSuccess(vendorProducts));
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.fetchVendorProductsFailure(e.json));
        return;
      }

      dispatch(
        actions.fetchVendorProductsFailure({
          title: 'productLoadTitle',
          message: 'productLoadMessage',
        })
      );
    }
  };
};

const createReservation = (
  vendorId: number,
  cart: ICartProducts,
  options: { instaVend?: boolean; productId: number } = { instaVend: false, productId: 0 }
): ThunkAction => {
  return async (dispatch, getState) => {
    dispatch(actions.createReservationInit());
    try {
      if (options.instaVend) {
        await createReservationApi(getState, vendorId, cart, true);
        dispatch(
          paymentActions.beginReservationPayment(
            () => {
              dispatch(
                userActions.getReservations((lastReservation) => {
                  dispatch(replace('/home/machine'));
                  dispatch(actions.vendInit());
                  dispatch(vend(options.productId));
                  dispatch(pollReservations(lastReservation));
                })
              );
            },
            () => {
              dispatch(replace('/home/machine'));
            }
          )
        );
      } else {
        await createReservationApi(getState, vendorId, cart, false);
        dispatch(
          paymentActions.beginReservationPayment(
            () => {
              dispatch(
                userActions.getReservations((lastReservation) => {
                  dispatch(replace('/home/cart'));
                  dispatch(push('/home/cartCheckout'));
                  dispatch(pollReservations(lastReservation));
                  dispatch(userActions.resetCart());
                })
              );
            },
            () => {
              dispatch(replace('/home/cart'));
            }
          )
        );
      }
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.createReservationFailure(e.json));
        return;
      }

      let error: IGlobalModalBody = { title: '', message: '' };

      if (e.json['Narysts klaida']) {
        error.title = 'Narysts klaida';
        error.message = e.json['Narysts klaida'];
      }

      if (e.json['Rezervacijos klaida']) {
        error.title = 'Rezervacijos klaida';
        error.message = e.json['Rezervacijos klaida'][0];
      }

      if (e.json['Vartotojo profilio klaida']) {
        error.title = 'Vartotojo profilio klaida';
        error.message = e.json['Vartotojo profilio klaida'];
      }

      if (e.json['Saugyklos klaida']) {
        error.title = 'Saugyklos klaida';
        error.message = e.json['Saugyklos klaida'];

        dispatch(getProducts(vendorId));
      }

      dispatch(actions.createReservationFailure(error));
    }
  };
};

const pollReservations = (lastReservation: IReservation): ThunkAction => {
  return (dispatch) => {
    errors = parseCommaList(lastReservation.errors);
    vended = parseCommaList(lastReservation.vended);

    pollInterval = setInterval(() => {
      dispatch(userActions.getReservations(getReservationsCb));
    }, 1000);
  };
};

const clearPollInterval = (): ThunkAction => {
  return () => clearInterval(pollInterval);
};

const onReservationCancel = (): ThunkAction => {
  return (dispatch, getState) => {
    clearInterval(pollInterval);
    dispatch(userActions.getData());
    dispatch(
      globalActions.showGlobalErrorModal({
        title: 'reservationTimeEndTitle',
        message: 'reservationTImeEndMessage',
      })
    );

    if (getState().router.location.pathname === '/home/cartCheckout') {
      dispatch(replace('/home'));
    }
  };
};

const onReservationVending = (): ThunkAction => {
  return async (dispatch, getState) => {
    const { show } = getState().globalReducer.codeModal;

    if (!show) {
      dispatch(globalActions.hideGlobalModal());

      const id = await getVendingProductId();

      if (id) {
        try {
          dispatch(globalActions.showCodeModal(getVendingCode(id)));
        } catch (e) {
          console.error(e);
        }
      }
    }
  };
};

const onVendTimesUp = (): ThunkAction => {
  return (dispatch) => {
    dispatch(globalActions.hideCodeModal());
    dispatch(
      globalActions.showGlobalErrorModal({
        title: 'vendTimeEndTitle',
        message: 'vendTimeEndMessage',
      })
    );
  };
};

const onReservationDone = (): ThunkAction => {
  return (dispatch, getState) => {
    const selectedVendor = getState().globalReducer.selectedVendor;

    clearInterval(pollInterval);
    dispatch(userActions.getData());

    if (selectedVendor) {
      dispatch(fetchVendorData(selectedVendor.id));
    }

    if (getState().router.location.pathname === '/home/cartCheckout') {
      dispatch(replace('/home'));
    }
  };
};

const getReservationsCb = async (
  lastReservation: IReservation,
  dispatch: ThunkDispatch<IState, undefined, any>
) => {
  if (lastReservation === undefined) {
    clearInterval(pollInterval);
    return;
  }

  if (lastReservation.status === 0) {
    return dispatch(onReservationCancel());
  }

  if (lastReservation.status === 3) {
    dispatch(onReservationVending());
  }

  if (lastReservation.errors) {
    const newErrors = parseCommaList(lastReservation.errors);

    for (let product_code in newErrors) {
      if (newErrors[product_code] > (errors[product_code] || 0)) {
        dispatch(onVendTimesUp());
      }
    }

    errors = newErrors;
  }

  if (lastReservation.vended) {
    const newVended = parseCommaList(lastReservation.vended);

    for (let product_code in newVended) {
      if (newVended[product_code] > (vended[product_code] || 0)) {
        dispatch(globalActions.hideCodeModal());
        dispatch(actions.vendSuccess());
        await removeVendingProductId();
      }
    }

    vended = newVended;
  }

  if (lastReservation.status === 2) {
    return dispatch(onReservationDone());
  }
};

const vend = (productId: number): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      await vendApi(getState, productId);
      const product = getState().globalReducer.products.find((product) => product.id === productId)!;
      await storeVendingProductId(product.product_code);
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.vendFailure(e.json));
        return;
      }

      dispatch(
        actions.vendFailure({
          title: 'vendErrorTitle',
          message: e.json['Produkto idavimo klaida'],
        })
      );
    }
  };
};

const takeProduct = (vendorId: number, productId: number): ThunkAction => {
  return (dispatch, getState) => {
    const vendor = getState().globalReducer.vendors.find((vendor) => vendor.id === vendorId)!;
    const cart: ICartProducts = {};

    for (let product of vendor.products) {
      cart[product] = 0;
    }
    cart[productId] = 1;

    dispatch(createReservation(vendorId, cart, { instaVend: true, productId }));
  };
};

const takeProductFromReservation = (productId: number): ThunkAction => {
  return (dispatch) => {
    dispatch(actions.vendInit());
    dispatch(vend(productId));
  };
};

export const actions = {
  fetchVendorProductsInit: createAction(ActionTypes.FETCH_VENDOR_PRODUCTS_INIT),
  fetchVendorProductsSuccess: createAction(
    ActionTypes.FETCH_VENDOR_PRODUCTS_SUCCESS,
    (action) => (vendorProducts: IVendorProduct[]) => action(vendorProducts)
  ),
  fetchVendorProductsFailure: createAction(
    ActionTypes.FETCH_VENDOR_PRODUCTS_FAILURE,
    (action) => (body: IGlobalModalBody) => action(body)
  ),

  createReservationInit: createAction(ActionTypes.CREATE_RESERVATION_INIT),
  createReservationSuccess: createAction(ActionTypes.CREATE_RESERVATION_SUCCESS),
  createReservationFailure: createAction(
    ActionTypes.CREATE_RESERVATION_FAILURE,
    (action) => (body: IGlobalModalBody) => action(body)
  ),
  vendInit: createAction(ActionTypes.VEND_INIT),
  vendSuccess: createAction(ActionTypes.VEND_SUCCESS),
  vendFailure: createAction(ActionTypes.VEND_FAILURE, (action) => (body: IGlobalModalBody) => action(body)),
};

export default {
  ...actions,
  fetchVendorData,
  getProducts,
  createReservation,
  pollReservations,
  takeProduct,
  vend,
  takeProductFromReservation,
  clearPollInterval,
};
