import * as React from 'react';
import { Component } from 'react';
import { Text, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import paymentActions from '../../actions/payment';
import { Red } from '../../Colors';

type INotPayedProps = typeof localActions;

class NotPayed extends Component<INotPayedProps & WithTranslation> {
  render() {
    const { t } = this.props;
    return (
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={this.props.begin}
        style={{ padding: 8, backgroundColor: Red }}
      >
        <Text style={{ fontSize: 16, color: 'white', textAlign: 'center' }}>{t('beginPayment')}</Text>
        <Text style={{ fontSize: 12, color: 'hsla(0, 100%, 100%, 0.9)', textAlign: 'center' }}>
          {t('needPaymentAndIdentification')}
        </Text>
      </TouchableOpacity>
    );
  }
}

const localActions = {
  begin: paymentActions.beginRegistrationFeePayment,
};

export default withTranslation('notPayed')(connect(null, localActions)(NotPayed));
