import * as React from 'react';
import { Component } from 'react';
import { Text, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import loginActions from '../../actions/login';
import { Red } from '../../Colors';

type INotIdentifiedProps = typeof localActions;

class NotIdentified extends Component<INotIdentifiedProps & WithTranslation> {
  render() {
    const { t, reidentify } = this.props;

    return (
      <TouchableOpacity activeOpacity={0.8} onPress={reidentify} style={{ padding: 8, backgroundColor: Red }}>
        <Text style={{ fontSize: 16, color: 'white', textAlign: 'center' }}>{t('beginIdentification')}</Text>
        <Text style={{ fontSize: 12, color: 'hsla(0, 100%, 100%, 0.9)', textAlign: 'center' }}>
          {t('needIdentification')}
        </Text>
      </TouchableOpacity>
    );
  }
}

const localActions = {
  reidentify: loginActions.reidentify,
};

export default withTranslation('notIdentified')(connect(null, localActions)(NotIdentified));
