import { createAction } from 'typesafe-actions';
import { replace, goBack, go, push } from 'connected-react-router';

import * as ActionTypes from '../ActionTypes';
import { ThunkAction } from './helpers';
import {
  ILocation,
  IUserMembership,
  IReservation,
  IState,
  ICreateUserError,
  IGlobalModalBody,
  IUserVault,
  ICartProducts,
} from '../StateTypes';
import {
  createUser,
  pollUser,
  getUserMembership,
  getUserReservations,
  getVault as apiGetVault,
  deleteLastVaultOrder as apiDeleteLastVaultOrder,
  addToVault,
} from '../api';
import { ThunkDispatch } from 'redux-thunk';
import globalActions from './global';
import paymentActions from './payment';

const create = (user: { email: string; password: string }): ThunkAction => {
  return async (dispatch, getState) => {
    dispatch(actions.createUserInit());
    try {
      await createUser(getState, user);

      dispatch(actions.createUserSuccess());
      dispatch(push('/login'));
    } catch (e) {
      console.error(e.json);

      const createUserError: ICreateUserError = {};

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        dispatch(actions.createUserFailure(createUserError));
        return;
      }

      if (e.json.email && e.json.email['Vartotojo profilio klaida']) {
        createUserError.email = e.json.email['Vartotojo profilio klaida'];
      } else {
        createUserError.email = e.json.email[0];
      }

      if (e.json.password && e.json.password['vesties klaida']) {
        createUserError.password = e.json.password['vesties klaida'];
      }

      dispatch(actions.createUserFailure(createUserError));
    }
  };
};

const poll = (username: string): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const { idenfy_responded, idenfy_confirmed } = (await pollUser(getState, username))[0];

      if (idenfy_responded) {
        if (idenfy_confirmed) {
          dispatch(replace('/idenfy/success'));
        } else {
          dispatch(replace('/idenfy/failure'));
        }
      }
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
      }
    }
  };
};

const getReservations = (
  cb?: (lastReservation: IReservation, dispatch: ThunkDispatch<IState, undefined, any>) => void
): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const reservations = await getUserReservations(getState);
      dispatch(actions.setUserReservations(reservations));
      cb && cb(reservations[0], dispatch);
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        return;
      }

      dispatch(globalActions.showGlobalErrorModal({ title: 'Klaida', message: JSON.stringify(e.json) }));
    }
  };
};

const getData = (cb?: () => void): ThunkAction => {
  return async (dispatch, getState) => {
    dispatch(actions.fetchUserDataInit());
    try {
      const [membership, reservations] = await Promise.all([
        getUserMembership(getState),
        getUserReservations(getState),
      ]);

      dispatch(actions.fetchUserDataSuccess(membership, reservations));
      cb && cb();
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(actions.fetchUserDataFailure(e.json));
        return;
      }

      dispatch(actions.fetchUserDataFailure({ title: 'Klaida', message: JSON.stringify(e.json) }));
    }
  };
};

const getVault = (cb?: () => void): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const vault = await apiGetVault(getState);
      dispatch(actions.setVault(vault));
      cb && cb();
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        return;
      }

      dispatch(globalActions.showGlobalErrorModal({ title: 'Klaida', message: JSON.stringify(e.json) }));
    }
  };
};

const onVaultPaymentConfirm = (cart: ICartProducts): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      const token = getState().loginReducer.token!.access_token;
      let body: string = '';

      for (let key in cart) {
        for (let i = cart[key]; i > 0; i--) {
          if (body.length === 0) {
            body = `${body}${key}=`;
          } else {
            body = `${body}&${key}=`;
          }
        }
      }

      dispatch(paymentActions.paymentInit());
      const res = await addToVault(getState, body, token);
      dispatch(globalActions.hideGlobalModal());

      dispatch(go(-2));
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        return;
      }

      if (e.json['Narysts klaida']) {
        let error: IGlobalModalBody = { title: '', message: '' };
        error.title = 'Narysts klaida';
        error.message = e.json['Narysts klaida'];
        dispatch(globalActions.showGlobalErrorModal(error));
        return;
      }

      if (e.json['Vartotojo profilio klaida']) {
        let error: IGlobalModalBody = { title: '', message: '' };
        error.title = 'Vartotojo profilio klaida';
        error.message = e.json['Vartotojo profilio klaida'];
        dispatch(globalActions.showGlobalErrorModal(error));
        return;
      }

      dispatch(globalActions.showGlobalErrorModal({ title: 'Klaida', message: JSON.stringify(e.json) }));
    }
  };
};

const deleteLastVaultOrder = (): ThunkAction => {
  return async (dispatch, getState) => {
    try {
      dispatch(paymentActions.paymentInit());
      const vault = await apiDeleteLastVaultOrder(getState);
      dispatch(actions.setVault(vault));
      dispatch(globalActions.hideGlobalModal());
    } catch (e) {
      console.error(e.json);

      if (e.json.statusCode >= 500) {
        dispatch(globalActions.showGlobalErrorModal(e.json));
        return;
      }

      dispatch(globalActions.showGlobalErrorModal({ title: 'Klaida', message: JSON.stringify(e.json) }));
    }
  };
};

export const actions = {
  resetRegisterFormErrors: createAction(ActionTypes.RESET_REGISTER_FORM_ERRORS),
  createUserInit: createAction(ActionTypes.CREATE_USER_INIT),
  createUserSuccess: createAction(ActionTypes.CREATE_USER_SUCCESS),
  createUserFailure: createAction(ActionTypes.CREATE_USER_FAILURE, (action) => (error: ICreateUserError) =>
    action(error)
  ),
  setUserLocation: createAction(ActionTypes.SET_USER_LOCATION, (action) => (location: ILocation) =>
    action(location)
  ),
  addVendedProduct: createAction(ActionTypes.ADD_VENDED_PRODUCT, (action) => (productCode: number) =>
    action(productCode)
  ),
  addProductToCart: createAction(
    ActionTypes.ADD_PRODUCT_TO_CART,
    (action) => (vendorId: number, productId: number, count: number) => action({ vendorId, productId, count })
  ),
  removeProductFromCart: createAction(
    ActionTypes.REMOVE_PRODUCT_FROM_CART,
    (action) => (vendorId: number, productId: number, count: number) => action({ vendorId, productId, count })
  ),
  resetCart: createAction(ActionTypes.RESET_CART),
  addProductToVaultCart: createAction(
    ActionTypes.ADD_PRODUCT_TO_VAULTCART,
    (action) => (vendorId: number, productId: number, count: number) => action({ productId, count })
  ),
  removeProductFromVaultCart: createAction(
    ActionTypes.REMOVE_PRODUCT_FROM_VAULTCART,
    (action) => (vendorId: number, productId: number, count: number) => action({ productId, count })
  ),
  fetchUserDataInit: createAction(ActionTypes.FETCH_USER_DATA_INIT),
  fetchUserDataSuccess: createAction(
    ActionTypes.FETCH_USER_DATA_SUCCESS,
    (action) => (membership: IUserMembership, reservations: IReservation[]) =>
      action({ membership, reservations })
  ),
  fetchUserDataFailure: createAction(
    ActionTypes.FETCH_USER_DATA_FAILURE,
    (action) => (error: IGlobalModalBody) => action(error)
  ),
  setUserReservations: createAction(
    ActionTypes.SET_USER_RESERVATIONS,
    (action) => (reservations: IReservation[]) => action(reservations)
  ),
  setIdentified: createAction(ActionTypes.SET_IDENTIFIED, (action) => (identified: boolean) =>
    action(identified)
  ),
  setPayed: createAction(ActionTypes.SET_REGISTRATION_FEE_PAYED, (action) => (payed: boolean) =>
    action(payed)
  ),
  setVault: createAction(ActionTypes.SET_VAULT, (action) => (vault: IUserVault) => action(vault)),
};

export default {
  ...actions,
  create,
  poll,
  getData,
  getReservations,
  getVault,
  onVaultPaymentConfirm,
  deleteLastVaultOrder,
};
