import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import { IAuthorizeState, AuthorizeInitial } from '../StateTypes';

export default function authorizeReducer(state = AuthorizeInitial, action: Action): IAuthorizeState {
  switch (action.type) {
    case getType(actions.setHTML):
      return { ...state, html: action.payload };
    case getType(actions.setParameters):
      return { ...state, info: action.payload };
    default:
      return state;
  }
}
