import React, { Component } from 'react';
import { StyleSheet, Text, TouchableWithoutFeedback, TouchableHighlight, View } from 'react-native';
import * as Animatable from 'react-native-animatable';
import { connect } from 'react-redux';
import { withTranslation, WithTranslation } from 'react-i18next';

import globalActions from './actions/global';
import userActions from './actions/user';
import { Black, DarkGray, Gray, Red } from './Colors';

type IModalProps = WithTranslation & typeof localActions;

class VaultModal extends Component<IModalProps> {
  onConfirm = () => {
    this.props.deleteLastVaultOrder();
    this.onClose();
  };

  onClose = () => this.props.hide();

  render() {
    const { t } = this.props;

    return (
      <TouchableWithoutFeedback onPress={this.onClose}>
        <Animatable.View
          style={styles.background}
          animation='fadeIn'
          easing='ease-out'
          duration={150}
          useNativeDriver
        >
          <TouchableWithoutFeedback onPress={() => {}}>
            <View style={styles.message}>
              <Text style={styles.title}>{t('adding')}</Text>
              <Text style={styles.body}>{t('areYouSure')}</Text>
              <View style={styles.buttonWrapper}>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={this.onClose}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Gray }}>{t('no').toUpperCase()}</Text>
                </TouchableHighlight>
                <TouchableHighlight
                  style={styles.button}
                  activeOpacity={0.8}
                  onPress={this.onConfirm}
                  underlayColor='hsl(0, 0%, 96%)'
                >
                  <Text style={{ color: Red }}>{t('yes').toUpperCase()}</Text>
                </TouchableHighlight>
              </View>
            </View>
          </TouchableWithoutFeedback>
        </Animatable.View>
      </TouchableWithoutFeedback>
    );
  }
}

const styles = StyleSheet.create({
  background: {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'hsla(0, 0%, 0%, 0.55)',
  },
  message: {
    width: 280,
    padding: 24,
    backgroundColor: 'white',
    elevation: 5,
    borderRadius: 3,
  },
  title: {
    marginBottom: 12,
    color: Black,
    fontWeight: 'bold',
    fontSize: 20,
  },
  body: {
    marginBottom: 16,
    color: DarkGray,
    fontSize: 16,
    lineHeight: 22,
  },
  buttonWrapper: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    marginRight: -12,
    marginBottom: -16,
  },
  button: {
    alignSelf: 'flex-end',
    paddingVertical: 10,
    paddingHorizontal: 12,
  },
});

const localActions = {
  hide: globalActions.hideVaultModal,
  deleteLastVaultOrder: userActions.deleteLastVaultOrder,
};

export default withTranslation('vaultModal')(connect(null, localActions)(VaultModal));
