import { getType } from 'typesafe-actions';

import { actions, Action } from '../actions';
import { IUserState, UserInitial } from '../StateTypes';

export default function userReducer(state = UserInitial, action: Action): IUserState {
  switch (action.type) {
    case getType(actions.resetRegisterFormErrors):
      return { ...state, error: undefined };
    case getType(actions.setIdentified):
      return { ...state, identified: action.payload };
    case getType(actions.setPayed):
      return { ...state, registrationFeePayed: action.payload };
    case getType(actions.registrationPaymentSuccess):
      return {
        ...state,
        registrationFeePayed: true,
      };
    case getType(actions.createUserInit):
      return { ...state, creatingUser: true };
    case getType(actions.createUserSuccess):
      return { ...state, creatingUser: false };
    case getType(actions.createUserFailure):
      return { ...state, creatingUser: false, error: action.payload };
    case getType(actions.loginSuccess):
      return { ...state, email: action.payload.email, username: action.payload.username };
    case getType(actions.setUserLocation):
      return { ...state, location: action.payload };
    case getType(actions.setUserReservations):
      return { ...state, reservations: action.payload };
    case getType(actions.fetchAppDataSuccess):
    case getType(actions.fetchUserDataSuccess):
      const { membership, reservations } = action.payload;
      return { ...state, membership, reservations };
    case getType(actions.addVendedProduct):
      const newReservations = [...state.reservations];
      const { vended } = newReservations[0];
      newReservations[0].vended = `${vended ? vended : ''}${action.payload},`;

      return { ...state, reservations: newReservations };
    case getType(actions.addProductToCart): {
      const { vendorId, productId, count } = action.payload;

      let carts = { ...state.carts };
      let cart = { ...state.carts[vendorId] };

      if (cart) {
        if (cart[productId]) {
          cart[productId] = cart[productId] + count;
        } else {
          cart[productId] = count;
        }
      } else {
        cart = { [productId]: count };
      }

      carts[vendorId] = cart;

      return { ...state, carts };
    }
    case getType(actions.removeProductFromCart): {
      const { vendorId, productId, count } = action.payload;

      let carts = { ...state.carts };
      let cart = { ...state.carts[vendorId] };

      if (cart) {
        if (cart[productId] && cart[productId] > count) {
          cart[productId] = cart[productId] - count;
        } else if (cart[productId] && cart[productId] === count) {
          delete cart[productId];
        }

        if (Object.keys(cart).length === 0) {
          delete carts[vendorId];
        } else {
          carts[vendorId] = cart;
        }
      }

      return { ...state, carts };
    }
    case getType(actions.resetCart):
      return { ...state, carts: {} };
    case getType(actions.addProductToVaultCart): {
      const { productId, count } = action.payload;

      let vaultCart = { ...state.vaultCart };

      if (vaultCart[productId]) {
        vaultCart[productId] = vaultCart[productId] + count;
      } else {
        vaultCart[productId] = count;
      }

      return { ...state, vaultCart };
    }
    case getType(actions.removeProductFromVaultCart): {
      const { productId, count } = action.payload;

      let vaultCart = { ...state.vaultCart };

      if (vaultCart[productId] && vaultCart[productId] > count) {
        vaultCart[productId] = vaultCart[productId] - count;
      } else if (vaultCart[productId] && vaultCart[productId] === count) {
        delete vaultCart[productId];
      }

      return { ...state, vaultCart };
    }
    case getType(actions.setVault): {
      return { ...state, vault: action.payload };
    }
    default:
      return state;
  }
}
